import React, { Fragment } from 'react';
import { Card, Form, Input, Menu, Select, Spin, Tooltip, Icon } from 'antd';
import { withPropsAPI, NodePanel } from 'gg-editor';
import { connect } from 'react-redux';
// import { actionCreator } from '../store';
import { actionCreator } from '../../store';

const { Item } = Form;
const { Option } = Select;

class NodeItemPanel extends React.Component {

  constructor(props){
    super(props);
    this.state = {}
  }

  componentDidMount(){
    const { componentInfo, detail } = this.props;
    const id = detail.componentId;
    if(componentInfo){
      this.setState({
        variables: componentInfo[id]
      })
    }

    
  }

  handleSubmit = (e) => {
    if (e && e.preventDefault) {
      e.preventDefault();
    }

    const { form, propsAPI } = this.props;
    const { getSelected, executeCommand, update } = propsAPI;
    const { label } = this.props.detail

    setTimeout(() => {
      form.validateFieldsAndScroll((err, values) => {
        console.log(values);
        const { nodeType, nodeRemarks, ...conf } = values
        if (err) {
          return;
        }

        for(let key in conf){
          console.log(label+'_'+key, conf[key])
          this.props.saveParams(label+'_'+key, conf[key])
        }

        const item = window.ITEM;

        if (!item) {
          return;
        }

        executeCommand(() => {
          update(item, {
            nodeType,
            nodeRemarks,
            conf
          });
        });
      });
    }, 0);
  };
 
  render() {
    console.log("子组件",this.props.detail);
    console.log("组件参数", this.state.variables);
    
    const { 
      nodeType,
      nodeRemarks,
      label,
      conf
     } = this.props.detail;
    const { TextArea } = Input;
    const { form, paramsData } = this.props;
    const { variables } = this.state;
    //console.log(variables);
    return (
      <Fragment>
        <Form onSubmit={this.handleSubmit} labelCol={{span:8}} wrapperCol={{span:15}}>
          <Item label="节点名称">
            {form.getFieldDecorator('nodeType', {
              initialValue: nodeType,
            })
            (<Input onBlur={this.handleSubmit} readOnly={true}/>)}
          </Item>
          <Item label="备注">
            {form.getFieldDecorator('nodeRemarks', {
              initialValue: nodeRemarks,
            })
            (<TextArea onBlur={this.handleSubmit} rows={3} readOnly={true}/>)}
          </Item>
          <Menu mode="horizontal" style={{marginBottom: '15px'}}>
            <Menu.Item key="configuration">
              参数配置
            </Menu.Item>
          </Menu>
            {
              variables && variables.map(item=>{
                console.log("variables",variables)
                console.log("item",item)
                return(
                  <Item 
                    label={
                    <span>
                    {item.variableName} &nbsp;
                      <Tooltip title={item.variableDes}>
                        <Icon type="question-circle-o" style={{color:'#5f6267'}} />
                      </Tooltip>
                    </span>
                    } required>
                    {form.getFieldDecorator(item.variableName, {
                      initialValue: paramsData[label+'_'+item.variableName] ? paramsData[label+'_'+item.variableName] : conf[item.variableName] ,
                    })
                    (
                      item.variableType == 0 ? <Input onBlur={this.handleSubmit} /> : (item.variableType == 1 ?
                        <Select onBlur={this.handleSubmit}>
                          {item.enumValues.map(arr=>
                            (<Option value={arr.value}>{arr.value}</Option>)
                          )}
                        </Select>: <Input onBlur={this.handleSubmit} />)
                    )
                    }
                  </Item>
                )
              })
            }
          {/* </Card> */}
        </Form>
      </Fragment>
    );
  }
};

const mapStateToProps = (state) => ({
  componentInfo: state.process.componentInfo,
  paramsData: state.process.paramsData
})

const mapDispatchToProps = (dispatch) => ({
  saveParams(name, param) {
    dispatch(actionCreator.saveParamsData(name, param));
  }
})

export default Form.create()(withPropsAPI(connect(mapStateToProps,mapDispatchToProps)(NodeItemPanel)));