import React from "react";
import { withPropsAPI, Command } from "gg-editor";
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { message, Modal, Input, Form } from 'antd';
import { getSpecialNodes, getXML } from '../getXML';
import IconFont from '../../../../../../common/IconFont';
import {createProcess, updateWorkflow} from '../../../../../../api/process.js'
import { actionCreator } from '../../../../store';
import styles from '../../index.module.less';

@withPropsAPI @Form.create() @withRouter
class Save extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      visible: false,
      buttonLoading: false,
    }
  }

  componentDidMount(){
    this.props.saveProject('','');
  }

  componentWillUnmount(){
    this.props.clearProcessId()
  }

  //保存画布上的流程图 
  handleClick = () => {
    // console.log('processState',processState)
    // if(processState == 0){
      // this.changeStateIcon();
      this.saveObject()
    // }else{
    //   message.error('运行中，请勿保存！')
    // }
  };

  saveObject = () => {
    const { propsAPI, saveProject, processState } = this.props;
    let obj = this.props.propsAPI.save();
      console.log('obj',obj);
      // obj.edges && obj.edges.forEach((element, index) => {
      //   element.id = 'edges-' + index
      // });
      const { nodes, edges } = obj;
      let specialNodes;
      let xmlString;
      if(nodes) {
        //有边才会有网关节点
        if(edges) {
          specialNodes = getSpecialNodes(obj);
          console.log(specialNodes)
          if(specialNodes.firstNode.length !== 1 || specialNodes.firstNode[0].label !== 'start'){
            message.error("请加入源点或图形错误！")
          }else{
            xmlString = getXML(nodes,edges,specialNodes);
          }
          if(xmlString){
            saveProject( JSON.stringify(obj), xmlString );
            if(this.props.processId){
              this.updateProcss(xmlString)
            }else{  // 新建流程的情况
              this.setState({
                visible: true,
              });
            }
          }else{
            message.info('流程图解析失败')
          }
        }else{
          message.info('没有任务流程！')
        }
      }
      else {
        message.info('没有任务节点！')
      }
  }

  // changeStateIcon = () => {
  //   const { propsAPI, saveNodesId } = this.props;
  //   const { executeCommand, update, getSelected, find } = propsAPI;
  //   const nodesId = {};
  //   console.log(propsAPI);
  //   const selectedNodes = getSelected();
  //   console.log(selectedNodes);
  //   for(let i=0;i<selectedNodes.length;i++){
  //     const item = selectedNodes[i];
  //     const label = item.model.label;
  //     const id = item.id;
  //     nodesId[label] = id;
  //     executeCommand(() => {
  //       console.log(item)
  //       update(item, {
  //         state_icon_url:'',
  //       });
  //     });
  //   }
  //   saveNodesId(nodesId);
  // }

  updateProcss = (workflowXml) => {
    let ggeditorObjectString = JSON.stringify(this.props.propsAPI.save())
    const { processId:workflowId } = this.props;

    updateWorkflow({ggeditorObjectString, workflowId, workflowXml }).then(res=>{
      if(res.data.meta.code === '001'){
        message.success(res.data.meta.message)
        // this.props.history.push('/index/process')
      }else{
        message.error(res.data.meta.message)
      }
    })
  }

  //保存XML字符串和GGEditor对象
  handleSaveXML = () => {
    const {savedXmlString} = this.props;
    const { getFieldsValue} = this.props.form
    let params = getFieldsValue();
    var f=true;
    this.setState({ buttonLoading:true})
    for(let i in params){
      if(params[i]===undefined){
        message.warning('请填写完整信息!');
        f=false;
        this.setState({ buttonLoading:false})
        break;
      }
    }
    if(f){
      let ggeditorObjectString = JSON.stringify(this.props.propsAPI.save())
      console.log(ggeditorObjectString)
      params = {...params, ggeditorObjectString, workflowXml: savedXmlString, isMl: this.props.isMl}
      createProcess(params).then(res=>{
        this.setState({ buttonLoading:false})
        if(res.data.meta.code === '001'){
          message.success(res.data.meta.message)
          this.setState({visible: false})
          this.props.history.push('/index/process')
        }
      }).catch(err=>this.setState({ buttonLoading:false}))
    }
  };

  handleCancel = e => {
    this.setState({
      visible: false,
      nameValue: ''
    });
  };


  handleChange = e => {
    this.setState({
      nameValue: e.target.value,
    });
  }

  render() {
    const {getFieldDecorator}=this.props.form;
    const config={
        rules:[{
            required:true,
            message:'Please input!'
        }]
    }
    const {visible} = this.state
    return (
      <div>
        <Command name="selectAll">
          <div className={styles.com} title="保存" onClick={this.saveObject}>
            <IconFont type={`icon-baocun`}/><span>保存</span>
          </div>
        </Command>
        {visible && <Modal
          title="流程"
          visible={visible}
          onOk={this.handleSaveXML}
          onCancel={this.handleCancel}
          confirmLoading={this.state.buttonLoading}
        >
          {/* 流程名称：<Input placeholder="请输入流程名称" onChange={this.handleChange} value={this.state.nameValue} style={{marginTop:'8px'}}/> */}
          <Form labelCol={{span:6}} wrapperCol={{span:16}}>
            <Form.Item label='流程名称'>
              {
                  getFieldDecorator('workflowName',config)(<Input placeholder='请输入流程名称' />)
              }
            </Form.Item>
            <Form.Item label='流程标签'>
              {
                  getFieldDecorator('tagString',config)(<Input placeholder='请输入流程标签，以逗号分割' />)
              }
            </Form.Item>
            <Form.Item label='流程描述'>
              {
                  getFieldDecorator('workflowDesc',config)(<Input.TextArea placeholder='请输入流程描述' />)
              }
            </Form.Item>
          </Form>
        </Modal>}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  GGEditorObj: state.process.GGEditorObj,
  savedXmlString: state.process.xmlString,
  processState: state.process.processState,
  processId: state.process.processId,
  isMl: state.process.isML,
})

const mapDispatchToProps = dispatch => {
  return{
    saveProject(obj, xmlString){
      dispatch(actionCreator.saveProject(obj, xmlString));
    },
    clearProcessId(){
      dispatch(actionCreator.saveProcessId(null));
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Save);