import React, {Component} from 'react'
import {Radio,Steps,Button,message} from 'antd'
import CreateProcess from './createProcess';
import CreateModel from './createModel';
import ComponentForm from '../componentForm';
import ParamsForm from '../paramsForm';
import InputOutputForm from '../inputOutputForm';
import {createCustomComponent} from '../../../api/component';

class CreateCustomComponent extends Component{
    constructor(props){
        super(props);
        
        this.state = {
            componentType: '',
            current:0,
            values:{},
            paramsValues:{},
            inputOutput:{}
        }
    }

    changeComponentType = (e) => {
        this.setState({
            componentType: e.target.value
        })
    }
    handleParamsValues=(values)=>{
        if(values.name||values.inputName){
            var l;
            if(values.name){
                l=values.name.length;
            }else{
                l=values.inputName.length;
            }
            const p=[];
            for(let j=0;j<l;j++){
                let obj={};
                for(let i in values){
                    if(i!=='keys'&&values[i][j]){
                        obj[i]=values[i][j];
                    }
                }
                if(JSON.stringify(obj)!=='{}')  p.push(obj);
            }
            return p;
        }else{
            return []
        }
    }
    nextStep=()=>{
        const {current}=this.state;
        if(current===0){
            this.componentForm.props.form.validateFields((err,values)=>{
                if(!err){
                    this.setState({
                        current:current+1,
                        values
                    })
                }else{
                    message.warning('请填写完整信息!')
                }
            });
        }else{
            this.paramsForm.props.form.validateFields((err,values)=>{
                if(!err){
                    this.setState({
                        current:current+1,
                        paramsValues:this.handleParamsValues(values)
                    })
                }else{
    
                }
            })
        }
    }
    preStep=()=>{
        const {current}=this.state;
        if(current===2){
            this.inputOutputForm.props.form.validateFields((err,values)=>{
                if(!err){
                    this.setState({
                        current:current-1,
                        inputOutput:this.handleParamsValues(values)
                    })
                }else{
    
                }
            })
        }else{
            this.paramsForm.props.form.validateFields((err,values)=>{
                if(!err){
                    this.setState({
                        current:current-1,
                        paramsValues:this.handleParamsValues(values)
                    })
                }else{
    
                }
            })
        }
    }
    createComponent=()=>{
        const componentCreateInfo={};
        this.inputOutputForm.props.form.validateFields((err,values)=>{
            if(!err){
                this.setState({
                    inputOutput:this.handleParamsValues(values)
                },()=>{
                    const {values,paramsValues,inputOutput}=this.state;
                    const v=JSON.parse(JSON.stringify(values));
                    delete v.sourceId;
                    delete v.package;
                    const input=inputOutput.map(i=>({inputName:i.inputName,inputType:i.inputType})) 
                    const output=inputOutput.map(i=>({outputName:i.outputName,outputType:i.outputType}))
                    componentCreateInfo.componentType=values.type;
                    componentCreateInfo.sourceId=parseInt(values.sourceId);
                    componentCreateInfo.inputStubInfoList=input;
                    componentCreateInfo.outputStubInfoList=output;
                    componentCreateInfo.componentInfo=v;
                    componentCreateInfo.componentParamList=paramsValues;
                    componentCreateInfo.componentType=0;
                    componentCreateInfo.sourceId='';
                    console.log(componentCreateInfo);
                    const params={
                        componentCreateInfo:componentCreateInfo,
                        yamlFileName:values.package?values.package:''
                        }
                    console.log(params);
                    createCustomComponent(params).then(res=>{
                        if(res.data.meta.code==='001'){
                            message.success('创建组件成功!');
                            this.close();
                        }else{
                            message.error('创建组件失败!');
                        }
                    }).catch(err=>{
                        console.log(err);
                    })
                    
                })
            }else{

            }
        })
    }
    clear=()=>{
        this.setState({
            current:0,
            values:{},
            paramsValues:{},
            inputOutput:{}
        })
        this.componentForm.props.form.resetFields();
        this.componentForm.clearFileList();
    }
    close=()=>{
        this.clear();
        this.props.onclose();
    }

    render(){
        const {componentType,current,values,paramsValues,inputOutput} = this.state;
        const steps = [
            {
              title: '配置组件基本信息',
              content:<ComponentForm onRef={i=>this.componentForm=i} values={values}/>,
            },
            {
              title: '配置组件参数信息',
              content: <ParamsForm onRef={i=>this.paramsForm=i} values={paramsValues}/>,
            },
            {
                title:'配置输入桩和输出桩',
                content:<InputOutputForm onRef={i=>this.inputOutputForm=i} values={inputOutput}/>
            }
          ];
        return(
            <div>
                <div style={{textAlign: 'center'}}>
                <span>自定义组件类型：&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <Radio.Group value={componentType} onChange={this.changeComponentType}>
                    <Radio value='process'>流程组件</Radio>
                    <Radio value='algorithm'>算法组件</Radio>
                    <Radio value='model'>模型组件</Radio>
                </Radio.Group>
                </div>
                {
                    componentType === 'process' && <CreateProcess onclose={this.props.onclose}></CreateProcess>
                }{
                    componentType === 'model' && 
                    <div style={{paddingTop: 10, width: "80%", margin:'0 auto'}}>
                        <CreateModel onclose={this.props.onclose}></CreateModel>
                    </div>
                }{
                    componentType === 'algorithm' && 
                    <div style={{padding:'0 200px',marginTop:20}}>
                        <Steps current={current}>
                            {steps.map(item => (
                                <Steps.Step key={item.title} title={item.title} />
                            ))}
                        </Steps>
                        <div>{steps[current].content}</div>
                        {current == 0 ? <div style={{textAlign:'center'}}><Button type='primary' onClick={this.nextStep}>下一步</Button></div>:
                            current === 1 ?
                            <div style={{textAlign:'center'}}>
                                <Button type='primary' onClick={this.preStep} style={{marginRight:10}}>上一步</Button>
                                <Button type='primary' onClick={this.nextStep}>下一步</Button>
                            </div>:
                            <div style={{textAlign:'center'}}>
                                <Button type='primary' onClick={this.preStep} style={{marginRight:10}}>上一步</Button>
                                <Button onClick={this.close} style={{marginRight:10}}>取消</Button>
                                <Button type='primary' onClick={this.createComponent}>创建算法组件</Button>
                            </div>
                        }
                    </div>
                }
            </div>
        )
    }
}

export default CreateCustomComponent