import React,{ Component } from 'react'
import  { Table ,message, Divider, Badge, Modal, Avatar, Icon, Form, Input, Button} from 'antd'
import { getExperimentRunning, deleteExperimentRunning, stopExperimentRunning } from '../../../../api/experimets'
import {withRouter} from 'react-router-dom'
import { createModel} from '../../../../api/model'

@withRouter @Form.create()
class ExperimentRunning extends Component{
  constructor(props){
    super(props);
    this.state={
      loading:false,
      dataSource:[],
      page:1,
      total:0,
      createModelVisible:false,       //导出模型对话框
      runningId:null
    }
  }

  componentDidMount(){
    this.getRunDataSource()
  }
  //获取实验运行记录信息
  getRunDataSource=()=>{
    this.setState({loading: true})
    const params = {
      pageNum: this.state.page,
      pageSize: 10,
      experimentId:this.props.experimentId
    }
    const {page} = this.state
    getExperimentRunning(params).then((res)=>{
        console.log(res);
        if(res){
          if(res.data.meta.code == '001'){
            const dataSource = res.data.data['ExperimentRunning List'].map((item,index)=>{
              return { ...item, index: index+1+(page-1)*10,key:item.id }
            })
            this.setState({ 
              dataSource, 
              loading: false,
              total: res.data.data.Total  
            })
          }else{
            message.error(res.data.meta.message)
          }
        }
      }).catch(err=>{
        console.log(err)
      })
  }
  //删除实验运行
  deleteRunning=(id)=>{
    const params = {
      runningIds:id
    }
    console.log('删除实验'+id)
    deleteExperimentRunning(params).then(res=>{
      if(res){
        if(res.data.meta.code == '001'){
          message.success(res.data.meta.message)
          this.getRunDataSource()
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
      console.log(err)
    })
  }

  onPageChange = page => {
    let that = this
    this.setState(
      {page},
      ()=>{
        that.getRunDataSource()
      }
    );
  }

  checkRecord = (id) => {
    console.log(id);
    this.props.history.push({
      pathname: '/index/experiment/run',
      query:{
        runningId: id
      }
    })
  }

  submit=()=>{
    const { getFieldValue,validateFields } = this.props.form
    validateFields(err => {
      if(!err){
        let params = {
          modelName: getFieldValue('name'),
          modelDesc: getFieldValue('desc')
          }
          params = {...params, modelId: this.state.modelId}
          console.log(params)
          createModel(params).then((res)=>{
            const newRes=res.data.meta;
            if(newRes.code==='001'){
              message.success(newRes.message)
            }else{
              message.error(newRes.message)
            }
            //关闭modal
            this.setState({createModelVisible: false, modelId: undefined})
          }).catch(err=>{
            console.log(err)
          })
      }
    });
  }
 
  //重置
  reset=()=>{
    this.props.form.resetFields();
  }

  stopRunning = (runningId) => {
    stopExperimentRunning({runningId}).then(res=>{
      if(res.data.meta.code === '001'){
        message.success('停止运行成功')
        this.getRunDataSource()
      }else{
        message.success(res.data.meta.message)
      }
    })
  }

  render(){
    const columns = [
      { key: 'icon',render: ()=><Avatar size={36} icon={<Icon type="file-sync" />} style={{ backgroundColor: '#13c2c2' }} />},
      
      // {title: '运行id',dataIndex: 'id',key:'id'},
      {title: '实验id', dataIndex: 'fkExperimentId', key:'fkExperimentId'},
      {title: '开始时间', dataIndex: 'startTime', key:'startTime'},
      {title: '结束时间', dataIndex: 'endTime', key:'endTime'},
      {
        title: '运行状态',key: 'runningStatus',dataIndex: 'runningStatus',
        render: (text,record) => (
          <span>
            <Badge status={text===0 ? 'processing' : text===1 ? 'success' : 'error'} />
            {text===0 ? '运行中' : text===1 ? '成功' : '失败'}
          </span>
        ),
      },
      {title: '操作',dataIndex: 'action', key: 'operation',
        render:(text,record)=>(
          <>
          <a onClick={()=>this.deleteRunning(record.id)}>删除</a>
          <Divider type='vertical'/>
          <a onClick={() => this.checkRecord(record.id)}>查看</a>
          <Divider type='vertical'/>
          {
            record.runningStatus === 0 &&
            <a onClick={()=>this.stopRunning(record.id)}>停止</a>
          }{
            record.runningStatus === 1 && record.fkModelId &&  // 运行成功并且有模型
            <a onClick={()=>this.setState({createModelVisible:true, modelId:record.fkModelId})}>导出模型</a>
          }
          </>
        )},
    ];
    const { getFieldDecorator } = this.props.form;
    return(
      <div>
          <Table 
              size='middle'
              columns={columns} 
              dataSource={this.state.dataSource} 
              pagination={{
                onChange: this.onPageChange,
                current:this.state.page,
                pageSize: this.state.pageSize,
                total: this.state.total
              }}
          />

          {this.state.createModelVisible &&
          <Modal
            title="导出模型"
            visible={this.state.createModelVisible}
            footer={null}
            onCancel={()=>{this.setState({createModelVisible:false, modelId: undefined})}}
          >
            <Form labelAlign='left'> 
              <Form.Item label='模型名称'>
                {getFieldDecorator('name',{rules: [{ required: true, message: 'Please input!' }]})(<Input placeholder="请输入模型名称"/>)} 
              </Form.Item>

              <Form.Item label='模型描述'>
                {getFieldDecorator('desc',{initialValue:''})(<Input.TextArea placeholder="请输入模型描述" />)} 
              </Form.Item>
              <Form.Item style={{textAlign:'center'}}>
                <Button style={{marginRight:'5px'}} type='primary'
                  onClick={this.reset}
                >
                  重置
                </Button>
                <Button style={{marginLeft:'5px'}} type='primary'
                  onClick={this.submit}
                >
                  导出
                </Button>
              </Form.Item>
            </Form>
          </Modal>}
        </div>
    )
  }
}

export  default ExperimentRunning;