import Axios from '../utils/request';

export async function getTemplate(params) {
    return Axios({
      url: '/template/getTemplate',
      method: 'POST',
      params
    })
}
export async function createExperiment(params) {
    return Axios({
      url: '/template/createExperimentFromTemplate',
      method: 'POST',
      params
    })
}
export async function deleteTemplate(params) {
    return Axios({
      url: '/template/deleteTemplate',
      method: 'POST',
      params
    })
}

export async function restoreTemplate(params) {
    return Axios({
      url: '/workflow/restoreTemplate',
      method: 'POST',
      params
    })
}
export async function searchTemplate(params) {
    return Axios({
      url: '/template/searchTemplateByKeyword',
      method: 'POST',
      params
    })
}
export async function updateTemplate(params) {
    return Axios({
      url: '/template/updateTemplate',
      method: 'POST',
      params
    })
}
export async function saveTemplate(params) {
    return Axios({
      url: '/template/saveTemplate',
      method: 'POST',
      params
    })
}
export async function insertTemplate(params) {
    return Axios({
      url: '/template/insertTemplate',
      method: 'POST',
      params
    })
}
