import Axios from '../utils/request';

// 获取所有的系统组件
export async function loadPublicComponentInfo(params){
  return Axios({
    url: '/publicComponent/loadPublicComponentInfo',
    method: 'POST',
    params
  })
}

export async function workflowToComponent(params) {
  let data = new FormData()
  for(let key in params){
    data.append(key, params[key])
  }
  return Axios({
    url:'/workflow/workflowToComponent',
    method: 'POST',
    data,
    headers: {
      'Content-Type': 'application/json',
    }
  })
}

