import React, {Component} from 'react'
import {Radio, Form, Input, Button, message, Pagination} from 'antd'
import {getModelList, modelToComponent} from '../../../api/model'

@Form.create()
class CreateModel extends Component{
    constructor(props){
        super(props);
        
        this.state = {
            page: 1,
            pageSize: 10,
            loading: false,
            modelList: [],
            total: 0,
        }
    }

    componentDidMount(){
        this.getModelList()
    }

    //获取模型列表
    getModelList(){
        const { page: pageNum, pageSize } = this.state
        this.setState({loading:true})
        getModelList({pageNum, pageSize}).then(res=>{
            if(res){
                this.setState({loading:false})
                if(res.data.meta.code == '001'){
                    const modelList = res.data.data['Model List']
                    this.setState({
                        modelList,
                        loading: false,
                        total: res.data.data.Total,
                    })
                }else{
                    message.error(res.data.meta.message)
                }
            }
        }).catch(err=>{
            this.setState({loading: false})
        })
    }

    modelToComponent= () =>{
        const { validateFields } = this.props.form
        console.log(this.props)
        validateFields((err,res) => {
          console.log(res)
          if(!err){
            modelToComponent(res).then((res)=>{
                const newRes= res.data.meta;
                if(newRes.code==='001'){
                    message.success(newRes.message)
                    this.props.onclose()
                }else{
                  message.error(newRes.message)
                }
              }).catch(err=>{
                console.log(err)
              })
          }
        });
      }
    
    onPageChange = (page) => {
        let that = this
        this.setState({page}, ()=>{
            that.getModelList()
        })
    }
     
    //重置
    reset=()=>{
        this.props.form.resetFields();
    }

    render(){
        const { getFieldDecorator } = this.props.form;
        const { modelList, total, page, pageSize } = this.state

        return(
            <Form labelCol={{span:6}} wrapperCol={{span:16}}> 
                <Form.Item label='组件名称'>
                {getFieldDecorator('componentName', {rules: [{ required: true, message: '请输入组件名称!' }]}
                    )(
                    <Input placeholder="请输入组件名称"/>
                )} 
                </Form.Item>
                <Form.Item label='组件标签'>
                {getFieldDecorator('tag',
                    {rules: [{ required: true, message: '请输入组件标签!' }]}
                    )(
                    <Input placeholder="请输入组件标签，以逗号分隔。"/>
                )} 
                </Form.Item>
                <Form.Item label='模型'>
                    <div style={{width: "100%", padding: 10, border: '1px solid #d9d9d9', borderRadius: 5}}>
                        {
                            getFieldDecorator('modelId', {rules: [{ required: true, message: '请选择要封装为组件的模型!' }]})(
                                <Radio.Group>
                                    {
                                        modelList.map(model=>{
                                            return <><Radio style={{padding:5}} key={model.id} value={model.id}>{model.name}</Radio><br/></>
                                        })
                                    }
                                </Radio.Group>
                            )
                        }
                        <div style={{height: 30}}>
                        <Pagination style={{float: 'right'}} size="small" current={page} total={total} pageSize={pageSize} onChange={this.onPageChange} />
                        </div>
                    </div>
                </Form.Item>
                <Form.Item label='组件描述'>
                    {getFieldDecorator('componentDec')(<Input.TextArea placeholder="请输入组件描述" />)} 
                </Form.Item>
                <div style={{display: 'flex', justifyContent:'center'}}> 
                    <Button   onClick={this.reset}>
                        重置
                    </Button>
                    <Button style={{marginLeft:20}} type='primary' onClick={this.modelToComponent}
                    >
                        创建模型组件
                    </Button>
                </div>
            </Form>
        )
    }
}

export default CreateModel