import React from 'react';
import { Row, Col, Tabs, PageHeader,  message } from 'antd';
import GGEditor, { Flow } from 'gg-editor';
import { withRouter } from 'react-router-dom';
import {connect} from 'react-redux'

import { FlowContextMenu } from './components/EditorContextMenu';
import { FlowToolbar } from './components/EditorToolbar';
import { FlowItemPanel } from './components/EditorItemPanel';
import CustomNode from './components/node/nodeTensorflow';
import StartNode from './components/node/nodeStart';
import styles from './index.module.less'
import {actionCreator} from './store'

import {processisEdit} from '../../api/process'

@withRouter
class CreateProcess extends React.Component {

  constructor(props){
    super(props);
    
    this.state = {
    }
  }

  componentWillMount(){
    const {processId} = this.props
    if(processId){  // 查看和编辑流程的情况
      this.isProcessCanEdit(processId)
      const processObj = this.props.processData ? JSON.parse(this.props.processData) : {}
      if(processObj){
        processObj.nodes && processObj.nodes.map(item=>{
          item.id = item.id.toString()  // 将id转为字符串
        });
        processObj.edges && processObj.edges.map((item,index)=>{
          item.id = (item.id + index).toString();
          item.source = item.source.toString();
          item.target = item.target.toString();
        })
      }
      this.setState({processObj})
    }
  }
  
  isProcessCanEdit = (workflowId) => {
    let canEdit = false
    processisEdit({workflowId}).then(res=>{
      if(res){
        if(res.data.meta.code === '001'){
          const {data} = res.data
          
          for(let [code, mess] of Object.entries(data)){
            if(code === '0'){
              canEdit = true;
            }else{
              message.info(mess)
            }
          }
        }else{
          message.error("获取可编辑状态失败！")
        }
      }else{
        message.error("获取可编辑状态失败！")
        this.toProcessPage(false)
      }
    }).catch(err=>{
        message.error("获取可编辑状态失败！")
    }).finally(res=>{
      this.setState({canEdit})
    })
  }

  componentWillUnmount(){
    this.props.saveProcess('')
  }
  
  listenCommand = ({command}) => { 
    if(command.name === 'add' && command.type === 'node'){
      console.log(command);
      console.log(this.flow)
      const {componentType, ggeditorObjectString, x: addX, y: addY} = command.addModel
      if(componentType && componentType === 'process'){  // 添加一个流程组件
        this.flow.remove(command.addId)
        if(ggeditorObjectString){
          try{
            const process = JSON.parse(ggeditorObjectString)
            const {addId} = command
            const {nodes, edges} = process
            let minX = nodes ? nodes[0].x : 0
            let minY = nodes ? nodes[0].y : 0
            nodes && nodes.forEach(node=>{
              if(node.x < minX) minX = node.x
              if(node.y < minY) minY = node.y
            })
            nodes && nodes.forEach(node=>{
              console.log(this.flow)
              this.flow.add('node', {...node, id: addId + node.id, x: node.x-minX+addX, y: node.y-minY+addY})
            })
            edges && edges.forEach(edge=>{
              const {id, ...model} = edge
              this.flow.add('edge', {...model, source: addId+model.source, target: addId+model.target})
            })
          }catch{
            message.error('解析流程出错！')
          }
        }
      }
    }
  }

  render() {
    const {canEdit} = this.state
    return (<div className={styles.canvasContainer}>
        <GGEditor 
          className={styles.editor}
          onAfterCommandExecute={this.listenCommand}
        >
          <PageHeader
            className={styles.pageheader}
            title="流程管理"
            onBack={() => this.props.history.push('/index/process')}
            subTitle="请拖拽节点创建一个流程"
            // extra={[canEdit && <Button type="primary">保存流程</Button>]}
          />
          <Row type="flex" className={styles.editorBd} style={{height:'100%'}}>
              
            {(canEdit || canEdit === undefined) && <Col xxl={4} xl={5} lg={6} md={7} sm={8} xs={8} className={styles.editorSidebar}>
            
            {/* <Input.Search
              placeholder="搜索组件"
              style={{margin: '5px 0', width: '95%', textAlign: 'center', alignSelf: 'center'}}
              enterButton
              size="large"
              onSearch={value => console.log(value)}
            /> */}
              <FlowItemPanel />
            </Col>}
            <Col xxl={20} xl={19} lg={18} md={17} sm={16} xs={16} className={styles.editorContent}>
            {(canEdit || canEdit === undefined) && <FlowToolbar />}
              <CustomNode />
              <StartNode />
              <Flow 
                className={styles.flow}
                data={this.state.processObj}
                ref={component => {
                  if (component) {
                    console.log('ref',component)
                    this.flow = component.page;
                  }
                }}
              />
            </Col>
          </Row>
          {(canEdit || canEdit === undefined) && <FlowContextMenu />}
        </GGEditor>
   </div> );
  }
};

const mapStateToProps = (state) => {
  return {
    processData: state.process.processData,
    processId: state.process.processId,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    saveProcess(process){
      dispatch(actionCreator.saveProcess(process))
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateProcess);
