import * as constants from './constants'

export const loadChart = data=>({
    type: constants.LOAD_COMPONENT,
    chartData: data.chartData,
    activeIndex: data.activeIndex
})

export const addChart = chart => ({
    type: constants.ADD_COMPONENT,
    chart
})

export const changeTitle = title => ({
    type: constants.CHANGE_TITLE,
    title
})

export const changeSize = size => ({
    type: constants.CHANGE_SIZE,
    size
})

export const setOffset = offset => ({
    type: constants.SET_OFFSET,
    offset
})

export const moveChart = config => ({
    type: constants.MOVE_COMPONENT,
    config
})

export const changeActiveIndex = index => ({
    type: constants.CHANGE_ACTIVEINDEX,
    index
})

export const changeLineStyle = lineStyle => ({
    type: constants.CHANGE_LINESTYLE,
    lineStyle
})

export const changeXAxisData = data => ({
    type: constants.CHANGE_XAXISDATA,
    data
})

export const changeYAxisData = data => ({
    type: constants.CHANGE_YAXISDATA,
    data
})

