import React from 'react'
import {useDispatch, useSelector} from 'react-redux'

import HeatMapConfig from './HeatMapConfig'
import LineChartConfig from './LineChartConfig'
import CanvasConfig from './CanvasConfig'

import {saveChartsConfig } from '../../../api/chartConfig'

import * as chartType from '../chartConstants'
import styles from './index.module.less'

import { message ,Button  } from 'antd'

function saveChartConfig(charts){
    //console.log(chartData);
    
    console.log(JSON.stringify(charts))
    let params={
        fkExperimentRunningId:sessionStorage.getItem("runningId"),
        resultJsonString:JSON.stringify(charts)
    }

    console.log(params);

    saveChartsConfig(params).then(res=>{
        message.success("保存配置成功！");
    }).catch(err=>{
        message.error(err);
    })
}

export default function ConfigPanel(props){
    const charts = useSelector(state => state.charts);
    const chartData = useSelector(state => state.charts.chartData);
    const activeIndex = useSelector(state => state.charts.activeIndex);

    const getConfig = () => {
        switch(chartData[activeIndex].type){
            case chartType.HEAT_MAP:    
                return <HeatMapConfig />
            case chartType.LINE_CHART:    
                return <LineChartConfig />
            default:    
                return <CanvasConfig />
        }
    }

    return (
        <div className={styles.wrapper}>
            <Button className={styles.saveButton} 
                type="primary" 
                onClick={()=>saveChartConfig(charts)}>
                保存配置
            </Button>
            
            {
                activeIndex > -1 && getConfig()
            }{
                activeIndex === -1 && <CanvasConfig />
            }
            
            
        </div>
    )
}