import React from 'react';

import {Card, List, Avatar, message, Icon} from 'antd'
import styles from './index.module.less';

import IconFont from '../../common/IconFont';
import {recentRunning, recentExperiment} from '../../api/experimets'
import {withRouter} from 'react-router-dom'
import {connect} from 'react-redux'
import {actionCreator} from '../process/store'

@withRouter
class Home extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      number: 10,
      recentRunning: [],
      recentExperiment: [],
    }
  }

  componentDidMount(){
    this.getRecentExperiment()
    this.getRunning()
  }

  getRecentExperiment = () => {
    const {number: experimentNum} = this.state
    recentExperiment({experimentNum}).then(res=>{
      if(res.data.meta.code === '001'){
        this.setState({recentExperiment: res.data.data})
      }else{
        message.error(res.data.meta.message)
      }
    }).catch(err=>{message.error('获取最近运行失败！')})
  }

  getRunning = () => {
    const {number: experimentRunningNum} = this.state
    recentRunning({experimentRunningNum}).then(res=>{
      if(res.data.meta.code === '001'){
        this.setState({recentRunning: res.data.data})
      }else{
        message.error(res.data.meta.message)
      }
    }).catch(err=>{message.error('获取最近运行失败！')})
  }

  checkRunning = (item) => {
    this.props.saveProcess(item.ggeditorObjectString)
    this.props.history.push({
      pathname: '/index/experiment/run',
      query:{
        runningId: item.id
      }
    })
  }

  checkExperiment = (item) => {
    this.props.saveProcess(item.ggeditorObjectString);
    this.props.history.push({
        pathname: '/index/experiment/create',
        query: {
            paramJsonString: item.paramJsonString,
            experimentId: item.id
        }
    })
  }

  render() {
    const {recentExperiment, recentRunning} = this.state
    const data = [
      {
        title: ' 创建新流程', href: '/index/process/create',
        description: '为了方便流程设计，提供了可视化新建流程的方式，用户只需要明确要设计机器学习整体业务有哪些步骤，然后在画布上依次使用相关组件即可。',
      },{
        title: '从模板创建实验', href: '/index/template',
        description: '模板与流程和实验关联，从模板创建的实验拥有完整流程设计信息，并且各个组件已经配置了参数，可以直接运行。',
      },{
        title: '查看所有流程', href: '/index/process',
        description: '流程指将多个组件通过可视化界面拖拽生成的工作流，用于形成某个复合的组件或为了解决实际的应用问题而形成的处理流程（组件的连接关系）。',
      },{
        title: '查看自定义组件', href: '/index/component',
        description: '组件是创建一个流程的最小单元，根据组件的封装内容，可以将组件分为三类：算法组件、模型组件和流程组件。',
      },{
        title: '查看所有模型', href: '/index/model',
        description: '模型主要包括完整的流程，算法完整结构，训练好的超参数，主要功能是接受输入，进行预测，并得到预测结果。',
      },{
        title: '查看所有数据集', href: '/index/dataset',
        description: '支持开发者针对不同的问题使用合适的数据集，也支持从从本地上传注册自定义的数据集，完成相应的机器学习任务。',
      },
    ];
    return (
      <div className={styles.homeWrapper}>
        <Card title="快捷方式" style={{ width: 450, marginRight: 20 }}>
          <List
            itemLayout="horizontal"
            dataSource={data}
            renderItem={item => (
              <List.Item onClick={()=>this.props.history.push(item.href)}>
                <List.Item.Meta
                  avatar={<Avatar src={require('../../common/1.png')} />}
                  title={<span>{item.title}</span>}
                  description={item.description}
                />
              </List.Item>
            )}
          />
        </Card>
        <Card title="最近实验" style={{ width: 450, marginRight: 20 }}>
        <List
            itemLayout="horizontal"
            dataSource={recentExperiment}
            renderItem={item => (
              <List.Item onClick={()=>this.checkExperiment(item)}>
                <List.Item.Meta
                  avatar={<Avatar size={36} icon={<Icon type="experiment"  />} style={{ backgroundColor: '#7cb305' }} />}
                  title={<span>{item.name}</span>}
                  description={'创建时间:' + item.createTime}
                />
              </List.Item>
            )}
          />
        </Card>
        <Card title="最近运行" style={{ width: 450 }}>
        <List
            itemLayout="horizontal"
            dataSource={recentRunning}
            renderItem={item => (
              <List.Item onClick={()=>this.checkRunning(item)}>
                <List.Item.Meta
                  avatar={
                    <Avatar size={24} 
                          icon={<Icon 
                            type={item.runningStatus === 0 ? "loading" : (item.runningStatus === 1 ? 'check' : 'close')}   />} 
                            style={item.runningStatus === 0 ? {backgroundColor:'#1890ff'} : (item.runningStatus === 1 ? {backgroundColor:'#52c41a'} : {backgroundColor:'#f5222d'})} 
                  />}
                  title={<span>{item.runningStatus === 0 ? "正在运行" : (item.runningStatus === 1 ? '运行成功' : '运行失败')}</span>}
                  description={'创建时间:' + item.startTime}
                />
              </List.Item>
            )}
          />
        </Card>
      </div>
    );
  }
};

const mapDispatchToProps=(dispatch)=>({
  saveProcess(ggeditorString){
    dispatch(actionCreator.saveProcess(ggeditorString))
  },
})

export default connect(null, mapDispatchToProps)(Home);

