
let x_Axis={
    type: 'category',
    data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
}
let _data=[120, 132, 101, 134, 90, 230, 210];
const _option = {
  title: {
    text: '折线图',
    left: 'center',
    fontSize: '16px',
    show: true,
    top:'top' 
    },
  tooltip: {
    trigger: 'item',
    formatter:(params)=>{
    //   console.log(params.data[2]);
      return "深度 :"+params.data[0]+"<br>"+"电阻率ρ :"+params.data[1];
    }
  },
  // legend: {
  //     data: ['Step Start']
  // },
  grid: {
      left: '3%',
      right: '4%',
      bottom: '3%',
      containLabel: true
  },
  toolbox: {},
  yAxis: {
      type: 'value',
      name: "电阻率(ρ)"
  },
  xAxis: {
    type: 'value',
  },
  series: 
      {
          // name: 'Step Start',
          type: 'line',
          step: 'end',
          lineStyle: {
            type: "solid",  //dotted dashed
            width: 2, 
            color: "red",
          }
      }
};

export default function stepLineInit(data=_data,xAxis=x_Axis,option=_option ){
    console.log(data,option);
    option.xAxis = {...option.xAxis,...xAxis};
    // option.yAxis = yAxis;
    //暂时默认选择第一条折线
    option.series.data = data;
    return _option;
}