import React, { Component } from 'react'
import {Form, Input, Button, message } from 'antd'
import {createProcess, cloneProcess } from '../../../../api/process'

class ProcessModal extends Component{
  constructor(props){
    super(props);
  }

  submit=()=>{
    const { getFieldValue, validateFields } = this.props.form
    validateFields(err => {
      if(!err){
        let params = {
          workflowName: getFieldValue('processName'),
          tagString: getFieldValue('tags'),
          workflowDesc: getFieldValue('desc')
          }
        //判断克隆||新建
        if(this.props.cloneId){
          params={...params,workflowId:this.props.cloneId};
          console.log('克隆流程')
         
          cloneProcess(params).then((res)=>{
            const newRes=res.data.meta;
            if(newRes.code==='001'){
              message.success(newRes.message)
            }else{
              message.error(newRes.message)
            }
            //关闭modal
             this.props.close();
          }).catch(err=>{
            console.log(err)
          })
        }else{
          console.log('新建流程')
          params={...params,userId:2};
          createProcess(params).then((res)=>{
            const newRes=res.data.meta;
            if(newRes.code==='001'){
              message.success(newRes.message)
            }else{
              message.error(newRes.message)
            }
            //关闭modal
            this.props.close();
          }).catch(err=>{
            console.log(err)
          })
        }
        
      }
    });
  }
 
  //重置
  reset=()=>{
    this.props.form.resetFields();
  }

  render(){
    const { getFieldDecorator } = this.props.form;
    return(
      <Form labelAlign='left'> 
        <Form.Item label='流程名称'>
          {getFieldDecorator('processName',{rules: [{ required: true, message: 'Please input!' }]})(<Input placeholder="请输入流程名称"/>)} 
        </Form.Item>


        <Form.Item label='流程标签'
          name='tags'
        >
          {getFieldDecorator('tags', {initialValue:'',rules: [{ required: true, message: 'Please input!' }]})
          (<Input.TextArea 
            placeholder='请输入标签，并以 , 分割'/>
            )}
        </Form.Item>

        <Form.Item label='流程描述'>
          {getFieldDecorator('desc',{initialValue:''})(<Input.TextArea placeholder="请输入流程描述" />)} 
        </Form.Item>
        <Form.Item style={{textAlign:'center'}}>
          <Button style={{marginRight:'5px'}} type='primary'
            onClick={this.reset}
          >
            重置
          </Button>
          <Button style={{marginLeft:'5px'}} type='primary'
            onClick={this.submit}
          >
            {this.props.isClone ? '克隆':'新建'}
          </Button>
        </Form.Item>
        
      </Form>
    )
  }
}

export default Form.create()(ProcessModal);