import * as constants from './constants';

const defaultState = {
  processData:'',   // 流程图数据  从后端传过来  是一个字符串
  processId: '',
  paramsData: {},  // 用于保存实验参数
  conversationId: '',   // 用于保存SSE的id

  processState: 0,      // 流程是否正在运行   0表示否  1表示是
  nodesId: {},           // 流程运行时保存选中的节点
  xmlString: '',
  GGEditorObj: '',
  runningResult: [],

  currentNode: undefined,
  label: '',
  isML: '1',  // 当前是机器学习流程还是深度学习流程  机器学习 1， 深度学习 0
};

export default(state=defaultState, action) => {
  const newState = JSON.parse(JSON.stringify(state));
  switch(action.type) {
    case(constants.SAVE_PROCESS):
      return {...newState, processData:action.data}
    case(constants.SAVE_PROCESSID):
      return {...newState, processId:action.processId}
    case(constants.SAVE_PARAMS):
      let paramsData = action.name ? {...newState.paramsData, [action.name]: action.param} : {}
      return {...newState, paramsData}
    case(constants.SAVE_CONVERID):
      return {...newState, conversationId: action.conversationId}
    case constants.SAVE_NODESID :
      const { nodesId } = action;
      return { ...newState, nodesId };
    case constants.SAVE_RESULT_LIST :
      const { resultListData, isModel, processLogId } = action;
      return { ...newState, resultList:resultListData, isModel, processLogId };
    case constants.SAVE_PROCESS_STATE :
      const { processState } = action;
      return { ...newState, processState };
    case constants.SAVE_PROJECT :
      const { obj, xmlString } = action;
      return { ...newState, GGEditorObj:obj, xmlString };
    case constants.SAVE_COMPONENT_INFO:
      return { ...newState, componentInfo:action.componentInfo }
    case constants.SAVE_ITEM :
      newState.currentNode = action.item; 
      return newState;
    case constants.CHANGE_CURRENT_KEY :
      newState.configCurrentKey = action.key;
      return newState;
    case constants.SAVE_RUNINGID:   // 运行试验时后台返回的运行id 
      newState.experimentRunningId = action.experimentRunningId;
      return newState
    case constants.SAVE_RUNING_RESULT: // 运行的中间结果
      if(action.result){
        if(action.result.resultList){
          let index = getResultIndex(newState.runningResult, action.result)
          newState.runningResult[index] = action.result
        }else{
          newState.runningResult = [...newState.runningResult, action.result]
        }
      }else{
        newState.runningResult = []
      }
      return newState
    case constants.SAVE_CHANGEML: // 机器学习还是深度学习
      newState.isML = action.isML
      return newState
    default:
      return state;
  }
}

const  getResultIndex = (resultArray, result) =>{
  return resultArray.findIndex(value => {
    return value.componentName === result.componentName
  })
}
