import React,{Component} from 'react';
import {Form,Input} from 'antd';

class CreateExperiment extends Component{
    
    componentDidMount(){
        this.props.onRef(this);
    }
    render(){
        const {processId}=this.props;
        const {getFieldDecorator}=this.props.form;
        const layout={
            labelCol: {
                span:6
              },
              wrapperCol: {
                span:12
              },
        }
        return (
            <Form>
                {
                    processId==null?(
                        <div>
                            <Form.Item {...layout} label='流程名称'>
                                {
                                    getFieldDecorator('workflowName',{
                                        rules:[{required:true,whitespace:true}],
                                    })(<Input placeholder='请输入流程名称'/>)
                                }
                            </Form.Item>
                            <Form.Item {...layout} label='流程标签'>
                                {
                                    getFieldDecorator('workflowTags',{
                                        rules:[{required:true,whitespace:true}],
                                        initialValue:''
                                    })(<Input placeholder='请输入流程标签'/>)
                                }
                            </Form.Item>
                            <Form.Item {...layout} label='流程描述'>
                                {
                                    getFieldDecorator('workflowDesc',{
                                        rules:[{required:true,whitespace:true}],
                                        initialValue:''
                                    })(<Input placeholder='请输入流程描述'/>)
                                }
                            </Form.Item>
                        </div>
                    ):null
                }
                <Form.Item {...layout} label='实验名称'>
                    {
                        getFieldDecorator('experimentName',{
                            rules:[{required:true,whitespace:true}],
                            initialValue:''
                        })(<Input placeholder='请输入实验名称'/>)
                    }
                </Form.Item>
                <Form.Item {...layout} label='实验描述'>
                    {
                        getFieldDecorator('experimentDesc',{
                            rules:[{required:true,whitespace:true}],
                            initialValue:''
                        })(<Input placeholder='请输入实验描述'/>)
                    }
                </Form.Item>
                
            </Form>
        )
    }
}
export default Form.create()(CreateExperiment);