function getStyle(config, isActive){
    const style = {
        height: config.height, 
        width: config.width,
        position: 'absolute',
        left: config.x,
        top: config.y,
    }
    if(isActive){
        return {
            ...style,
            border: '1px dashed #999'
        }
    }else{
        return style;
    }
}

const DragableChart = (props) => {

    const {config, isActive, index} = props
    const [canDrag, setCanDrag] = useState(true)
    const dispatch = useDispatch()
    const activeIndex = useSelector(state => state.charts.activeIndex);
    useEffect(()=>{
        const domArray = Array.from(document.getElementsByClassName('react-resizable-handle'))
        //console.log(domArray[0]);
        domArray.forEach(item=>item.addEventListener('mousedown',()=>setCanDrag(false)))
        domArray.forEach(item=>item.addEventListener('mouseup',()=>setCanDrag(true)))
    },[])
    useEffect(()=>{
        const domArray = Array.from(document.getElementsByClassName('react-resizable-handle'))
        domArray.forEach(item=>item.style.visibility = 'hidden')
        domArray[activeIndex].style.visibility = 'visible'
    },[activeIndex])
    const [{ isDragging },drag] = useDrag({
        item:{
            type:CHART_COMPONENT
        },
        begin:() => {
            const {x: wrapperX, y: wrapperY} = document.getElementById('chart-canvas')==null?{x:300,y:-4}
        :document.getElementById('chart-canvas').getBoundingClientRect()
            // const {x: wrapperX, y: wrapperY} = document.getElementById('chart-canvas').getBoundingClientRect()
            const {pageX, pageY} = window.event
            const x = pageX - wrapperX - config.x
            const y = pageY - wrapperY -  config.y
            return {
                type:CHART_COMPONENT,
                x,
                y
            }
        },
        collect: (monitor) => ({
            isDragging: monitor.isDragging(),
        }),
        canDrag: (monitor) => {
            return canDrag && isActive
        }
    })

    const changeActiveIndex = () =>{
        dispatch(actionCreator.changeActiveIndex(index))
    }

    return (
        //这里宽高重写,虚线需要+2
        <div style={{...getStyle(config, isActive), 
                    opacity:isDragging ? 0.5 : 1 , 
                    zIndex : isActive? 10 : 1 ,
                    height: config.height+2, 
                    width: config.width+2,
                }} 
             ref={drag} 
             onClick={changeActiveIndex}>
            {props.children}
        </div>
    )
}


export default function ChartRender(props){
    const {config, isActive, index} = props

    const {width, height} = document.getElementById('chart-canvas')==null?{width:1000,height:800}
        :document.getElementById('chart-canvas').getBoundingClientRect()
    const {width:w, height:h, left, top} = getStyle(config, isActive)
    const [currentWidth, setCurrentWidth] = useState(w)
    const [currentHeight, setCurrentHeight] = useState(h)
    const dispatch = useDispatch()
    const resizeCharts = (e,{size}) => {
        console.log(e);
        setCurrentWidth(size.width)
        setCurrentHeight(size.height)
        console.log(currentWidth,currentHeight);
        dispatch(actionCreator.changeSize(size))
    }

    return (
        <Resizable  onResize={resizeCharts} width={currentWidth} height={currentHeight} maxConstraints={[width-config.x, height-config.y]}>
            {/* <div style={{width:w, height:h}}> */}
            <DragableChart config={config} isActive={isActive} index={index} >
                <ReactEcharts
                    option={config.options}
                    notMerge={true}
                    style={{width:w, height:h, backgroundColor:'#fafafa'}}
                    // echarts={echarts}
                    // theme='dark'
                    // onChartReady={this.onChartReadyCallback}
                    // onEvents={EventsDict}
                    // opts={} 
                    lazyUpdate={true}
                />
            </DragableChart>
            {/* </div> */}
        </Resizable>
        // </DragableChart>
    )
}
