import React, { Component } from 'react';
import {Button, Table, Divider, message} from 'antd'
import { getTrashModelData, restoreModel, deepDeleteModel} from '../../api/model'


class Trash extends Component{
  constructor(props){
    super(props);
    this.state={
      trashSelectedRowKeys:[],
      dataSource:[],
      page:1,
      total:1,
      loading:false
    }
  }
  
  componentDidMount(){
    this.getTrashModelData()
  }

  //回收站选择框改变
  onSelectChange = (trashSelectedRowKeys) => {
    this.setState({ trashSelectedRowKeys });
  };

  //获取模型回收站
  getTrashModelData=()=>{
    this.setState({
      loading: true,
      trashSelectedRowKeys:[]
    })
    const params = {
      pageNum: this.state.page,
      pageSize: 10,
      userId:2
    }
    const {page} = this.state
    getTrashModelData(params).then((res)=>{
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['Model List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*10, key: item.id}
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total  
          })
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
        console.log(err)
    })
  }
  
  //还原操作
  restore=(ids)=>{
    let params={'modelId':String(ids)}
    console.log(params)
    restoreModel(params).then(res=>{
      if(res){
        if(res.data.meta.code == '001'){
          message.success(res.data.meta.message)
          this.getTrashModelData()
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
      console.log(err)
    })
  }
  //彻底删除
  deepDelete=(ids)=>{
    let params={'modelId':String(ids)}
    console.log(params)
    deepDeleteModel(params).then(res=>{
      if(res){
        if(res.data.meta.code == '001'){
          message.success(res.data.meta.message)
          this.getTrashModelData()
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
      console.log(err)
    })
  }

  render(){
    const {dataSource,trashSelectedRowKeys}=this.state;
    //回收站表格结构
    const trashColumns = [
      {title: '模型id',dataIndex: 'id',},
      {title: '模型名称', dataIndex: 'name',},
      {title: '模型描述', dataIndex: 'modelDesc',width:'120px'},
      {title: '操作',dataIndex: 'action',align:'center',
        render:(text,record)=>(
          <>
          <a onClick={()=>this.restore(record.id)}>还原</a>
          <Divider type='vertical'/>
          <a onClick={()=>this.deepDelete(record.id)}>彻底删除</a>
          </>
        )},
    ];
    return(
      <div>
        <Table 
            bordered size='small'
            columns={trashColumns} 
            dataSource={dataSource} 
            // pagination={false}
            rowSelection={
              { selectedRowKeys:trashSelectedRowKeys,
                onChange: this.onSelectChange
              }
            }
          />
          <div style={{textAlign:'left'}}>
            <Button style={{margin:'5px 5px 0 0'}} type='primary'
              onClick={()=>this.restore(trashSelectedRowKeys)}
            >
              批量还原
            </Button>
            <Button style={{margin:'5px 0 0 5px'}} type='primary'
              onClick={()=>this.deepDelete(trashSelectedRowKeys)}
            >
              批量删除
            </Button>
          </div>
        </div>
    )
  }
}

export default Trash;