import Axios from '../utils/request';

//获取图表json字符串
export async function getResultJson(params) {
  return Axios({
    url:'/experimentRunningJsonResult/getResultJson',
    method: 'POST',
    params
  })
}



export async function saveChartsConfig(params) {
  let f = new FormData()
  for(let key in params){
    f.append(key, params[key])
  }
  return Axios({
    url:'/experimentRunningJsonResult/updateExperimentRunningJsonResultByRunningId',
    method: 'POST',
    data:f,
    headers: {
      'Content-Type': 'application/json',
    }
  })
}

export async function fetchChartsConfig(params) {
  return Axios({
    url:'/experimentRunningJsonResult/fetchExperimentRunningJsonResultByRunningId',
    method: 'POST',
    params
  })
}
