import React from 'react';

import * as chartType from '../chartConstants'

import {HeatMapOutlined, AreaChartOutlined, PieChartOutlined,
        BarChartOutlined, DotChartOutlined, LineChartOutlined, RadarChartOutlined
  } from "@ant-design/icons";

export default function IconItem(props){
  const {type}=props;
  switch(type){
    case chartType.HEAT_MAP: return <HeatMapOutlined/>; break;
    case chartType.LINE_CHART: return <LineChartOutlined/>; break;
    case chartType.AREA_CHART: return <AreaChartOutlined/>; break;
    case chartType.BAR_CHART: return <BarChartOutlined/>; break;
    case chartType.DOT_CHART: return <DotChartOutlined/>; break;
    case chartType.RADAR_CHART: return <RadarChartOutlined/>; break;
    case chartType.PIE_CHART: return <PieChartOutlined/>; break;

    default : return(<div>无icon</div>);
  }
}