import React, { Component } from 'react';
import {Button, Table, Divider, Badge, Radio, message,  Popconfirm, Avatar, Icon} from 'antd'
import {getTrashProcessData, deepDeleteProcess, restoreProcess} from '../../../../api/process'
import {getTrashExperimentsData, getTrashRunningData,
        restoreExperiment, deepDeleteExperiment, 
        deepDeleteExperimentRunning, restoreExperimentRunning } from '../../../../api/experimets'

class Trash extends Component{
  constructor(props){
    super(props);
    this.state={
      select:0,       //0-流程  1-实验 2-运行
      trashSelectedRowKeys:[],
      dataSource:[],
      page:1,
      total:1,
      pageSize: 10,
      loading:false
    }
  }
  
  componentDidMount(){
    this.getTrashProcessData()
  }
  //单选框改变
  onChange= e =>{
    let that = this
    this.setState({
      select: e.target.value,
      dataSource: [],
      page: 1
    }, ()=>that.getData())
  }

  getData = () => {
    const {select} = this.state
    if(select === 0){
      this.getTrashProcessData()
    }else if(select === 1){
      this.getTrashExperimentsData()
    }else{
      this.getTrashRunningData()
    }
  }
  //回收站选择框改变
  onSelectChange = (trashSelectedRowKeys) => {
    this.setState({ trashSelectedRowKeys });
  };

  //获取流程回收站
  getTrashProcessData=()=>{
    this.setState({
      loading: true,
      trashSelectedRowKeys:[]
    })
    const {page: pageNum, pageSize} = this.state
    getTrashProcessData({pageNum, pageSize, isDeleted:'1'}).then((res)=>{
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data.WorkflowVOList.map((item,index)=>{
            return { ...item, index: index+1+(pageNum-1)*10, key: item.id}
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total  
          })
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
      console.log(err)
    })
    
  }
  //获取实验回收站
  getTrashExperimentsData=()=>{
    this.setState({
      loading: true,
      trashSelectedRowKeys:[]
    })
    const {page: pageNum, pageSize} = this.state
    getTrashExperimentsData({pageNum, pageSize}).then((res)=>{
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['ExperimentRunning List'].map((item,index)=>{
            return { ...item, index: index+1+(pageNum-1)*10, key: item.id}
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total 
          })
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
      console.log(err)
    })
  }
  //获取运行回收站
  getTrashRunningData=()=>{
    this.setState({
      loading: true,
      trashSelectedRowKeys:[]
    })
    const {page: pageNum, pageSize} = this.state
    getTrashRunningData({pageNum, pageSize}).then((res)=>{
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['ExperimentRunning List'].map((item,index)=>{
            return { Workflowid:item.Workflowid,
                      ...item.ExperiemntRunning,
                     index: index+1+(pageNum-1)*10,
                    key: item.ExperiemntRunning.id}
          })
          console.log(dataSource)
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total  
          })
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
      console.log(err)
    })
  }
  
  //还原操作
  restore=(ids)=>{
    const {select}=this.state;
    if(select===0){
      let params={'workflowIds':String(ids)}
      console.log(params)
      restoreProcess(params).then(res=>{
        this.showMessage(res)
        this.getTrashProcessData()
      }).catch(err=>{
        console.log(err)
      })
    }else if(select===1){
      let params={'experimentIds':String(ids)}
      console.log(params)
      restoreExperiment(params).then(res=>{
        this.showMessage(res)
        this.getTrashExperimentsData()
      }).catch(err=>{
        console.log(err)
      })
    }else{
      let params={'runningIds':String(ids)}
      console.log(params)
      restoreExperimentRunning(params).then(res=>{
        this.showMessage(res)
        this.getTrashRunningData()
      }).catch(err=>{
        console.log(err)
      })
    }
  }
  //方法执行结束的message
  showMessage=(res)=>{
    if(res){
      if(res.data.meta.code == '001'){
        message.success(res.data.meta.message)
      }else{
        message.error(res.data.meta.message)
      }
    }
  }
  //彻底删除
  deepDelete = (ids) => {
    const {select}=this.state;
    if(select===0){
      let params={'workflowIds':String(ids)}
      console.log(params)
      deepDeleteProcess(params).then(res=>{
        this.showMessage(res)
        this.getTrashProcessData()
      }).catch(err=>{
        console.log(err)
      })
    }else if(select===1){
      let params={'experimentIds':String(ids)}

      deepDeleteExperiment(params).then(res=>{
        this.showMessage(res)
        this.getTrashExperimentsData()
      }).catch(err=>{
        console.log(err)
      })
    }else{
      let params={'runningIds':String(ids)}
      console.log(params)
      deepDeleteExperimentRunning(params).then(res=>{
        this.showMessage(res)
        this.getTrashRunningData()
      }).catch(err=>{
        console.log(err)
      })
    }
  }

  onPageChange = (page)=>{
    let that = this
    this.setState({page}, () => that.getData())
  }

  render(){
    const {dataSource,select,trashSelectedRowKeys, page}=this.state;
    //回收站流程表格结构id
    const trashProcessColumns = [
      // {title: '流程id',dataIndex: 'id',key: 'id'},
      { key: 'icon',render: ()=><Avatar size={36} icon={<Icon type="apartment"  />} style={{ backgroundColor: '#27afff' }} />},
      {title: '名称', dataIndex: 'name',key:'name'},
      {title: '操作',dataIndex: 'action',
        render:(text,record)=>(
          <>
          <a onClick={()=>this.restore(record.id)}>还原</a>
          <Divider type='vertical'/>
          <a onClick={()=>this.deepDelete(record.id)}>彻底删除</a>
          </>
        )},
    ];
     //回收站实验表格结构
     const trashExperimentsColumns = [
      // {title: '实验id',dataIndex: 'id',key:'id'},
      { key: 'icon',render: ()=><Avatar size={36} icon={<Icon type="experiment"  />} style={{ backgroundColor: '#7cb305' }} />},
      {title: '名称', dataIndex: 'name',key:'name'},
      {title: '所属流程', dataIndex: 'fkWorkflowId',key:'fkWorkflowId'},
      {title: '操作',dataIndex: 'action',
        render:(text,record)=>(
          <>
          <Popconfirm
            title="可能会还原所属流程，继续操作？"
            onConfirm={()=>this.restore(record.id)}
            // onCancel={cancel}
            okText="是"
            cancelText="否"
          >
              <a>还原</a>
          </Popconfirm>
          
          <Divider type='vertical'/>
          <a onClick={()=>this.deepDelete(record.id)}>彻底删除</a>
          </>
        )},
    ];
    //回收站实验运行表格结构
    const trashRunningColumns = [
      // {title: 'id',dataIndex: 'id',key:'id'},
      { key: 'icon',render: ()=><Avatar size={36} icon={<Icon type="file-sync" />} style={{ backgroundColor: '#13c2c2' }} />},
      {title: '所属实验', dataIndex: 'fkExperimentId', key:'fkExperimentId'},
      {title: '开始时间', dataIndex: 'startTime', ellipsis: true,key:'startTime'},
      {
        title: '运行状态',key: 'runningStatus',dataIndex: 'runningStatus',
        render: (text,record) => (
          <span>
            <Badge status={text===0 ? 'processing' : text===1 ? 'success' : 'error'} />
            {text===0 ? '运行中' : text===1 ? '成功' : '失败'}
          </span>
        ),
      },
      {title: '操作',dataIndex: 'action',
        render:(text,record)=>(
          <>
          <Popconfirm
            title="可能会还原所属实验和流程，继续操作？"
            onConfirm={()=>this.restore(record.id)}
            // onCancel={cancel}
            okText="是"
            cancelText="否"
          >
              <a>还原</a>
          </Popconfirm>
          <Divider type='vertical'/>
          <a onClick={()=>this.deepDelete(record.id)}>彻底删除</a>
          </>
        )},
    ];
    return(
      <div>
        <div style={{ marginBottom: 16 }}>
          {/* 可以修改为下拉框 */}
          <Radio.Group onChange={this.onChange} value={this.state.select}>
            <Radio value={0}>流程回收站</Radio>
            <Radio value={1}>实验回收站</Radio>
            <Radio value={2}>运行回收站</Radio>
          </Radio.Group>
          
        </div>
        <Table 
            size='middle'
            columns={select===0 ? trashProcessColumns:select===1?trashExperimentsColumns : trashRunningColumns} 
            dataSource={dataSource} 
            pagination={{
              onChange: this.onPageChange,
              current:this.state.page,
              pageSize: 10,
              total: this.state.total
            }}
            rowSelection={
              { 
                selectedRowKeys:trashSelectedRowKeys,
                onChange: this.onSelectChange
              }
            }
          />
          <div style={{textAlign:'left'}}>
            <Button style={{marginRight:'5px'}} type='primary'
              onClick={()=>this.restore(trashSelectedRowKeys)}
            >
              批量还原
            </Button>
            <Button style={{marginLeft:'5px'}} type='primary'
              onClick={()=>this.deepDelete(trashSelectedRowKeys)}
            >
              批量删除
            </Button>
          </div>
        </div>
    )
  }
}

export default Trash;