import React, { Component } from 'react';
import { Link, withRouter } from 'react-router-dom'
import { Table, Divider, Spin, message, PageHeader, Modal, Avatar, Icon, Input,Button, Form } from 'antd';
import { getModelList, deleteModel, getSearchData, editorModel,positioningModel,
     modelToComponent, downloadModel} from '../../api/model.js'
import {connect} from 'react-redux'
import {actionCreator} from '../process/store'
import Trash from './trash'
import style from './index.module.less'

@withRouter @Form.create()
class Model extends Component{
  constructor(props){
    super(props);
    this.state = {
      dataSource: [],
      loading: false,
      page: 1,
      total:1,
      search:null,
      trashVisible:false,
      editorModelVisible:false,
      modelId:null,
    }
  }

  componentDidMount(){
    this.getModelList();
  }
  //获取模型列表
  getModelList(){
    const params = {
      pageNum: this.state.page,
      pageSize: 10,
    }
    const { page } = this.state
    this.setState({loading:true})
    getModelList(params).then(res=>{
      console.log(res)
      if(res){
        this.setState({loading:false})
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['Model List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*10,key:item.id }
          })
          this.setState({
            dataSource,
            loading: false,
            total: res.data.data.Total,
          })
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
      console.log(err)
    })
  }
  //删除模型
  handleDelect = (id) =>{
    console.log('删除模型')
    const params = {
      modelId: id
    }
    deleteModel(params).then(res=>{
      if(res){
        if(res.data.meta.code == '001'){
          message.success(res.data.meta.message)
          this.getModelList()
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
      console.log(err)
    })
  }
  //获取搜索结果
  getSearchData = value => {
    let params={
      modelName : value,
      pageNum: this.state.page,
      pageSize: 10,
      userId:2
    }
    const {page}=this.state
    getSearchData(params).then(res=>{
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['model List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*10,key:item.id}
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total
          })
        }else{
          message.error(res.data.meta.message)
          this.setState({ loading: false })
        }
      }
    }).catch(err=>{
      this.setState({ loading: false })
      console.log(err)
    })
  }
  //搜索模型
  handleSearch = value => {
    let that = this
    this.setState({
      page: 1
    }, function(){
      that.getSearchData(value)      
    })
  }
  //换页
  changePage = () => {
    const { search } = this.state
    search ? this.getSearchData(search) : this.getModelList()
  }


  //下载模型
  downloadModel=(model)=>{
    downloadModel({modelId: model.id}).then(res=>{
      if(res){
        var blob = new Blob([res.data], {type: 'application/octet-stream'});
        // FileReader主要用于将文件内容读入内存
        var reader = new FileReader();
        reader.readAsDataURL(blob);
        // onload当读取操作成功完成时调用
        reader.onload = function(e) {
          var a = document.createElement('a');
          // 获取文件名fileName
          var fileName = res.headers["content-disposition"].split(".");
          fileName = model.name + '.' + fileName[fileName.length - 1];
          fileName = fileName.replace(/"/g, "");
          a.download = fileName;
          a.href = e.target.result;
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
        }
      }else{
        message.error('下载失败！')
      }
    })
    .catch(err=>message.error('下载失败！'))
  }
  
  //编辑模型
  editorModel=(model)=>{
    this.setState({
      currentModel: model,
      editorModelVisible:true
    })
  }

  submit=()=>{
    const { getFieldValue,validateFields } = this.props.form
    validateFields(err => {
      if(!err){
        let params = {
          modelName: getFieldValue('name'),
          modelDesc: getFieldValue('desc')
          }
          console.log(params)
        // 编辑模板
        params={...params, modelId: this.state.currentModel.id}
        editorModel(params).then((res)=>{
          const newRes=res.data.meta;
          if(newRes.code==='001'){
            message.success(newRes.message)
            this.setState({editorModelVisible: false, currentModel: undefined})
            this.getModelList()
          }else{
            message.error(newRes.message)
          }
        }).catch(err=>{
          console.log(err)
        })
      }
    });
  }

  toComponent = (model) => {
    this.setState({
      componentVisible: true,
      currentModel: model
    })
  } 

  //定位模型
  positionModel=(id)=>{
    //此处是模型id
    console.log(id);
    const params = {
      modelId: id,
    }
    positioningModel(params).then(res=>{
      if(res&&res.data.data!=null){
        let data=res.data.data;
        //后端接口写的有问题
        // console.log(data[0]); 流程id
        // console.log(data[1]); 实验id
        // console.log(data[2]); 运行id
        // console.log(data[3]);  ggeditor
        this.props.saveProcess(data[3]);
        this.props.history.push({
          pathname: '/index/experiment/run',
          query:{
            runningId: data[2]
          }
        })
      }else{
          message.error(res.data.meta.message);
      }
    }).catch(err=>{
      message.error(err);
    })

    
  }

  modelToComponent = () => {
    const { getFieldValue,validateFields } = this.props.form
    validateFields(err => {
      if(!err){
        let params = {
          componentDec: getFieldValue('componentDec'),
          componentName: getFieldValue('componentName'),
          tag: getFieldValue('tag')
        }
        // 编辑模板
        params={...params, modelId: this.state.currentModel.id}
        modelToComponent(params).then((res)=>{
          const newRes= res.data.meta;
          if(newRes.code==='001'){
            message.success(newRes.message)
            this.setState({componentVisible: false, currentModel: undefined})
          }else{
            message.error(newRes.message)
          }
        }).catch(err=>{
          console.log(err)
        })
      }
    });
  }

  //重置
  reset=()=>{
    this.props.form.resetFields();
  }

  render(){
    const { total, dataSource, trashVisible, editorModelVisible, currentModel, componentVisible } = this.state
    const { getFieldDecorator } = this.props.form;

    const columns = [
      { align: 'center', render: ()=><Avatar size={40} icon={<Icon type="funnel-plot" />} style={{ backgroundColor: '#27afff' }} />},
      // {title:'模型id',dataIndex:'id',key:'id'},
      {title:'模型名称', dataIndex: 'name', key: 'name'},
      // {title: '实验运行id',dataIndex: 'fkRunningId', key: 'fkRunningId',
      //   render:(text,record)=>(
      //   <a onClick={()=>this.locateExperiments(text)}>{text}</a>
      //   )
      // },
      {title: '模型描述',dataIndex:'modelDesc',key: 'modelDesc'},
      {title: '创建时间',dataIndex:'createTime',key: 'createTime'},
      {title: '操作',key: 'action', align:'center',
        render: (text, record) => (
          <span>
            <a onClick={()=>this.editorModel(record)}>编辑</a>
            <Divider type="vertical" />
            <a onClick={()=>this.handleDelect(record.id)}>删除</a>
            <Divider type="vertical" />
            <a onClick={()=>this.downloadModel(record)}>下载</a>
            <Divider type="vertical" />
            <a onClick={()=>this.toComponent(record)}>标记为组件</a>
            <Divider type="vertical" />
            <a onClick={()=>this.positionModel(record.id)}>定位模型</a>
          </span>
        ),
      },
    ]

    return(
    <div className={style.contentWrap}>
      <PageHeader
        style={{border: '1px solid rgb(235, 237, 240)', marginBottom: '19px'}}
        title={<span onClick={()=>this.getModelList()}>模型管理</span>}
        extra={[
            <Button type='primary' onClick={()=>{this.setState({trashVisible:true})}}>回收站</Button>,
            <Input.Search style={{width:'200px'}} onSearch={value => this.handleSearch(value)} placeholder="搜索模型" />
        ]}
      />
      <Spin spinning={this.state.loading} tip="loading...">
        <Table 
          dataSource={dataSource} 
          columns={columns} 
          pagination={{
            onChange: page => {
              console.log(page);
              this.setState({page},()=>{
                this.changePage()
              });
            },
            current:this.state.page,
            total,
            pageSize: 10,
          }}
        />
      </Spin>
      
      {trashVisible&&
      <Modal
        title="回收站"
        visible={trashVisible}
        footer={null}
        onCancel={()=>{
          this.setState({trashVisible:false})
          this.getModelList()
          }}>
        <Trash/>
      </Modal>}

      {editorModelVisible &&
        <Modal
          title="编辑模型"
          visible={editorModelVisible}
          footer={null}
          onCancel={()=>{this.setState({editorModelVisible:false, currentModel:undefined})}}>
          <Form labelAlign='left'> 
            <Form.Item label='模型名称'>
              {getFieldDecorator('name',
                {initialValue: currentModel.name, rules: [{ required: true, message: 'Please input!' }]}
                )(
                <Input placeholder="请输入模型名称"/>
              )} 
            </Form.Item>

            <Form.Item label='模型描述'>
              {getFieldDecorator('desc',{initialValue: currentModel.modelDesc})(<Input.TextArea placeholder="请输入模型描述" />)} 
            </Form.Item>
            <Form.Item style={{textAlign:'center'}}>
              <Button style={{marginRight:'5px'}} type='primary'
                onClick={this.reset}
              >
                重置
              </Button>
              <Button style={{marginLeft:'5px'}} type='primary'
                onClick={this.submit}
              >
                编辑
              </Button>
            </Form.Item>
          </Form>
        </Modal>
      }

      {componentVisible &&
        <Modal
          title="创建自定义模型组件"
          visible={componentVisible}
          footer={null}
          onCancel={()=>{this.setState({componentVisible:false, currentModel:undefined})}}>
          <Form labelAlign='left'> 
            <Form.Item label='组件名称'>
              {getFieldDecorator('componentName',
                {rules: [{ required: true, message: 'Please input!' }]}
                )(
                <Input placeholder="请输入组件名称"/>
              )} 
            </Form.Item>
            <Form.Item label='组件标签'>
              {getFieldDecorator('tag',
                {rules: [{ required: true, message: 'Please input!' }]}
                )(
                <Input placeholder="请输入组件标签，以逗号分隔。"/>
              )} 
            </Form.Item>
            <Form.Item label='组件描述'>
              {getFieldDecorator('componentDec')(<Input.TextArea placeholder="请输入组件描述" />)} 
            </Form.Item>
            <Form.Item style={{textAlign:'center'}}> 
              <Button style={{marginLeft:'5px'}} type='primary'
                onClick={this.reset}
              >
                重置
              </Button>
              <Button style={{marginLeft:'5px'}} type='primary'
                onClick={this.modelToComponent}
              >
                标记
              </Button>
            </Form.Item>
          </Form>
        </Modal>
      }
    </div>
    )
  }
}
const mapDispatchToProps=(dispatch)=>({
    saveProcess(ggeditorString){
      dispatch(actionCreator.saveProcess(ggeditorString))
    }
  })


export default connect(null,mapDispatchToProps)(Model);