import Axios from '../utils/request';
import axios from 'axios'

export async function getUploadDataset(params){
    return Axios({
        url:'/dataset/getUserDataset',
        method:'GET',
        params
    })
}
export async function getPublicDataset(params){
    return Axios({
        url:'/dataset/getPublicDataset',
        method:'GET',
        params
    })
}

export async function getDldatasetInTrash(params){
    return Axios({
        url:'/dlDataset/getDlDatasetInTrash',
        method:'POST',
        params
    })
}

export async function insertUserDataset(params){
    const formData = new FormData();
    for(let i in params){
        formData.append(i, params[i])
    }

    return Axios({
        url:'/dlDataset/insertUserDataset',
        method:'POST',
        data:formData
    })
}

