import React, { Fragment } from 'react';
import { Card, Form, Input, Menu, Select, Tooltip, Icon } from 'antd';
import { withPropsAPI } from 'gg-editor';

const { Item } = Form;
const { Option } = Select;

class SVMNLine extends React.Component {

  handleSubmit = (e) => {
    if (e && e.preventDefault) {
      e.preventDefault();
    }

    const { form, propsAPI } = this.props;
    const { getSelected, executeCommand, update } = propsAPI;

    setTimeout(() => {
      form.validateFieldsAndScroll((err, values) => {
        if (err) {
          return;
        }

        const item = window.ITEM;

        if (!item) {
          return;
        }

        executeCommand(() => {
          update(item, {
            ...values,
          });
        });
      });
    }, 0);
  };
 
  render() {
    console.log("子组件",this.props.detail);
    const { 
      nodeType,
      nodeRemarks,
      conf
     } = this.props.detail;
    const { TextArea } = Input;
    const { form } = this.props;
    return (
      <Fragment>
        <Form onSubmit={this.handleSubmit}>
          <Item label="节点名称">
            {form.getFieldDecorator('nodeType', {
              initialValue: nodeType,
              rules:[{
                validator:  (rule, val, callback)=>{
                  if(val.length > 12){
                    callback('节点名称的最大长度为12！')
                  }
                  callback();
                }
              }]
            })
            (<Input onBlur={this.handleSubmit} />)}
          </Item>
          <Item label="备注">
            {form.getFieldDecorator('nodeRemarks', {
              initialValue: nodeRemarks,
            })
            (<TextArea onBlur={this.handleSubmit} rows={3}/>)}
          </Item>
          <Menu onClick={this.props.handleClick} selectedKeys={[this.props.configCurrentKey]} mode="horizontal">
            <Menu.Item key="configuration">
              参数配置
            </Menu.Item>
            {/* <Menu.Item key="description">
              描述说明
            </Menu.Item> */}
          </Menu>
          <Card title='算法IO参数' bordered={false} bodyStyle={{background: '#fff'}}>
            <Item label={
              <span>
              kernel&nbsp;
                <Tooltip title="核函数类型">
                  <Icon type="question-circle-o" style={{color:'#5f6267'}} />
                </Tooltip>
              </span>
              } required>
              {form.getFieldDecorator('conf.kernel', {
                initialValue: conf.kernel,
                rules: [{required: true, message: 'Please input data!'}],
              })
              (<Select onBlur={this.handleSubmit}>
                <Option value="linear">linear</Option>
                <Option value="poly">poly</Option>
                <Option value="rbf">rbf</Option>
                <Option value="sigmoid">sigmoid</Option>
                <Option value="precomputed">precomputed</Option>
              </Select>)}
            </Item>
            <Item label={
              <span>
              degree&nbsp;
                <Tooltip title="多项式核函数的阶数">
                  <Icon type="question-circle-o" style={{color:'#5f6267'}} />
                </Tooltip>
              </span>
              } required>
              {form.getFieldDecorator('conf.degree', {
                initialValue: conf.degree,
                rules: [{required: true, pattern: new RegExp(/^[1-9]\d*$/, "g"), message: 'Please input number data!'}],
              })
              (<Input onBlur={this.handleSubmit} disabled={ form.getFieldValue('conf.kernel') !== 'poly' } />)}
            </Item>
            <Item label={
              <span>
              C&nbsp;
                <Tooltip title="惩罚系数">
                  <Icon type="question-circle-o" style={{color:'#5f6267'}} />
                </Tooltip>
              </span>
              } required>
              {form.getFieldDecorator('conf.C', {
                initialValue: conf.C,
                rules: [{required: true, message: 'Please input data!'}],
              })
              (<Input onBlur={this.handleSubmit} />)}
            </Item>
          </Card>
        </Form>
      </Fragment>
    );
  }
};

export default Form.create()(withPropsAPI(SVMNLine));