var base = 1;

export const getSpecialNodes = (obj, params) => {
  const nodes = obj.nodes;
  const edges = obj.edges;
  //分开节点
  let separateNodes = [];
  //聚合节点
  let gatherNodes = [];
  // console.log('params',params)
  //既是分开又是聚合
  let separateAndGatherNodes = [];
  //第一个节点和最后一个节点
  let firstNode = [], lastNode;
  for (let node of nodes) {
    let sourceNum = 0;
    let targetNum = 0;
    for (let edge of edges) {
      if(edge.source === node.id){
        edge.source = base;
        sourceNum++;
      }
      if(edge.target === node.id){
        edge.target = base
        targetNum++;
      }
    }
    if(params){
      params[base]=params[node.id];
      delete params[node.id];
    }
    node.id = base;
    base++;
    if(targetNum === 0) {
      firstNode.push(node)
    }
    if(sourceNum === 0) {
      lastNode = node;
    }
    if(sourceNum > 1 && targetNum <=1 ){
      separateNodes.push(node);
    }
    if(targetNum > 1 && sourceNum <=1) {
      gatherNodes.push(node);
    }
    if(targetNum > 1 && sourceNum > 1) {
      separateAndGatherNodes.push(node);
    }
  }
  return ({
    firstNode,
    lastNode,
    separateNodes,
    gatherNodes,
    separateAndGatherNodes
  })
};

//获取 去掉分支节点到后继节点的边，前驱节点到聚合节点的边，以及分支聚合节点到后继节点的边，前驱节点到分支聚合节点的边  剩下的普通边
const getEdges = (separateNodes, gatherNodes, separateAndGatherNodes, edges) => {
  const simpleEdges = edges.slice(0, edges.length);
  // console.log('getEdges separateNodes', separateNodes);
  // console.log('getEdges gatherNodes', gatherNodes);
  // console.log('getEdges separateAndGatherNodes', separateAndGatherNodes);
  // console.log('getEdges edges', simpleEdges);
  for (let e = simpleEdges.length-1; e >= 0; e--) {
    // console.log('for edge',simpleEdges[e]);
    //separateNodes  去掉分支节点到后继节点的边
    if(separateNodes) {
      for(let i = 0; i < separateNodes.length; i++) {
        if( separateNodes[i].id === simpleEdges[e].source ) {
          simpleEdges.splice(e, 1);
        }
      }
    }
  }
  console.log(simpleEdges.length);
  for (let e = simpleEdges.length-1; e >= 0; e--) {  
    console.log('for edge',simpleEdges[e]);
    //gatherNodes  前驱节点到聚合节点的边
    if(gatherNodes) {
      for(let i = 0; i < gatherNodes.length; i++) {
        console.log(gatherNodes[i].id === simpleEdges[e].target)
        if( gatherNodes[i].id === simpleEdges[e].target ) {
          simpleEdges.splice(e, 1);
          console.log('for gatherNodes',simpleEdges);
        }
      }
    }
  }
  console.log(simpleEdges.length);
  for (let e = simpleEdges.length-1; e >= 0; e--) {  
    console.log('getEdges', simpleEdges);
    //separateAndGatherNodes  分支聚合节点到后继节点的边，前驱节点到分支聚合节点的边
    if(separateAndGatherNodes) {
      for(let i = 0; i < separateAndGatherNodes.length; i++) {
        if( separateAndGatherNodes[i].id === simpleEdges[e].target || separateAndGatherNodes[i].id === simpleEdges[e].source) {
          simpleEdges.splice(e, 1);
        }
      }
    } 
  }
  console.log('getEdges', simpleEdges);
  return simpleEdges;
}

//获取后继节点
const getNextNodesID = (node, edges) => {
  let nextNodesID = [];
  for (let edge of edges) {
    if(edge.source === node.id) {
      nextNodesID.push(edge.target)
    }
  }
  return nextNodesID
}


//获取前驱节点
const getPriorNodesID = (node, edges) => {
  let priorNodesID = [];
  for (let edge of edges) {
    if(edge.target === node.id) {
      priorNodesID.push(edge.source)
    }
  }
  return priorNodesID
}

//判断后继节点时候为聚合节点,是就返回该聚合节点在聚合节点数组中的下标
const isGatherNode = (nextNodeID, gatherNodes) => {
  console.log('isGatherNode');
  console.log(nextNodeID);
  console.log(gatherNodes);
  for (let i=0; i<gatherNodes.length; i++) {
    console.log(gatherNodes[i].id);
    console.log(nextNodeID);
    if(gatherNodes[i].id === nextNodeID) {
      console.log('gather:  '+i);
      return i;
    }
  }
}


//判断后继节点时候为分开节点,是就返回该分开节点在分开节点数组中的下标
const isSeparateNode = ( priorNodeID, separateNodes) => {
  for (let i=0; i<separateNodes.length; i++) {
    if(separateNodes[i].id === priorNodeID) {
      console.log('separate:  '+i);
      return i;
    }
  }
}



export const getXML = (nodes,edges,specialNodes) => {
  // console.log(nodes);
  // console.log('getXML start edges', edges);
  const firstNode = specialNodes.firstNode;
  const lastNode = specialNodes.lastNode;
  const separateNodes = specialNodes.separateNodes;
  const gatherNodes = specialNodes.gatherNodes;
  const separateAndGatherNodes = specialNodes.separateAndGatherNodes;
  
  //process startEvent
  var xmlString = 
`<?xml version="1.0" encoding="UTF-8" standalone="yes"?><definitions xmlns="http://www.omg.org/spec/BPMN/20100524/MODEL" xmlns:activiti="http://activiti.org/bpmn" xmlns:bpmndi="http://www.omg.org/spec/BPMN/20100524/DI" xmlns:omgdc="http://www.omg.org/spec/DD/20100524/DC" xmlns:omgdi="http://www.omg.org/spec/DD/20100524/DI" xmlns:tns="http://www.activiti.org/test" xmlns:xsd="http://www.w3.org/2001/XMLSchema" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" expressionLanguage="http://www.w3.org/1999/XPath" id="m1563795588614" name="" targetNamespace="http://www.activiti.org/test" typeLanguage="http://www.w3.org/2001/XMLSchema">
<process id="myProcess_1" isClosed="false" isExecutable="true" processType="None">
`;

    //node
    for(let i = 0; i < nodes.length; i++) {
      console.log(nodes[i]);
      let inputString ='[input] ';
      if(nodes[i].input) {
        inputString += nodes[i].input
      } 
      let outputString =',[output] ';
      if(nodes[i].output) {
        outputString += nodes[i].output
      }
      // let conf = ',[algorithm] ';
      // console.log(nodes[i].conf);
      // if(nodes[i].conf) {
      //   for (let [key, value] of Object.entries(nodes[i].conf)) {
      //     conf += key +'=' + value +' '
      //   }
      // }  
      let priorNodesID = getPriorNodesID(nodes[i],edges);
      let priorNodesIDString = ',[prior] ';
      if(priorNodesID.length > 0) {
        for(let n=0; n<priorNodesID.length; n++) {
          priorNodesIDString += '_' + priorNodesID[n]+' '
        }
      }
      // console.log(conf);
      let endString = ',[end] '
      let nextNodesID = getNextNodesID(nodes[i],edges);
      if(nextNodesID.length > 0) {
        endString += false;
      }
      else{
        endString += true;
      }
      if(nodes[i].label === 'start'){
        xmlString += 
`<startEvent id="start_1" name="StartEvent"/>
`
      }else{
      xmlString += 
`<userTask activiti:exclusive="true" id="_${nodes[i].componentId}_${nodes[i].id}" name="${nodes[i].label}">
<documentation id="_${nodes[i].id}_D_${i + 1}"><![CDATA[${inputString}${outputString}${priorNodesIDString}${endString}]]></documentation>
</userTask>
`
      }
    }

    //separateNodes 对应的网关和sequenceFlow
    let separateGatewayID = [];
    if(separateNodes) {
      for(let i = 0; i < separateNodes.length; i++) {
        separateGatewayID.push(base);
        base++;
        xmlString +=

`<parallelGateway gatewayDirection="Unspecified" id="_${separateGatewayID[i]}" name="${separateNodes[i].label}"/>
`
      }
    }
    // console.log(separateGatewayID);
    //gatherNodes 对应的网关
    let gatherGatewayID = [];
    if(gatherNodes) {
      for(let i = 0; i < gatherNodes.length; i++) {
        gatherGatewayID.push(base);
        base++;
        xmlString +=
`<parallelGateway gatewayDirection="Unspecified" id="_${gatherGatewayID[i]}" name="${gatherNodes[i].label}"/>
`
      }
    }
    console.log(gatherGatewayID);
    //separateAndGatherNodes 对应的网关
    let separateAndGatherGatewayID = [];
    if(separateAndGatherNodes) {
      for(let i = 0; i < separateAndGatherNodes.length; i++) {
        separateAndGatherGatewayID.push(base);
        base++;
        xmlString +=
`<parallelGateway gatewayDirection="Unspecified" id="_${separateAndGatherNodes[i].id}" name="separateGatewa${separateAndGatherNodes[i].id}"/>
`
      }
    }
    // console.log(separateAndGatherGatewayID);
    //endEvent
    xmlString += 

`<endEvent id="end_1" name="EndEvent"/>
`;
    //edges 对应的sequenceFlow    
    //(要去掉分支节点到后继节点的边，前驱节点到聚合节点的边，以及分支聚合节点到后继节点的边，前驱节点到分支聚合节点的边)
    if(edges) {
      console.log('nodes',nodes);
      const simpleEdges = getEdges(separateNodes, gatherNodes, separateAndGatherNodes, edges);
      console.log(simpleEdges);
      for(let i = 0; i < simpleEdges.length; i++) {
        console.log('simpleEdges',simpleEdges[i].source)
        simpleEdges[i].id = base;
        let targetNode = nodes.filter(item=>{return(item.id == simpleEdges[i].target)});
        let sourceNode = nodes.filter(item=>{return(item.id == simpleEdges[i].source)});
        if(simpleEdges[i].source == firstNode[0].id){
          xmlString +=
`<sequenceFlow id="start" sourceRef="start_1" targetRef="_${targetNode[0].componentId}_${simpleEdges[i].target}"/>
`
        }else{
          base++;
          xmlString +=
`<sequenceFlow id="_${simpleEdges[i].id}" sourceRef="_${sourceNode[0].componentId}_${simpleEdges[i].source}" targetRef="_${targetNode[0].componentId}_${simpleEdges[i].target}"/>
`
        }
      }
    }
    //startEvent firstNode lastNode endEvent对应的sequenceFlow
    xmlString += 
`<sequenceFlow id="end" sourceRef="_${lastNode.componentId}_${lastNode.id}" targetRef="end_1"/>
`    
    //separateGateway 对应的sequenceFlow
    if(separateNodes) {
      for(let i = 0; i < separateNodes.length; i++) {
        const nextNodesID = getNextNodesID(separateNodes[i], edges);
        console.log(firstNode)
        //separateNodes[i] 到网关 对应的sequenceFlow
        let sourceNode = nodes.filter(item=>{return(item.id == separateNodes[i].id)});
        if(separateNodes[i] == firstNode[0]){
          xmlString += 
`<sequenceFlow id="start" sourceRef="start_1" targetRef="_${separateGatewayID[i]}"/>
`
        }else{
        xmlString += 
`<sequenceFlow id="_${base}" sourceRef="_${sourceNode[0].componentId}_${separateNodes[i].id}" targetRef="_${separateGatewayID[i]}"/>
`
        }
        base++;

        //网关 到 nextNodes节点  对应的sequenceFlow
        for(let j = 0; j < nextNodesID.length; j++) {
          const gatherIndex = isGatherNode(nextNodesID[j], gatherNodes);
          console.log('nextNodesID',nextNodesID);
          let targetNode = nodes.filter(item=>{return(item.id == nextNodesID[j])});
          if(gatherIndex>=0) {
            xmlString +=
`<sequenceFlow id="_${base}" sourceRef="_${separateGatewayID[i]}" targetRef="_${gatherGatewayID[gatherIndex]}"/>
`
            base++;
          }else{
            xmlString +=
`<sequenceFlow id="_${base}" sourceRef="_${separateGatewayID[i]}" targetRef="_${targetNode[0].componentId}_${nextNodesID[j]}"/>
`
          base++;
          }
        }
      }
    }

    //gatherNodes 对应的sequenceFlow
    if(gatherNodes) {
      for(let i = 0; i < gatherNodes.length; i++) {
        const priorNodesID = getPriorNodesID(gatherNodes[i], edges);
        //priorNodes 到 网关  对应的sequenceFlow
        for(let j = 0; j < priorNodesID.length; j++) {
          const separateIndex = isSeparateNode( priorNodesID[j], separateNodes);
          
          if(separateIndex>=0) {
            continue;
          }else{
          let sourceNode = nodes.filter(item=>{return(item.id == priorNodesID[j])});
          xmlString += 
`<sequenceFlow id="_${base}" sourceRef="_${sourceNode[0].componentId}_${priorNodesID[j]}" targetRef="_${gatherGatewayID[i]}"/>
`
          base++;
          }
        }
       
        //网关 到 gatherNodes[i] 对应的sequenceFlow
        let targetNode = nodes.filter(item=>{return(item.id == gatherNodes[i].id)});
        xmlString += 
`<sequenceFlow id="_${base}" sourceRef="_${gatherGatewayID[i]}" targetRef="_${targetNode[0].componentId}_${gatherNodes[i].id}"/>
`
        base++;
      }
    }

    //separateAndGatherNodes 对应的sequenceFlow
    if(separateAndGatherNodes) {
      for(let i = 0; i < separateAndGatherNodes.length; i++) {
        //作为分开节点
        
        const nextNodesID = getNextNodesID(separateAndGatherNodes, edges);
        
        //separateAndGatherNodes[i] 到网关 对应的sequenceFlow
        let sourceNode = nodes.filter(item=>{return(item.id == separateAndGatherNodes[i].id)});
        xmlString += 
`<sequenceFlow id="_${base}" sourceRef="_${sourceNode[0].componentId}_${separateAndGatherNodes[i].id}" targetRef="_${separateAndGatherGatewayID[i]}"/>
`
        base++; 

        //网关 到 nextNodes节点  对应的sequenceFlow
        for(let j = 0; j < nextNodesID.length; j++) {
          let targetNode = nodes.filter(item=>{return(item.id == nextNodesID[j])});
          xmlString += 
`<sequenceFlow id="_${base}" sourceRef="_${separateAndGatherGatewayID[i]}" targetRef="_${targetNode[0].componentId}_${nextNodesID[j]}"/>
`
          base++;
        }

        //作为聚合节点
        const priorNodesID = this.getPriorNodesID(separateAndGatherNodes, edges);
  
        //priorNodes 到 网关  对应的sequenceFlow
        for(let j = 0; j < priorNodesID.length; j++) {
          let sourceNode = nodes.filter(item=>{return(item.id == priorNodesID[j])});
          xmlString += 
`<sequenceFlow id="_${base}" sourceRef="_${sourceNode[0].componentId}_${priorNodesID[j]}" targetRef="_${separateAndGatherGatewayID[i]}"/>
`
          base++;
        }
       
        //网关 到 separateAndGatherNodes[i] 对应的sequenceFlow
        let targetNode = nodes.filter(item=>{return(item.id == separateAndGatherNodes[i].id)});
        xmlString += 
`<sequenceFlow id="_${base}" sourceRef="_${separateAndGatherGatewayID[i]}" targetRef="_${targetNode[0].componentId}_${separateAndGatherNodes[i].id}"/>
`
      }
    }


    xmlString += 
`</process></definitions>
`
    base = 1
    return xmlString;
}





  