import Axios from '../utils/request';
import axios from 'axios'

//获取模型列表
export async function getModelList(params) {
  return Axios({
    url:'/model/getModelList',
    method: 'GET',
    params
  })
}
//新建模型
export async function createModel(params) {
  return Axios({
    url:'/model/createModel',
    method: 'POST',
    params
  })
}

//编辑模型
export async function editorModel(params) {
  return Axios({
    url:'/model/editModel',
    method: 'POST',
    params
  })
}
//删除模型
export async function deleteModel(params) {
  return Axios({
    url:'/model/deleteModel',
    method: 'POST',
    params
  })
}

//获取搜索结果
export async function getSearchData(params) {
  return Axios({
    url:'/model/searchModelByName',
    method: 'POST',
    params
  })
}

//获取模型回收站列表
export async function getTrashModelData(params) {
  return Axios({
    url:'/model/getModelInTrash',
    method: 'GET',
    params
  })
}
//还原模型
export async function restoreModel(params) {
  return Axios({
    url:'/model/restoreModel',
    method: 'GET',
    params
  })
}
//彻底删除模型
export async function deepDeleteModel(params) {
  return Axios({
    url:'/model/deleteModelPermanently',
    method: 'POST',
    params
  })
}

export async function modelToComponent(params) {
  return Axios({
    url:'/model/modelToComponent',
    method: 'POST',
    params
  })
}

// 下载模型
export async function downloadModel(params) {
  return axios({
    url:'/model/downloadModelFromMinio',
    method: 'GET',
    responseType: 'blob',
    params
  })
}

// 定位模型
export async function positioningModel(params) {
  return axios({
    url:'/model/positioningModel',
    method: 'POST',
    params
  })
}
