import React, { Fragment } from 'react';
import { Card, Form, Input, Menu, Select, Tooltip, Icon } from 'antd';
import { withPropsAPI } from 'gg-editor';

const { Item } = Form;
const { Option } = Select;

class KNN extends React.Component {

  handleSubmit = (e) => {
    if (e && e.preventDefault) {
      e.preventDefault();
    }

    const { form, propsAPI } = this.props;
    const { getSelected, executeCommand, update } = propsAPI;

    form.validateFieldsAndScroll((err, values) => {
      
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });


    setTimeout(() => {
      form.validateFieldsAndScroll((err, values) => {
        console.log(values);
        if (err) {
          return;
        }

        const item = window.ITEM;

        if (!item) {
          return;
        }

        executeCommand(() => {
          update(item, {
            ...values,
          });
        });
      });
    }, 0);
  };


 
  render() {
    console.log("子组件",this.props.detail);
    const { 
      nodeType,
      nodeRemarks,
      conf
     } = this.props.detail;
    const { TextArea } = Input;
    const { form } = this.props;
    return (
      <Fragment>
        <Form onSubmit={this.handleSubmit}>
          <Item label="节点名称">
            {form.getFieldDecorator('nodeType', {
              initialValue: nodeType,
              rules:[{
                validator:  (rule, val, callback)=>{
                  if(val.length > 12){
                    callback('节点名称的最大长度为12！')
                  }
                  callback();
                }
              }]
            })
            (<Input onBlur={this.handleSubmit} />)}
          </Item>
          <Item label="备注">
            {form.getFieldDecorator('nodeRemarks', {
              initialValue: nodeRemarks,
            })
            (<TextArea onBlur={this.handleSubmit} rows={3}/>)}
          </Item>
          <Menu onClick={this.props.handleClick} selectedKeys={[this.props.configCurrentKey]} mode="horizontal">
            <Menu.Item key="configuration">
              参数配置
            </Menu.Item>
            {/* <Menu.Item key="description">
              描述说明
            </Menu.Item> */}
          </Menu>
          <Card title='算法IO参数' bordered={false} bodyStyle={{background: '#fff'}}>
            <Item label={
              <span>
              numOfNeighbors&nbsp;
                <Tooltip title="近邻数量（任意大于零的整数）">
                  <Icon type="question-circle-o" style={{color:'#5f6267'}} />
                </Tooltip>
              </span>
              } required>
              {form.getFieldDecorator('conf.numOfNeighbors', {
                initialValue: conf.numOfNeighbors,
                rules: [{required: true, pattern: new RegExp(/^[1-9]\d*$/, "g"), message: 'Please input number data!'}],
              })
              (<Input onBlur={this.handleSubmit} />)}
            </Item>
            <Item label={
              <span>
              weightsOfNeighbors&nbsp;
                <Tooltip title="近邻权重">
                  <Icon type="question-circle-o" style={{color:'#5f6267'}} />
                </Tooltip>
              </span>
              } required>
              {form.getFieldDecorator('conf.weightsOfNeighbors', {
                initialValue: conf.weightsOfNeighbors,
              })
              (<Select onBlur={this.handleSubmit}>
                <Option value="uniform">uniform</Option>
                <Option value="distance">distance</Option>
              </Select>)}
            </Item>
            <Item label={
              <span>
              algorithm&nbsp;
                <Tooltip title="计算近邻所用算法">
                  <Icon type="question-circle-o" style={{color:'#5f6267'}} />
                </Tooltip>
              </span>
              } required>
              {form.getFieldDecorator('conf.algorithm', {
                // initialValue: conf.algorithm ,
                initialValue: 'auto'
              })
              (<Select onBlur={this.handleSubmit} >
                <Option value="ball_tree">ball_tree</Option>
                <Option value="kd_tree">kd_tree</Option>
                <Option value="brute">brute</Option>
                <Option value="auto">auto</Option>
              </Select>)}
            </Item>
            <Item label={
              <span>
              p&nbsp;
                <Tooltip title="距离类型">
                  <Icon type="question-circle-o" style={{color:'#5f6267'}} />
                </Tooltip>
              </span>
              } required>
              {form.getFieldDecorator('conf.p', {
                initialValue: conf.p,
              })
              (<Input onBlur={this.handleSubmit} disabled={form.getFieldValue('conf.algorithm') === 'auto'||form.getFieldValue('conf.algorithm') === 'brute'}/>)}
            </Item>
            <Item label={
              <span>
              leafSize&nbsp;
                <Tooltip title="叶子数量（任意大于零的整数）">
                  <Icon type="question-circle-o" style={{color:'#5f6267'}} />
                </Tooltip>
              </span>
              } required>
              {form.getFieldDecorator('conf.leafSize', {
                initialValue: conf.leafSize,
                rules: [{required: true, pattern: new RegExp(/^[1-9]\d*$/, "g"), message: 'Please input number data!'}],
              })
              (<Input onBlur={this.handleSubmit} disabled={form.getFieldValue('conf.algorithm') === 'auto'||form.getFieldValue('conf.algorithm') === 'brute'} />)}
            </Item>
          </Card>
        </Form>
      </Fragment>
    );
  }
};

export default Form.create()(withPropsAPI(KNN));