import React, { Component } from 'react'
import {Form, Input, Button, message} from 'antd'

import { markTemplate } from '../../../../api/experimets'

class TemplateModal extends Component{
  constructor(props){
    super(props);
  }
  submit=()=>{
    console.log('标记模板')
    const { getFieldValue,validateFields } = this.props.form
    validateFields(err => {
      if(!err){
        let params = {
          templateName: getFieldValue('templateName'),
          tags: getFieldValue('tags'),
          templateDesc: getFieldValue('templateDesc'),
          experimentId: this.props.experimentId
        }
        markTemplate(params).then((res)=>{
          const newRes=res.data.meta;
          if(newRes.code==='001'){
            message.success(newRes.message)
          }else{
            message.error(newRes.message)
          }
          //关闭modal
          this.props.close();
          }).catch(err=>{
            console.log(err)
        })
      }
    });
  }
 
  //重置
  reset=()=>{
    this.props.form.resetFields();
  }

  render(){
    const { getFieldDecorator } = this.props.form;
    return(
      <Form labelAlign='left'> 
        <Form.Item label='模板名称'>
          {getFieldDecorator('templateName',{rules: [{ required: true, message: 'Please input!' }]})(<Input placeholder="请输入模板名称"/>)} 
        </Form.Item>
        
        <Form.Item label='模板标签'
          name='tags'
        >
          {getFieldDecorator('tags', {initialValue:'',rules: [{ required: true, message: 'Please input!' }]})
          (<Input.TextArea 
            placeholder='请输入模板标签，并以 , 分割'/>
            )}
        </Form.Item>

        <Form.Item label='模板描述'>
          {getFieldDecorator('templateDesc',{initialValue:''})(<Input.TextArea placeholder="请输入模板描述" />)} 
        </Form.Item>
        <Form.Item style={{textAlign:'center'}}>
          <Button style={{marginRight:'5px'}} type='primary'
            onClick={this.reset}
          >
            重置
          </Button>
          <Button style={{marginLeft:'5px'}} type='primary' onClick={this.submit}>
            标记
          </Button>
        </Form.Item>
      </Form>
    )
  }
}

export default Form.create()(TemplateModal);