import React from "react";
import { RegisterNode } from "gg-editor";

class CustomNode extends React.Component {
  render() {
    const config = {
      draw(item) {
        const group = item.getGraphicGroup();
        const model = item.getModel();
        const width = 240;
        const height = 48;
        const x = -width / 2;
        const y = -height / 2;
        const borderRadius = 4;
        const keyShape = group.addShape('rect', {
          attrs: {
            x,
            y,
            width,
            height,
            radius: borderRadius,
            fill: 'white',
            stroke: '#D8D8D8',
          },
        });
  
        // 左侧色条
        group.addShape('path', {
          attrs: {
            path: [
              ['M', x + 45, y + height],
              ['L', x + 2.5, y + height],
              ['A', borderRadius, borderRadius, 0, 0, 0, x, y + height-2],
              ['L', x, y + 2],
              ['A', borderRadius, borderRadius, 0, 0, 0, x + 2, y],
              ['L', x + 45, y],
            ],
            fill: model.color_type,
          },
        });
  
        // 类型 logo
        group.addShape('image', {
          attrs: {
            img: this.type_icon_url,
            x: x + 16,
            y: y + 12,
            width: 20,
            height: 16,
          },
        });
  
        // 名称文本
        const label = model.nodeType ? model.nodeType : this.nodeType;
  
        group.addShape('text', {
          attrs: {
            text: label,
            x: x + 55,
            y: y + 18,
            fontSize: 15,
            textBaseline: 'top',
            fill: '#000',
          },
        });
  
        // 状态 logo
        group.addShape('image', {
          attrs: {
            img: model.state_icon_url,
            // img: require('../../../../../common/wait.jpg'),
            x: 80,
            y: y + 13,
            width: 21,
            height: 21,
          },
        })



        // 模块 logo
        group.addShape('image', {
          attrs: {
            img: require('../../../../common/module.png'),
            x: x + 14,
            y: y + 16,
            width: 19,
            height: 19,
          },
        });   
        
        group.addShape('rect', {
          attrs: {
            x: -78,
            y: 21,
            width: 0,
            height: 3,
            fill: '#85C57A'
          },
        }).animate({
          radius: model.animateRadius,
          width: model.animate,
          repeat: true    // 指定为重复动画
      }, 2000);
  
        return keyShape;
      },
  
      anchor: [
        [0.5, 0], // 上边中点
        [0.5, 1] // 底边中点
      ]
    };

    return (
      <RegisterNode name="custom-node" config={config} />
    );
  }
}

export default CustomNode;