import * as constants from './constants';

export const saveProcess = data => ({
  type: constants.SAVE_PROCESS,
  data
})

export const saveProcessId = processId => ({
  type: constants.SAVE_PROCESSID,
  processId
})

export const saveParamsData = (name, param) => ({
  type: constants.SAVE_PARAMS,
  name, param
})

export const saveConversationId = (conversationId) => ({
  type: constants.SAVE_CONVERID,
  conversationId
})

export const saveNodesId = nodesId => ({
  type: constants.SAVE_NODESID,
  nodesId
});

export const saveResultList = (resultList, processLogId) => ({
  type: constants.SAVE_RESULT_LIST,
  resultListData: resultList.slice(0,resultList.length-1),
  isModel:resultList[resultList.length-1],
  processLogId
});

export const saveProcessState = processState => ({
  type: constants.SAVE_PROCESS_STATE,
  processState
});

export const saveProject = (obj, xmlString) => ({   // 保存流程图和xml字符串
  type: constants.SAVE_PROJECT,
  obj,
  xmlString
});

export const saveComponentInfo = componentInfo => ({
  type: constants.SAVE_COMPONENT_INFO,
  componentInfo
});

export const saveItemObj = (item) => ({
  type: constants.SAVE_ITEM,
  item
});

export const saveExperimentRunningId = (experimentRunningId) => ({
  type: constants.SAVE_RUNINGID,
  experimentRunningId
})

export const saveRunningResult = (result) => ({
  type: constants.SAVE_RUNING_RESULT,
  result
})

export const changeMLorDL = (isML) => ({
  type: constants.SAVE_CHANGEML,
  isML
})


