import React, {Component} from 'react'
import {connect} from 'react-redux'
import { Tree, message, Icon, Popover, Modal, Dropdown, Menu} from 'antd'
import ProcessModal from'../../components/modals/processModal'

import {getBaseDataSource} from '../../../../api/process'
import {actionCreator} from '../../store'


const { TreeNode } = Tree;

class ProcessTree extends Component{
    constructor(props){
        super(props);
        
        this.state = {
            pageSize: 10000,
            page: 1,
            processList: [],
            cloneProcessVisible:false,
            experimentId:0,
            process:{}
        }
    }

    componentDidMount(){
        console.log('mount process tree')
        this.getProcessList()
    }

    //获取流程和实验信息
    getProcessList=()=>{
        const params = {
            pageNum: this.state.page,
            pageSize: this.state.pageSize,
            isDeleted:'0',
        }
        const {page} = this.state
        getBaseDataSource(params).then((res)=>{
            if(res){
                if(res.data.meta.code == '001'){
                    const processList = res.data.data.WorkflowVOList.map((item,index)=>{
                        const {ggeditorObejectString} = item
                        let experimentList = item.experimentList.map(experiment=>{
                            return {...experiment, ggeditorObejectString}
                        })
                        return { ...item, experimentList, index: index+1+(page-1)*10, key:item.id }
                    })
                    this.setState({ 
                        processList, 
                        total: res.data.data.Total
                    })
                    console.log(processList)
                }else{
                    message.error(res.data.meta.message)
                }
            }
        }).catch(err=>{
            console.log(err)
        })
    }

    onSelect = (selectedKeys, info) => {
        console.log('selected', selectedKeys, info);
    };
    processClick = (record) =>{
        this.setState({
            process:record
        })
        if(window.event.button==2){
            window.event.preventDefault();
        }
    }
    getContent = (exp) => (<div>
        <p style={{cursor:'pointer'}}>运行</p>
        <p style={{cursor:'pointer'}}>编辑</p>
        <p style={{cursor:'pointer'}}>删除</p>
        <p style={{cursor:'pointer'}}>运行记录</p>
        <p style={{cursor:'pointer'}}>克隆</p>
        {exp.isMarkTemplate === 0 && <p style={{cursor:'pointer'}}>标记</p>}
    </div>)
    //克隆流程 
  cloneProcess=()=>{
    this.setState({
      cloneProcessVisible:true
    })
  }
  //从流程创建实验
  newExperiments=()=>{
    //   const {process} = this.state;
    //   this.props.saveProcess(process.ggeditorObejectString);
    //   this.props.saveProcessId(process.id)
  }

    render(){
        const {cloneProcessVisible } = this.state;
        const content = (
            <div>
                <p style={{cursor:'pointer'}} onClick={this.cloneProcess}>克隆</p>
                <p style={{cursor:'pointer'}} onClick={this.newExperiments}>创建实验</p>
                <p style={{cursor:'pointer'}}>删除</p>
                <p style={{cursor:'pointer'}}>标记为组件</p>
            </div>
        );
        const expContent= (
            <div>
                <p style={{cursor:'pointer'}}>运行</p>
                <p style={{cursor:'pointer'}}>编辑</p>
                <p style={{cursor:'pointer'}}>删除</p>
                <p style={{cursor:'pointer'}}>运行记录</p>
                <p style={{cursor:'pointer'}}>克隆</p>
            </div>
        );
        return (
            <div>

                <Tree
                    showIcon
                    defaultExpandedKeys={["38_"]}
                    defaultSelectedKeys={["38_21"]}
                    onSelect={this.onSelect}
                    context={this.context}
                >
                    {
                        this.state.processList.map(item=>{
                            return (
                                <TreeNode title={<Popover placement="bottomLeft" content={content} trigger="contextMenu"><span onContextMenu={()=>this.processClick(item)}>{item.name}</span></Popover>} icon={<Icon type="apartment" />}  key={item.id+'_'}>
                                    {
                                        item.experimentList.map(exp=>{
                                            return(
                                                <TreeNode title={<Popover placement="bottomLeft" content={this.getContent(exp)} trigger="contextMenu"><span onContextMenu={()=>this.processClick(exp.id)}>{exp.name}</span></Popover>} icon={<Icon type="experiment" />} key={item.id + '_' + exp.id}>
                                                    
                                                </TreeNode>
                                            )
                                        })
                                    }
                                </TreeNode>
                            )
                        })
                    }
                </Tree>
                <Modal
                    title="克隆流程"
                    visible={cloneProcessVisible}
                    footer={null}
                    onCancel={()=>{this.setState({cloneProcessVisible:false,cloneId:null})}}
                >
                    <ProcessModal 
                    cloneId={this.state.cloneId}
                    close={()=>{
                        this.setState({cloneProcessVisible:false})
                        this.getProcessList()
                    }}
                    isClone={true} 
                    />
                </Modal>
            </div>
        )
    }
}
const mapStateToProps = (state) => {
    return {
    }
  }
  const mapDispatchToProps=(dispatch)=>({
    saveProcessId(processId){
      dispatch(actionCreator.saveProcessId(processId));
    },
    saveProcess(ggeditorString){
      dispatch(actionCreator.saveProcess(ggeditorString))
    },
    saveConversationId(id){
      dispatch(actionCreator.saveConversationId(id))
    },
    saveExperimentRunningId(id){
      dispatch(actionCreator.saveExperimentRunningId(id))
    }
  })
  
  export default connect(mapStateToProps, mapDispatchToProps)(ProcessTree);