import React, { Fragment } from 'react';
import { Card, Form, Input, Menu, Select, Button, Modal, message, Radio, Spin, Upload, Icon, Row, Col } from 'antd';
import { withPropsAPI } from 'gg-editor';
import { connect } from 'react-redux';
// import { actionCreator } from '../../store';
// import {actionCreator} from '../../../store'
// import { List } from 'echarts/lib/export';
import './LocalData.less';
import {getPublicDataset, getUploadDataset} from '../../../../../api/dataset'

const { Item } = Form;
const { Option } = Select;

class LocalData extends React.Component {

  constructor(props){
    super(props);
    this.state={
      visible: false,
      uploadShow:'none',
      listShow:'none',
      loadingButton:false,
      listData:'',
      dataValue:'',
      loading: false,
    }
  }



  handleClick = () => {
    this.setState({
      visible: true
    });
  }

  openClassicList = () => {
    this.setState({
      listShow:'block',
      uploadShow: 'none',
      loading: true,
      listData:''
    })
    getPublicDataset().then(res=>{
      if(res.data.meta.code === '001'){
        this.setState({
          loading: false,
          listData: res.data.data['Dataset List']
        })
      }else{
        message.error(res.data.meta.m)
      }
    })
  }

  openUploadList = () => {
    this.setState({
      listShow:'block',
      uploadShow: 'none',
      loading: true,
      listData:''
    })
    getUploadDataset().then(res=>{
      console.log(res)
      if(res.data.meta.code === '001'){
        this.setState({
          loading: false,
          listData: res.data.data['Dataset List']
        })
      }else{
        message.error(res.data.meta.m)
      }
    })
  }

  handleOk = e => {
    let that = this;
    console.log(this.state);
    if(this.state.uploadShow === 'none'){
      that.handleConf()
    }else if(this.state.listShow === 'none'){
      this.upload.submit();
    }
  }

  handleConf = () => {
    const { propsAPI, saveDatasetId } = this.props;
    const { executeCommand, update } = propsAPI;
    const { label } = this.props.detail

    console.log('radiovalue', this.state.radioValue);
    // saveDatasetId(this.state.radioValue);
    this.props.saveParams(label+'_inputdata', this.state.radioValue)

    // const item = window.ITEM;
    // if (!item) {
    //   return;
    // }
    // executeCommand(() => { 
    //   update(item, {
    //     conf: {inputdata: this.state.dataValue}
    //   });
    // });
    this.setState({
      visible: false,
      radioValue:'',
      uploadShow:'none',
      listShow:'none',
    });
    // console.log(this.state)
  };

  handleCancel = e => {
    console.log(e);
    // this.upload.clear()
    this.setState({
      visible: false,
      radioValue:'',
      uploadShow:'none',
      listShow:'none',
    });
  };

  onChange = e => {
    console.log('radio checked', e.target.value);
    console.log('radio', e);
    this.setState({
      dataValue: e.target.addr,
      radioValue: e.target.value
    });
  };

  openUploadModal = () => {
    this.setState({
      uploadShow:'block',
      listShow: 'none'
    })
  }


  setButtonLoading = val => {
    this.setState({loadingButton: val})
  }

  render(){
    console.log("子组件",this.props.detail);
    const { nodeType, nodeRemarks } = this.props.detail;
    const { TextArea } = Input;
    const { listData } = this.state;
    const { form } = this.props;
    const radioStyle = {
      display: 'block',
      height: '30px',
      lineHeight: '30px',
      margin: '5px 0 5px 21px',
      fontSize: '16px'
    };

    return (
      <Fragment>
        <Form onSubmit={this.handleSubmit} labelCol={{span:5}} wrapperCol={{span:18}}>
          <Item label="节点名称">
            {form.getFieldDecorator('nodeType', {
              initialValue: nodeType,
            })
            (<Input onBlur={this.handleSubmit} readOnly={true}/>)}
          </Item>
          <Item label="备注">
            {form.getFieldDecorator('nodeRemarks', {
              initialValue: nodeRemarks,
            })
            (<TextArea onBlur={this.handleSubmit} rows={3} readOnly={true}/>)}
          </Item>
          <Menu selectedKeys={[this.props.configCurrentKey]} mode="horizontal" style={{marginBottom: '15px'}}>
            <Menu.Item key="configuration">
              参数配置
            </Menu.Item>
          </Menu>
          <Item label="数据集">
            <Button type="primary" onClick={()=>this.handleClick()} block>数据列表</Button>
          </Item>
        </Form>
        <Modal
          title="数据集"
          visible={this.state.visible}
          onOk={()=>this.handleOk()}
          onCancel={this.handleCancel}
          okButtonProps={{ loading: this.state.loadingButton }}
        >
          <Button type="primary" style={{marginRight: '10px'}} onClick={()=>this.openClassicList()}>公开数据集</Button>
          <Button type="primary" style={{marginRight: '10px'}} onClick={()=>this.openUploadList()}>自定义数据集</Button>
          {/* <Button onClick={this.openUploadModal}>上传数据</Button> */}
          <Spin spinning={this.state.loading} tip="Loading...">
            <div style={{display:this.state.listShow}} className='dataWrapper'>
              <Radio.Group onChange={this.onChange} value={this.state.radioValue} style={{margin:'9px 0 0 13px'}}>
                { listData &&
                    listData.map((item, index)=>{
                      // console.log(item.datasetFileAddr.toString())
                      return (
                        <Radio 
                          value={item.datasetAddr} 
                          // addr={item.datasetAddr} 
                          className='radioList' 
                          key={index}
                          style={radioStyle}
                        >
                          {item.name}</Radio>
                      )
                    }) 
                }
              </Radio.Group>
            </div>
          </Spin>
          {/* <div style={{display:this.state.uploadShow, marginTop:'14px'}}> */}
            {/* <UploadDataset 
              onRef={(ref)=>{ this.upload = ref}} 
              buttonLoading={this.setButtonLoading} 
              datasetType={'0'}
              updateData={()=>{}}
            /> */}
          {/* </div> */}
        </Modal>
      </Fragment>
    );
  }
}

const mapDispatchToProps = (dispatch) => ({
  // saveParams(name, param) {
  //   dispatch(actionCreator.saveParamsData(name, param));
  // }
})

export default Form.create()(withPropsAPI(connect(null,mapDispatchToProps)(LocalData)));

