import React,{Component} from 'react';
import {Button,Table,Tabs,Divider,Badge,Radio,message,Spin} from 'antd'
import {getDatasetInTrash,getDldatasetInTrash,deleteDatasetPermanently,restoreDataset,restoreDlDataset,deleteDlDatasetCompletely} from '../../api/dataset'

class Trash extends Component{
  constructor(props){
    super(props);
    this.state={
      trashSelectedRowKeys:[],
      
      dataSource:[],
      page:1,
      loading:false,
      datasetType:0,  //0-代表机器学习数据集  1-代表深度学习数据集
    }
  }

  //选择数据集类型
  onChange=(e)=>{
    //type代表的是上一个状态
    if(this.state.datasetType==1){
      //代表现在是机器学习
      this.getDatasetData();
      this.setState({
        datasetType:0,

      });
    }else if(this.state.datasetType==0){
      //代表现在是深度学习
      this.getDlDatasetData();
      this.setState({
        datasetType:1
      });
    }else{
      message.error("数据集类型不存在");
    }
  }
  
  componentDidMount(){
    this.getDatasetData();
    //console.log(1);
    this.props.onRef(this);
  }

  //获取机器学习数据集数据
  getDatasetData=()=>{
    this.setState({
      loading: true,
      trashSelectedRowKeys:[],
      dataSource:[],
    })
    const {page} = this.state
    const params = {
      pageNum: page,
      pageSize: 7,
      // userId:2
    }
    getDatasetInTrash(params).then((res)=>{
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['Dataset List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*10, key: item.id}
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total  //这个字段？？
          })
        }else{
          message.error('获取数据出错！')
          this.setState({ loading: false })
        }
      }
    }).catch(err=>this.setState({ loading: false }))
    
  }
  //获取深度学习数据集数据
  getDlDatasetData=()=>{
    this.setState({
      loading: true,
      trashSelectedRowKeys:[],
      dataSource:[],
    })
    const {page} = this.state
    const params = {
      pageNum: page,
      pageSize: 7,
      // userId:2
    }
    getDldatasetInTrash(params).then((res)=>{
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['Dataset List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*10, key: item.id}
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total  
          })
        }else{
          message.error('获取数据出错！')
          this.setState({ loading: false })
        }
      }
    }).catch(err=>this.setState({ loading: false }))
    
  }
  
  //回收站多选框改变
  onSelectChange = (trashSelectedRowKeys) => {
    this.setState({ trashSelectedRowKeys });
  };
  //还原操作
  restore=(ids)=>{
    const params={};
    const {trashSelectedRowKeys,datasetType}=this.state
    if(typeof(ids)=='number') params.datasetIds=ids+'';
    else params.datasetIds=trashSelectedRowKeys.join(',');
    // if(typeof(ids)=='number') ids==[ids];
    // let params={
    //   ids:[...ids]
    // };
    console.log(params)
    datasetType === 0 ?
    restoreDataset(params).then((res)=>{
      if(res){
        if(res.data.meta.code == '001'){
          message.success('还原成功！')
          this.getDatasetData()
        }else{
          message.error('还原失败！')
        }
      }
    }).catch(err=>{}) : restoreDlDataset(params).then((res)=>{
      if(res){
        if(res.data.meta.code == '001'){
          message.success('还原成功！')
          this.getDlDatasetData()
        }else{
          message.error('还原失败！')
        }
      }
    }).catch(err=>{})
  }
  //彻底删除
  deepDelete=(ids)=>{
    const params={};
    const {trashSelectedRowKeys,datasetType}=this.state
    if(typeof(ids) == 'number') params.datasetIds=ids+'';
    else params.datasetIds = trashSelectedRowKeys.join(',');
    // let params={
    //   idList:[...ids]
    // };
    // console.log(params)
    datasetType === 0 ?
    deleteDatasetPermanently(params).then((res)=>{
      if(res){
        if(res.data.meta.code == '001'){
          message.success('删除成功！')
          this.getDatasetData();
        }else{
          message.error('删除失败！')
        }
      }
    }).catch(err=>{}) : deleteDlDatasetCompletely(params).then((res)=>{
      if(res){
        if(res.data.meta.code == '001'){
          message.success('删除成功！')
          this.getDlDatasetData();
        }else{
          message.error('删除失败！')
        }
      }
    }).catch(err=>{})
    
  }
  clear = () => {
    this.setState({
      trashSelectedRowKeys:[],
      dataSource:[],
      page:1,
      loading:false,
      datasetType:0,
    })
  }

  render(){
    const {dataSource,select,trashSelectedRowKeys,datasetType}=this.state;
    //回收站表格结构
    const columns = [
      {title: '序号',dataIndex: 'index',},
      {title: '数据集名称', dataIndex: 'name',},
      {title: '操作',dataIndex: 'action',
        render:(text,record)=>(
          <>
          <a onClick={()=>this.restore(record.id)}>还原</a>
          <Divider type='vertical'/>
          <a onClick={()=>this.deepDelete(record.id)}>彻底删除</a>
          </>
        )},
    ];
    return(
      <div>
        <Radio.Group onChange={this.onChange} value={datasetType}>
            <Radio value={0}>机器学习数据集</Radio>
            <Radio value={1}>深度学习数据集</Radio>
        </Radio.Group>
        <Spin spinning={this.state.loading} tip='loading...'>
        <Table 
            bordered size='small'
            columns={columns} 
            dataSource={dataSource} 
            // pagination={false}
            rowSelection={
              { selectedRowKeys:trashSelectedRowKeys,
                onChange: this.onSelectChange
              }
            }
          />
        </Spin>
          <div style={{textAlign:'left'}}>
            <Button style={{margin:'5px 5px 0 0'}} type='primary'
              onClick={()=>this.restore(trashSelectedRowKeys)}
            >
              批量还原
            </Button>
            <Button style={{marginLeft:'5px 0 0 5px'}} type='primary'
              onClick={()=>this.deepDelete(trashSelectedRowKeys)}
            >
              批量删除
            </Button>
          </div>
        </div>
    )
  }
}

export default Trash;