import React, { Component } from 'react';
import { connect } from 'react-redux';
import echarts from 'echarts/lib/echarts';
// 引入提示框和标题组件
import 'echarts/lib/component/tooltip';
import  'echarts/lib/chart/heatmap';
import  'echarts/lib/chart/bar';

// 引入提示框和标题组件
import 'echarts/lib/component/visualMap';
import 'echarts/lib/component/title';


class Heatmap extends Component{

  constructor(props){
    super(props)
    this.state = {
        xAxis: [], 
        max: '', 
        min: '', 
        dataList:  []
    }
  }

  componentDidMount(){
    this.handleData()
  }

  
  handleData = () => {
    const { data} = this.props
    const xAxis = data[0].splice(1)  // 获取x轴， y轴和x轴的值一样
    let dataList = []
    let max = -1;
    let min = Number.MAX_SAFE_INTEGER
    data.splice(1).map((row, x)=>{
        row.splice(1).map((item, y)=>{
            if(item > max) max = item;
            if(item < min) min = item;
            dataList.push([x, y, item])
        })
    })
    let that = this
    this.setState({
            xAxis, max, min, dataList
        },
        () => that.eChartHeatmap()
    )
  }

  eChartHeatmap() {
    // 基于准备好的dom，初始化echarts实例
    const myChart = echarts.init(document.getElementById('heatmapEchart'));
    const {xAxis, max, min, dataList} = this.state

    let data = dataList.map(function (item) {
        return [item[1], item[0], item[2] || '-'];
    });
    console.log(data)

    let option = {
        title: {
            // 正标题
            text: '混淆矩阵图',
            padding: [ 5, 0, 30, 0],
            left: 'center',
            // 副标题
            // "subtext":"echarts踩坑"
        },
        tooltip: {
            position: 'top',
            // 鼠标可以进入tooltip提示框并点击提示框内部的信息
            enterable: true,
            formatter: function (params) {
            console.log(params);
            return params.value[2]
            }
        },
        animation: false,
        grid: {
            height: '50%',
            top: 75,
            left: 90,
            bottom: 10,
            right: 80,
            y: '10%'
        },
        xAxis: {
            name: '预测值',
            type: 'category',
            data: xAxis,
            position: "top",
            splitArea: {
                show: true
            },
            splitLine: {
                show: true,
                lineStyle: {
                    color: 'rgba(204,204,204,0.30)',
                    type: 'solid',
                }
            },
            // X轴字体样式
            axisLine: {
                show: false,
                lineStyle: {
                    color: '#333',
                    type: 'solid',
                }
            }
        },
        yAxis: {
            name: '真实值',
            type: 'category',
            data: xAxis,
            inverse: true,
            splitArea: {
                show: true
            },
            splitLine: {
                show: true,
                lineStyle: {
                    color: 'rgba(204,204,204,0.30)',
                    type: 'solid',
                }
            },
            // Y轴的颜色和字体宽度
            axisLine: {
              show: false,
              lineStyle: {
                color: '#333',
                type: 'solid',
              }
            },
        },
        visualMap: {
            min: min,
            max: max,
            calculable: true,
            orient: 'horizontal',
            left: 'center',
            bottom: '15%',
            inRange: {
                color: ['#F6FBFF', '#0B2E70'],
                // symbolSize: [30, 100]
            }
        },
        series: [{
            name: 'Punch Card',
            type: 'heatmap',
            data: data,
            label: {
                normal: {
                    show: true
                }
            },
            itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
            }
            // emphasis: {
            //     itemStyle: {
            //         shadowBlur: 10,
            //         shadowColor: 'rgba(0, 0, 0, 0.5)'
            //     }
            // }
        }]
    };
    myChart.setOption(option)
  };


  render(){
    
    return (
        <div id="heatmapEchart" style={{ width:500, height:500}} ></div>
    )
  }
}

export default Heatmap;