import React, { Component } from 'react';
import { PageHeader, Table, message, Spin, Divider, Button, Modal, Row, Col, Input, Form,
     Card, Empty, Tag, Select, Avatar, Icon,Tabs,Pagination} from 'antd';

import {getUploadDataset,getPublicDataset,deleteDataset,
  searchPublicDatasetByName,searchUserDatasetByName,searchPublicDatasetByTags,
    searchUserDatasetByTags,downloadDataset,previewDataset,getPublicDlDataset,
    getUserDlDataset,deleteDlDataset,getImagePaths,searchPublicDlDatasetByName,
    searchUserDlDatasetByName,searchPublicDlDatasetByTags,searchUserDlDatasetByTags,downloadDlDataset} from '../../api/dataset';
import UploadDataset from './UploadDataset';
import Trash from './trash'
import style from './index.module.less'

const { TabPane } = Tabs;

class  Dataset extends Component{

  constructor(props){
    super(props)
    this.state = {
      //测试数据
      dataSource: [],
      loading: false,
      visible: false,
      loadingButton: false,
      radioValue: 'user',
      datasetType:'machine',
      previewVisible: false,
      previewLoading:false,
      previewColumns: [],
      previewData: [],
      previewPage:1,
      trashVisible:false,
      total:1,
      searchType:'name',
      page:1,
      imageLinks:[1,2,3,4,5,6,7,8,9,7,8],
      previewPicLink:[],
      previewTotal:1
    }
  }

  componentDidMount(){
    this.getUploadData();
  }


  //异步获取公开数据集
  getData = () => {
    this.setState({loading: true})
    const {page} = this.state
    const params = {
      pageNum:page,
      pageSize:7
    }
    getPublicDataset(params).then(res=>{
      console.log(res)
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['Dataset List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*7 }
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total
          })
        }else{
          message.error('获取数据出错！')
          this.setState({ loading: false })
        }
      }
    }).catch(err=>this.setState({ loading: false }))
  }

  //异步获取自定义数据集
  getUploadData = () => {
    this.setState({loading: true, radioValue: 'user'})
    const {page} = this.state
    const params = {
      pageNum: page,
      pageSize: 7,
    }
    getUploadDataset(params).then(res=>{
      console.log(res)
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['Dataset List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*7 }
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data['Total']
          })
        }else{
          message.error('获取数据出错！')
          this.setState({ loading: false })
        }
      }
    }).catch(err=>this.setState({ loading: false }))
  }

  //异步获取深度学习公开数据集
  getDlData = () => {
    this.setState({loading: true})
    const {page} = this.state
    const params = {
      pageNum:page,
      pageSize:7
    }
    getPublicDlDataset(params).then(res=>{
      console.log(res)
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['Dataset List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*7 }
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total
          })
        }else{
          message.error('获取数据出错！')
          this.setState({ loading: false })
        }
      }
    }).catch(err=>this.setState({ loading: false }))
  }

  //异步获取深度学习自定义数据集
  getDlUploadData = () => {
    this.setState({loading: true, radioValue: 'user'})
    const {page} = this.state
    const params = {
      pageNum: page,
      pageSize: 7,
    }
    getUserDlDataset(params).then(res=>{
      console.log(res)
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['Dataset List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*7 }
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data['Total']
          })
        }else{
          message.error('获取数据出错！')
          this.setState({ loading: false })
        }
      }
    }).catch(err=>this.setState({ loading: false }))
  }

  //处理删除自定义机器学习数据集
  handleDelect = (record) => {
    console.log(record)
    const params = {datasetId:record.id}
    deleteDataset(params).then(res=>{
      if(res){
        console.log(res)
        if(res.data.meta.code == '001'){
          message.success('删除成功！')
          this.getUploadData()
        }else{
          message.error('删除失败！')
        }
      }
    })
  }
  //处理删除自定义深度学习数据集
  deleteDlDataset = (record) => {
    const params = {datasetId:record.id}
    deleteDlDataset(params).then(res=>{
      if(res){
        console.log(res)
        if(res.data.meta.code == '001'){
          message.success('删除成功！')
          this.getDlUploadData()
        }else{
          message.error('删除失败！')
        }
      }
    })
  }
  //切换机器学习数据集和深度学习数据集
  changeDatasetType = val => {
    var type={
      1:'machine',
      2:'deep'
    }
    this.setState({
      datasetType:type[val]
    },()=>{
      val=='1'?this.getUploadData():this.getDlUploadData(); 
    })
  }
  //切换数据集类型
  radioChange = val => {
    this.setState({
      radioValue: val,
      search:'',
      page: 1
    },()=>{
      console.log(this.state.radioValue)
      const { radioValue,datasetType } = this.state
      if(datasetType === 'machine'){
        radioValue === 'user' && this.getUploadData()
        radioValue === 'classic' && this.getData()
      }else{
        radioValue === 'user' && this.getDlUploadData()
        radioValue === 'classic' && this.getDlData()
      }
    });
  };
  //关闭注册数据集的modal框
  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };
  //展示注册数据集modal框
  showModal = () => {
    this.setState({
      visible: true,
    });
  };
  

  //数据集预览
  preview = record => {
    // console.log(record)
    this.setState({
      previewVisible: true,
      previewLoading: true
    })
    let previewColumns,previewData,previewDesc
    previewDataset({datasetId:record.id}).then(res=>{
      console.log(res)
      if(res){
        this.setState({previewLoading: false})
        if(res.data.meta.code === '001'){
          previewDesc = res.data.data.desc
          const {content} = res.data.data
          previewColumns = content[0] && content[0].map(item =>{
            return {
              title: item,
              dataIndex: item,
              key: item
            }
          })
          previewData = content.map(item=>{
            let preview = {}
            for(let i=0;i<item.length;i++){
              preview = { ...preview, [previewColumns[i].title]:item[i] }
            }
            return preview
          })
          this.setState({
            previewColumns,
            previewData,
            previewDesc
          })
        }
      }
    })
  }

  getPreviewPicLink = (val,id) => {
    const {previewPage} = this.state;
    // var host = 'localhost';
    // var port = '8002';
    // console.log(val,id)
    return val.slice((previewPage-1)*8,previewPage*8).map(item=>{

      const tmp = item.split('/')
      return {
        path:item,
        title: tmp[tmp.length-1]
      }
    })
  }
  //图片数据集预览
  previewPic = (record) => {
    this.setState({
      previewVisible:true,
      previewLoading:true
    })
    // this.setState({
    //   previewTotal:this.state.imageLinks.length,
    //   previewPicLink:this.state.imageLinks.slice(0,8)
    // })
    getImagePaths({datasetId:record.id}).then(res=>{
      console.log(res)
      if(res){
        this.setState({previewLoading: false})
        if(res.data.meta.code === '001'){
          var imageLinks=res.data.data;
          var previewPicLink=this.getPreviewPicLink(imageLinks,record.id);
          this.setState({
            imageLinks,
            datasetId:record.id,
            previewTotal:imageLinks.length,
            previewPicLink
          })
        }else{
          message.error("预览失败");
        }
      }
    })
  }
  changePicPage = page => {
    const {imageLinks,datasetId} = this.state;
    this.setState({
      previewPage:page,
    },()=>{
      var previewPicLink = this.getPreviewPicLink(imageLinks,datasetId);
      this.setState({
        previewPicLink
      })
    })
  }
  //预览数据集关闭
  handlePreCancel = () => {
    this.setState({
      previewVisible: false,
      previewPage: 1,
      previewData:[],
      previewDesc:''
    })
  }
  //数据集换页
  datasetPageChange = (page) => {
    console.log(page);
    this.setState({page},()=>{
      if(this.state.search){
        this.state.radioValue === 'user' && this.userSearch()
        this.state.radioValue === 'classic' && this.classicSearch()
      }else{
        this.state.radioValue === 'user' && this.getUploadData()
        this.state.radioValue === 'classic' && this.getData()
      }
    });
  }
  //换页 
  pageChange = (page) => {
    this.setState({
      previewPage: page
    })
  }


  //搜索
  handleSearch = search => {
    const { radioValue } = this.state
    this.setState({
      loading:true,
      page:1,
      search
    }, function(){
        radioValue === 'classic' ? this.classicSearch(search) : this.userSearch(search)
    })
  }

  searchDataset = (f,params) =>{
    const {page} = this.state;
    f(params).then(res=>{
      console.log(res)
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['Dataset List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*10 }
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total
          })
        }else{
          message.error('获取数据出错！')
          this.setState({ loading: false })
        }
      }
    }).catch(err=>this.setState({ loading: false }))
  }

  classicSearch = search => {
    const {searchType,datasetType}=this.state;
    const {page} = this.state
    const params = {
      pageNum: page,
      pageSize: 10,
    }
    if(searchType=='name'){
      params.datasetName=search;
      datasetType === 'machine' ? this.searchDataset(searchPublicDatasetByName,params) 
                                : this.searchDataset(searchPublicDlDatasetByName,params);
    }else{
      params.datasetTags=search;
      datasetType === 'machine' ? this.searchDataset(searchPublicDatasetByTags,params)
                                : this.searchDataset(searchPublicDlDatasetByTags,params);
    }
  }

  userSearch = search => {
    const {searchType,datasetType}=this.state;
    const {page} = this.state
    const params = {
      pageNum: page,
      pageSize: 10,
      // userId:2
    }
    if(searchType=='name'){
      params.datasetName=search;
      datasetType === 'machine' ? this.searchDataset(searchUserDatasetByName,params)
                                : this.searchDataset(searchUserDlDatasetByName,params);
    }else{
      params.datasetTags=search;
      datasetType === 'machine' ? this.searchDataset(searchUserDatasetByTags,params)
                                : this.searchDataset(searchUserDlDatasetByTags,params);
    }
  }

  onSearchChange = e =>{
    this.setState({
      search: e.target.value
    })
  }
  changeSearchType=(v)=>{
    this.setState({
      searchType:v
    })
  }
  downloadDataset=(record)=>{
    const params={datasetId:record.id};
    downloadDataset(params).then(res=>{
      if(res){
        var blob = new Blob([res.data], {type: 'application/octet-stream'});
        // FileReader主要用于将文件内容读入内存
        var reader = new FileReader();
        reader.readAsDataURL(blob);
        // onload当读取操作成功完成时调用
        reader.onload = function(e) {
          var a = document.createElement('a');
          // 获取文件名fileName
          var fileName = res.headers["content-disposition"].split(".");
          fileName = record.name + '.' + fileName[fileName.length - 1];
          fileName = fileName.replace(/"/g, "");
          a.download = fileName;
          a.href = e.target.result;
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
        }
      }else{
        message.error('导出失败！')
      }
    })
    .catch(err=>message.error('导出失败！'))
  }
  downloadDlDataset = (record) => {
    const params={datasetId:record.id};
    downloadDlDataset(params).then(res=>{
      console.log(res);
      if(res){
        var blob = new Blob([res.data], {type: 'application/zip; application/octet-stream'});
        // FileReader主要用于将文件内容读入内存
        var reader = new FileReader();
        reader.readAsDataURL(blob);
        // onload当读取操作成功完成时调用
        reader.onload = function(e) {
          var a = document.createElement('a');
          // 获取文件名fileName
          var fileName = res.headers["content-disposition"].split(".");
          fileName = record.name + '.' + fileName[fileName.length - 1];
          fileName = fileName.replace(/"/g, "");
          a.download = fileName;
          a.href = e.target.result;
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
      }
    }
  }).catch(err=>{})
  }

  render(){
    const { searchType,dataSource,previewColumns, previewData, previewDesc, total, radioValue, trashVisible,datasetType,previewPicLink,previewTotal} = this.state

    //数据集表格结构
    const columns = [
      { align: 'center', render: ()=><Avatar size={40} icon={<Icon type="file-text" theme="filled" />} style={{ backgroundColor: '#27afff' }} />},
      // {title: '序号', dataIndex: 'index', key: 'index',},
      {title: '数据集名称', dataIndex: 'name', key: 'name',},
      {title: '标签',dataIndex: 'tags', key: 'tags',
        render:tags=>(
          <>
            {tags.split(',').map((item,index)=>(
              <Tag color='orange' key={index}>{item}</Tag>
            ))}
          </>
        )},
      { title: '创建时间',dataIndex: 'createTime', key: 'createTime',},
      {title: '数据集类型', dataIndex: 'type',key: 'type',
        render: (text, record) => <span>{ text===0 ? '公开数据集' : text ===1 ? '自定义数据集' : '' }</span>
      },
      {title: '数据集描述',dataIndex: 'datasetDesc', key: 'datasetDesc',},
      {title: '操作',key: 'action',
        render: (text, record) => (
          <span>
            <a onClick={()=>datasetType === 'machine' ? this.preview(record) : this.previewPic(record)}>预览</a>
            <Divider type="vertical" />
            <a onClick={()=>datasetType === 'machine' ? this.downloadDataset(record) : this.downloadDlDataset(record)}>导出</a>
            {this.state.radioValue === 'user' &&  <Divider type="vertical" />}
            {this.state.radioValue === 'user' &&  <a onClick={e=>datasetType === 'machine' ? this.handleDelect(record) : this.deleteDlDataset(record)}>删除</a> }
          </span>
        )},
    ];

  return(
      <div className={style.contentWrap}>
        <PageHeader
          style={{border: '1px solid rgb(235, 237, 240)',marginBottom: '13px'}}
          title="数据集管理" subTitle="已有数据集列表"
          extra={[<Button type='primary' onClick={this.showModal}>注册数据集</Button>]}
        />
         <Tabs defaultActiveKey="1" onChange={this.changeDatasetType} type="card">
          <TabPane tab="机器学习数据集" key="1">
            <div className={style.buttonWrap}>
              <Button type={radioValue==='classic'?'primary':''} onClick={()=>this.radioChange('classic')}>公开数据集</Button>
              <Button type={radioValue==='classic'?'':'primary'} onClick={()=>this.radioChange('user')} style={{marginLeft:'10px'}}>自定义数据集</Button>
              <Button type='primary' style={{float:'right'}} onClick={()=>{
                    this.setState({trashVisible:true})
                    this.trash ? this.trash.getDatasetData() : console.log('第一次加载')
                }}>回收站</Button>
              
              <Input.Search 
                  onSearch={value=>this.handleSearch(value)} 
                  value={this.state.search}
                  onChange={this.onSearchChange}
                  placeholder="搜索数据集"
                  style={{float:"right",width:360, marginRight:'10px'}}
                  addonAfter={
                    <Select value={searchType} onChange={this.changeSearchType} style={{float:"right",width:120}}>
                      <Select.Option value='tag'>按标签搜索</Select.Option>
                      <Select.Option value='name'>按名称搜索</Select.Option>
                    </Select>
                  }
              />
            </div>
          </TabPane>
          <TabPane tab="深度学习数据集" key="2">
            <div className={style.buttonWrap}>
                <Button type={radioValue==='classic'?'primary':''} onClick={()=>this.radioChange('classic')}>公开数据集</Button>
                <Button type={radioValue==='classic'?'':'primary'} onClick={()=>this.radioChange('user')} style={{marginLeft:'10px'}}>自定义数据集</Button>
                <Button type='primary' style={{float:'right'}} onClick={()=>{
                      this.setState({trashVisible:true})
                      this.trash ? this.trash.getDatasetData() : console.log('第一次加载')
                  }}>回收站</Button>
                
                <Input.Search 
                    onSearch={value=>this.handleSearch(value)} 
                    value={this.state.search}
                    onChange={this.onSearchChange}
                    placeholder="搜索数据集"
                    style={{float:"right",width:360, marginRight:'10px'}}
                    addonAfter={
                      <Select value={searchType} onChange={this.changeSearchType} style={{float:"right",width:120}}>
                        <Select.Option value='tag'>按标签搜索</Select.Option>
                        <Select.Option value='name'>按名称搜索</Select.Option>
                      </Select>
                    }
                />
              </div>
          </TabPane>
        </Tabs>
        
        <Spin spinning={this.state.loading} tip='loading...'>
          <Table 
            columns={columns} 
            dataSource={dataSource} 
            pagination={{
              onChange:this.datasetPageChange,
              current:this.state.page,
              total,
              pageSize: 7,
            }}
          />
        </Spin>
        
        {/* 下面是各个modal框 */}
        <Modal
          title="注册数据集"
          footer={null}
          visible={this.state.visible}
          onCancel={()=>{
            this.setState({visible: false})
            this.upload.clear();
            datasetType === 'machine' ? this.getUploadData() : this.getDlUploadData();
        }}
        >
          <UploadDataset  close={()=>{
            this.setState({visible: false});
          }} onRef={ref=>this.upload=ref} getUploadData={this.getUploadData} datasetType={datasetType} getDlUploadData={this.getDlUploadData} />
        </Modal>

        <Modal
          title="回收站"
          visible={trashVisible}
          footer={null}
          onCancel={()=>{
            this.setState({trashVisible:false})
            if(datasetType === 'machine')
              radioValue=='classic'?this.getData():this.getUploadData();
            else radioValue=='classic'?this.getDlData():this.getDlUploadData();
            this.trash.clear();
          }}
        >
          <Trash onRef={ref=>this.trash=ref}/>
        </Modal>
        {/* 这个是copy智能平台，暂时没进行修改 */}
        <Modal
          title="预览数据集"
          visible={this.state.previewVisible}
          onCancel={this.handlePreCancel}
          footer={null}
          width={'85%'}
        >
          <Spin tips='loading' spinning={this.state.previewLoading}>
            <Row>
              {datasetType === 'machine' ? <Col span={17}>
                <Table 
                  columns={previewColumns} 
                  dataSource={previewData} 
                  size="small" 
                  showHeader={false}
                  pagination={{current:this.state.previewPage,onChange:this.pageChange}}
                />
              </Col> : <Col span={17}>
                <Row gutter={8}>
                  {
                    previewPicLink.slice(0,4).map((item) => (<Col span={6}>
                      <Card
                        hoverable
                        cover={
                          <div style={{height:200,backgroundImage:`url("${item.path}")` ,backgroundSize:'contain', backgroundPosition:'center center', backgroundRepeat:'no-repeat'}}></div>
                        }
                      >
                        <Card.Meta title={item.title}/>
                      </Card>
                    </Col>))
                  }
                </Row>  
                <Row gutter={[8,16]}>
                  {
                    previewPicLink.slice(4).map((item) => (<Col span={6}>
                      <Card
                        hoverable
                        cover={<div style={{height:200,backgroundImage:`url("${item.path}")`,backgroundSize:'contain', backgroundPosition:'center center', backgroundRepeat:'no-repeat'}}></div>}
                      >
                        <Card.Meta title={item.title} />
                      </Card>
                    </Col>))
                    }
                </Row> 
                <Pagination size="small" current={this.state.previewPage} total={previewTotal} onChange={this.changePicPage} style={{float:'right'}} pageSize={8}/>
              </Col>}
              <Col span={6} offset={1}>
                <Card style={{maxHeight:'420px',overflow:'auto'}}>
                  <p>{previewDesc ? previewDesc: <Empty description='暂无描述' /> }</p>
                </Card>
              </Col>
            </Row>
          </Spin>
        </Modal>
      </div>
  )
  }
}

export default Form.create()(Dataset);