import React, { Fragment } from 'react';
import { Card, Form, Input, Menu, Select, Tooltip, Icon } from 'antd';
import { withPropsAPI } from 'gg-editor';

const { Item } = Form;
const { Option } = Select;

class DensityCluster extends React.Component {

  handleSubmit = (e) => {
    if (e && e.preventDefault) {
      e.preventDefault();
    }

    const { form, propsAPI } = this.props;
    const { getSelected, executeCommand, update } = propsAPI;

    setTimeout(() => {
      form.validateFieldsAndScroll((err, values) => {
        if (err) {
          return;
        }

        const item = window.ITEM;

        if (!item) {
          return;
        }

        executeCommand(() => {
          update(item, {
            ...values,
          });
        });
      });
    }, 0);
  };
 
  render() {
    console.log("子组件",this.props.detail);
    const { 
      nodeType,
      nodeRemarks,
      conf
     } = this.props.detail;
    const { TextArea } = Input;
    const { form } = this.props;
    return (
      <Fragment>
        <Form onSubmit={this.handleSubmit}>
          <Item label="节点名称">
            {form.getFieldDecorator('nodeType', {
              initialValue: nodeType,
              rules:[{
                validator:  (rule, val, callback)=>{
                  if(val.length > 12){
                    callback('节点名称的最大长度为12！')
                  }
                  callback();
                }
              }]
            })
            (<Input onBlur={this.handleSubmit} />)}
          </Item>
          <Item label="备注">
            {form.getFieldDecorator('nodeRemarks', {
              initialValue: nodeRemarks,
            })
            (<TextArea onBlur={this.handleSubmit} rows={3}/>)}
          </Item>
          <Menu onClick={this.props.handleClick} selectedKeys={[this.props.configCurrentKey]} mode="horizontal">
            <Menu.Item key="configuration">
              参数配置
            </Menu.Item>
            {/* <Menu.Item key="description">
              描述说明
            </Menu.Item> */}
          </Menu>
          <Card title='算法IO参数' bordered={false} bodyStyle={{background: '#fff'}}>
            <Item label={
              <span>
              eps&nbsp;
                <Tooltip title="半径（float）">
                  <Icon type="question-circle-o" style={{color:'#5f6267'}} />
                </Tooltip>
              </span>
              } required>
              {form.getFieldDecorator('conf.eps', {
                initialValue: conf.eps,
                rules: [{required: true, message: 'Please input data!'}],
              })
              (<Input onBlur={this.handleSubmit} />)}
            </Item>
            <Item label={
              <span>
              minSamples&nbsp;
                <Tooltip title="密度阈值(int)">
                  <Icon type="question-circle-o" style={{color:'#5f6267'}} />
                </Tooltip>
              </span>
              } required>
              {form.getFieldDecorator('conf.minSamples', {
                initialValue: conf.minSamples,
                rules: [{required: true, pattern: new RegExp(/^[1-9]\d*$/, "g"), message: 'Please input number data!'}],
              })
              (<Input onBlur={this.handleSubmit} />)}
            </Item>
            <Item label={
              <span>
              metric&nbsp;
                <Tooltip title="度量方式">
                  <Icon type="question-circle-o" style={{color:'#5f6267'}} />
                </Tooltip>
              </span>
              } required>
              {form.getFieldDecorator('conf.metric', {
                initialValue: conf.metric,
                rules: [{required: true, message: 'Please input data!'}],
              })
              (<Select onBlur={this.handleSubmit}>
              <Option value="euclidean">euclidean</Option>
              <Option value="precomputed">precomputed</Option>
            </Select>)}
            </Item>
            <Item label={
              <span>
              algorithm&nbsp;
                <Tooltip title="近邻算法求解方式">
                  <Icon type="question-circle-o" style={{color:'#5f6267'}} />
                </Tooltip>
              </span>
              } required>
              {form.getFieldDecorator('conf.algorithm', {
                initialValue: conf.algorithm,
                rules: [{required: true, message: 'Please input data!'}],
              })
              (<Select onBlur={this.handleSubmit}>
                <Option value="auto ">auto </Option>
                <Option value="ball_tree">ball_tree</Option>
                <Option value="kd_tree">kd_tree</Option>
                <Option value="brute">brute</Option>
              </Select>)}
            </Item>
            <Item label={
              <span>
              leafSize&nbsp;
                <Tooltip title="叶的大小（大于0的整数）">
                  <Icon type="question-circle-o" style={{color:'#5f6267'}} />
                </Tooltip>
              </span>
              } required>
              {form.getFieldDecorator('conf.leafSize', {
                initialValue: conf.leafSize,
                rules: [{required: true, message: 'Please input data!'}],
              })
              (<Input disabled={form.getFieldValue('conf.algorithm') === 'auto'||form.getFieldValue('conf.algorithm') === 'brute'} />)}
            </Item>
          </Card>
        </Form>
      </Fragment>
    );
  }
};

export default Form.create()(withPropsAPI(DensityCluster));