import React from 'react';
import { Row, Col, Input, PageHeader, Button, Modal, Form, message } from 'antd';
import GGEditor, { Flow } from 'gg-editor';
import { withRouter } from 'react-router-dom';
import  {connect} from 'react-redux'

import { FlowDetailPanel } from './components/EditorDetailPanel';
import ProcessTree from './components/ProcessTree'
import CustomNode from './components/node/nodeTensorflow';
import StartNode from './components/node/nodeStart';

import { actionCreator } from './store'
import styles from './index.module.less'

import {loadPublicComponentInfo} from '../../api/component'
import {createExperiment, editorExperiment, experimentisEdit} from '../../api/experimets'

@withRouter @Form.create()
class CreateExperiment extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      processObj: {},
      isCreate: true,
      canEdit: false,
    }
  }

  handleSaveExp = () => {  // 保存实验
    console.log(this.state.processObj)
    const { getFieldsValue} = this.props.form;
    const { paramsData } =this.props.paramsData;
    let params = getFieldsValue();
    let f = true
    for(let i in params){
      if(params[i]===undefined){
        message.warning('请填写完整信息!');
        f=false;
        this.setState({ buttonLoading:false})
        break;
      }
    }
    console.log(typeof(this.props.isMl));
    //改变参数
    if(this.props.paramsData.mnist_load_data_inputdata&&this.props.isMl ===0){
      this.props.paramsData.mnist_load_data_data_file=this.props.paramsData.mnist_load_data_inputdata;
      delete this.props.paramsData.mnist_load_data_inputdata;
    }
    params = {
      fkWorkflowId: this.props.processId,
      paramJsonString: JSON.stringify(this.props.paramsData),
      ...params
    }
    console.log("params----------",params);
    //console.log(this.state.processObj);
    createExperiment(params).then(res=>{
      if(res){
        if(res.data.meta.code === '001'){
          message.success('实验保存成功')
          this.setState({visible: false})
          this.props.history.push('/index/process')
        }else{
          message.error(res.data.meta.message)
        }
      }
    })
  }

  handleCancel = () => {
    this.setState({visible: false})
  }

  editExperiment = () => {
    const {experimentId} = this.state
    editorExperiment({experimentId, paramJsonString: JSON.stringify(this.props.paramsData)}).then(res=>{
      if(res.data.meta.code === '001'){
        message.success(res.data.meta.message)
      }else{
        message.error(res.data.meta.message)
      }
    }).catch(err=>{
      message.error('实验保存失败！')
    })
  }

  componentWillMount(){
    let componentInfo = {}    // 获取组件的参数配置 
    loadPublicComponentInfo({isMl: this.props.isMl}).then(res=>{
      if(res.data.meta.code == '001'){
        res.data.data['System Component'].map(item=>{
          componentInfo = { ...componentInfo, [item.componentId] : item.variables}
        })
        this.props.saveComponentInfo(componentInfo)
      }
    })
    const processObj = this.props.processData ? JSON.parse(this.props.processData) : {}
    if(processObj){
      processObj.nodes && processObj.nodes.map(item=>{
        item.id = item.id.toString()  // 将id转为字符串
      });
      processObj.edges && processObj.edges.map((item,index)=>{
        item.id = (item.id + index).toString();
        item.source = item.source.toString();
        item.target = item.target.toString();
      })
    }
    this.setState({processObj})
    const {location} = this.props
    if(location.query && location.query.paramJsonString){  // 编辑和查看实验的情况
      const {paramJsonString, experimentId} = location.query
      this.isExperimentCanEdit(experimentId)  // 判断实验是否可以编辑
      const params = JSON.parse(paramJsonString)
      for(let key in params){
        this.props.saveParamsData(key, params[key])
      }
      this.setState({
        isCreate: false,
        experimentId,
      })
    }else{  // 创建实验的情况 
      processObj.nodes && processObj.nodes.map(item=>{
        // 初始化实验参数
        if(item.conf){
          for(let key in item.conf){
            console.log(item.label+'_'+key, item.conf[key])
            this.props.saveParamsData(item.label+'_'+key, item.conf[key])
          }
        }
      });
    }
  }

  isExperimentCanEdit = (id) => {
    let canEdit = false
    experimentisEdit({experimentId: id}).then(res=>{  // 判断实验是否可以编辑
      if(res){
        if(res.data.meta.code === '001'){
          const {data} = res.data
          for(let [code, mess] of Object.entries(data)){
            if(code === '0'){
              canEdit = true;
            }else{
              message.info(mess)
            }
          }
        }else{
          message.error("获取可编辑状态失败！")
        }
      }else{
        message.error("获取可编辑状态失败！")
      }
    }).catch(err=>{
        message.error("获取可编辑状态失败！")
    }).finally(res=>{
      this.setState({canEdit})
    })
  }

  componentWillUnmount(){
    this.props.saveComponentInfo(undefined)
    this.props.saveParamsData('', '')
    this.props.saveProcess('')
    this.props.saveProcessId(undefined)
  }

  onSaveClick = () => {
    const {paramsData} = this.props
    if(this.props.isMl === 1 && !paramsData['data_import_inputdata']){
      message.warning('请为数据导入组件选择数据集！');
      return
    }
    // if(this.props.isMl === '0' && !paramsData['data_import_inputdata']){
    //   message.warning('请为数据导入组件选择数据集！');
    //   return
    // }
    if(this.state.isCreate){  // 创建流程的情况
      this.setState({visible: true})
    }else{ // 更新实验的参数
      this.editExperiment()
    }
  }

  render() {
    const {getFieldDecorator}=this.props.form;
    console.log(this.state.processObj);
    const config={
        rules:[{
            required:true,
            message:'Please input!'
        }]
    }

    const { isCreate, canEdit} = this.state

    return (<div className={styles.canvasContainer}>
        <GGEditor className={styles.editor}>
            <PageHeader
              className={styles.pageheader}
              title="实验管理"
              onBack={() => this.props.history.goBack()}
              subTitle="请配置节点参数以创建一个实验"
              extra={
                (isCreate || canEdit) && <Button type="primary" onClick={this.onSaveClick}>保存实验</Button>
              }
          />
          <Row type="flex" className={styles.editorBd} style={{height:'100%'}}>
            <Col span={4} className={styles.editorSidebar}>
              <ProcessTree />
            </Col>
            <Col span={14} className={styles.editorContent}>
              <CustomNode />
              <StartNode />
              <Flow 
                className={styles.flow}
                data={this.state.processObj}
              />
            </Col>
            <Col span={6} className={styles.editorSidebar}>
              <FlowDetailPanel/>
            </Col>
          </Row>
        </GGEditor>
      <Modal
          title="流程"
          visible={this.state.visible}
          onOk={this.handleSaveExp}
          onCancel={this.handleCancel}
          confirmLoading={this.state.buttonLoading}
        >
          <Form labelCol={{span:6}} wrapperCol={{span:12}}>
            <Form.Item label='实验名称'>
              {
                  getFieldDecorator('name',config)(<Input placeholder='请输入实验名称' />)
              }
            </Form.Item>
            <Form.Item label='实验描述'>
              {
                  getFieldDecorator('experimentDesc',config)(<Input.TextArea placeholder='请输入实验描述' />)
              }
            </Form.Item>
          </Form>
        </Modal>
   </div> );
  }
};

const mapStateToProps = (state) => {
    return {
      processData: state.process.processData,
      processId: state.process.processId,
      paramsData: state.process.paramsData,
      isMl: state.process.isML,
    }
  }
  
const mapDispatchToProps = (dispatch) => {
    return {
      saveComponentInfo(componentInfo){
        dispatch(actionCreator.saveComponentInfo(componentInfo))
      },
      saveParamsData(name, param){
        dispatch(actionCreator.saveParamsData(name, param))
      },
      saveProcessId(id){
        dispatch(actionCreator.saveProcessId(id))
      },
      saveProcess(process){
        dispatch(actionCreator.saveProcess(process))
      }
    }
  }
  
export default connect(mapStateToProps, mapDispatchToProps)(CreateExperiment);

