import React from 'react'
import { useDrag } from 'react-dnd'

import * as chartType from '../chartConstants'

import IconItem from './IconItem';

import styles from './index.module.less'

function DragableComponent(props){
    const [{ isDragging }, drag] = useDrag({
        item: { type: props.type },
        collect: (monitor) => ({
          isDragging: monitor.isDragging(),
        }),
      })

    return (
        <div className={styles.chartItem} style={{ opacity: isDragging ? 0.5 : 1 }} ref={drag}>
            <IconItem type={props.type}/>
            &nbsp;
            {props.value}
        </div>
    )
}

export default function ComponentPanel(props){

    return (
        <div className={styles.wrapper}>
            <DragableComponent value="热度图" type={chartType.HEAT_MAP}/>
            <DragableComponent value="折线图" type={chartType.LINE_CHART}/>
        </div>
    )
}