import React, {Component} from 'react'
import { Toolbar, Command, withPropsAPI, ContextMenu } from 'gg-editor';
import { Row, Col, PageHeader, Form, message } from 'antd';
import {connect} from 'react-redux'

import { actionCreator } from '../../store';
import { getComponentResult } from '../../../../api/experimets'

import Loading from '../../../../common/Loading.png';
import Success from '../../../../common/success.png';
import ErrorImg from '../../../../common/Error.png';


@withPropsAPI
class run extends Component{
  constructor(props){
    super(props);
    this.state={
      nodesId: {},
      runningResult: {}
    }
  }

  componentDidMount(){
      document.getElementById("selectAll").click();
      this.getRunningStatus()
  }

  componentWillUnmount(){
    this.props.saveProcessState(0)
    if(this.state.source){
      this.state.source.close()
    }
  }

  changeStateIcon = () => {
    const { propsAPI, saveNodesId } = this.props;
    const { executeCommand, update, getSelected, find } = propsAPI;
    let nodesId = {};
    const selectedNodes = getSelected();
    for(let i=0;i<selectedNodes.length;i++){
      const item = selectedNodes[i];
      const label = item.model.label;
      const id = item.id;
      nodesId[label] = id;
      if(label == 'start'){
        continue
      }
      executeCommand(() => {
        console.log(item)
        update(item, {
          state_icon_url:Loading,
        });
      });
    }
    saveNodesId(nodesId);
    let that = this
    this.setState({nodesId}, ()=>{
      that.changeFirst()
    })
  }
  
  changeStateError = () => {
    const { propsAPI, nodesId } = this.props;
    const { executeCommand, update } = propsAPI;
    executeCommand(() => {
      for (let node in nodesId){
        update(nodesId[node], {
          animate: 0,
          animateRadius:0,
          state_icon_url:ErrorImg
        });
      }
    })
  }
  
  changeFirst = () => {
    const { propsAPI } = this.props;
    const {nodesId} = this.state;
    const { executeCommand, find, update } = propsAPI;
    let that = this;
    console.log('changeFirst时的nodesId', nodesId)
    executeCommand(() => {
      const firstNode = find(that.props.nodesId['data_import']);
      update(firstNode, {
        animate: 175,
        animateRadius:3
      });
    })
  }
  
  changeStateSuccess = (task, nextTask) => {
    let that = this;
    const { propsAPI, nodesId } = this.props;
    const { executeCommand, find, update } = propsAPI;
    console.log('task', task)
    console.log('nextTask', nextTask)
    executeCommand(() => {
      if(nodesId){
        const getNode = find(that.props.nodesId[task]);
        this.getNodeOutput(getNode.getModel());
        update(getNode, {
          state_icon_url:Success,
          animate: 0,
          animateRadius:0
        });
        const getNextNode = find(that.props.nodesId[nextTask]);
        update(getNextNode, {
          animate: 175,
          animateRadius:3
        });
      }
    });
  }

  getNodeOutput = (currentNode) => {
    const {experimentRunningId: runningId} = this.props
    console.log(currentNode)
    const {componentId, nodeType} = currentNode
    this.props.saveRunningResult({componentName: nodeType})
    getComponentResult({componentId, runningId}).then(res=>{
      if(res){
        if(res.data.meta.code === '001'){
          // message.success('获取组件输出结果成功！')
          this.props.saveRunningResult({resultList: res.data.data, componentName: nodeType})
        }
      }
    })
  }
  
  getRunningStatus = () => {
    const {saveProcessState} = this.props
    let that = this
    that.changeStateIcon();
    if(!!window.EventSource) {
      const {conversationId} = this.props
      saveProcessState(1);
      var source = new EventSource('/sse/push?uuid=' + conversationId );
      this.setState({source})
      source.addEventListener("message", function(event) {
        if(event.data){
          const eventData = JSON.parse(event.data)
          console.log(eventData);
          if(eventData.meta.code === '001'){
            that.changeStateSuccess(eventData.data.taskName, eventData.data.nextTask);
          }else if(eventData.meta.code === '004'){
            message.success(`流程完毕！`)
            saveProcessState(0);
            source.close();
            that.setState({source: undefined})
          }else if(eventData.meta.code === '005'){
            message.success('成功存储模型！')
          }else{
            message.error('执行出错！流程结束！')
            saveProcessState(0);
            that.changeStateError();
            source.close();
            that.setState({source: undefined})
          }
        }
      }, false);
      source.error = function(e){
        saveProcessState(0);
        message.error('执行出错！')
        that.changeStateError();
      }
    }
  }    

  render(){
    return(
        <ContextMenu>
            <Command name="selectAll" > 
            <div id="selectAll"></div>  
            </Command>
        </ContextMenu>
    )
  }
}

const mapStateToProps = (state) => {
    return {
      conversationId: state.process.conversationId,
      nodesId: state.process.nodesId,
      experimentRunningId: state.process.experimentRunningId,    // 运行id
      runningResult: state.process.runningResult,
    }
  }
  
const mapDispatchToProps = (dispatch) => {
    return {
      saveProcessState(processState){
        dispatch(actionCreator.saveProcessState(processState));
      },
      saveNodesId(nodesId){
        dispatch(actionCreator.saveNodesId(nodesId));
      },
      saveResultList(resultList, processLogId){
        dispatch(actionCreator.saveResultList(resultList, processLogId));
      },
      saveRunningResult(result){
        dispatch(actionCreator.saveRunningResult(result))
      }
    }
  }
  
export default connect(mapStateToProps, mapDispatchToProps)(withPropsAPI(run));