import React, {useState} from 'react'
import {useDispatch, useSelector} from 'react-redux'
import {InputNumber, Tag} from 'antd'
import {CaretRightFilled, CaretDownFilled} from '@ant-design/icons'


import { actionCreator } from "../../store";

import styles from './index.module.less'


export default function SizeConfig(props){
    const {x: wrapperX, y: wrapperY, width: wrapperW, height: wrapperH} 
                  =document.getElementById('chart-canvas')==null? {x:300,y:-4,width:1000,height:800}
                  :document.getElementById('chart-canvas').getBoundingClientRect();
    const chartData = useSelector(state => state.charts.chartData);
    const activeIndex = useSelector(state => state.charts.activeIndex);
    const dispatch = useDispatch()
    const [fold, setFold] = useState(false);

    const currentConfig = chartData[activeIndex] ? chartData[activeIndex] : null
    
    const handleFold = (e) => {
        setFold((fold) => !fold)
    }

    const onChange=(e,type)=>{
        
        let value=parseInt(e.target.value);
        console.log(value,type);
        switch(type){
            case 'w':dispatch(actionCreator.changeSize({
                width: value >= 0 ? (value >= wrapperW ? wrapperW : value) : 0,
                height:currentConfig.height
            }));break;
            case 'h':dispatch(actionCreator.changeSize({
                width:currentConfig.width,
                height:value >= 0 ? (value >= wrapperH ? wrapperH : value) : 0,
            }));break;
            case 'x':dispatch(actionCreator.moveChart({
                x:value >= 0 ? (value <= wrapperW-currentConfig.width ? value : wrapperW-currentConfig.width) : 0 ,
                y:currentConfig.y
            }));break;
            case 'y':dispatch(actionCreator.moveChart({
                x:currentConfig.x,
                y:value >= 0 ? (value <= wrapperH-currentConfig.height ? value : wrapperH-currentConfig.height) : 0
            }));break;
        }
       
    }

    return (
        <div>
            <div onClick={handleFold}>
                {
                    fold ? <CaretDownFilled /> : <CaretRightFilled />
                }
                <span>基础设置</span>
            </div>
            {
                fold &&
                <>
                  <p className={styles.line}>尺寸：</p>
                  <div className={styles.base}>
                    <span>W:&nbsp;</span>
                    <InputNumber 
                        value={parseInt(currentConfig.width)} 
                        onBlur={(e)=>onChange(e,'w')}
                        max={wrapperW}
                        min={0}
                        />
                    &emsp;
                    <span>H:&nbsp;</span>
                    <InputNumber 
                        value={parseInt(currentConfig.height)} 
                        onBlur={(e)=>onChange(e,'h')}
                        max={wrapperH}
                        min={0}
                        />
                  </div>
                  <p className={styles.line}>位置：</p>
                  <div className={styles.base}>
                    <span>X:&nbsp;</span>
                    <InputNumber 
                        value={parseInt(currentConfig.x)} 
                        onBlur={(e)=>onChange(e,'x')}
                        max={wrapperX+wrapperW-currentConfig.x}
                        min={0}
                        />
                    &emsp;
                    <span>Y:&nbsp;</span>
                    <InputNumber 
                        value={parseInt(currentConfig.y)} 
                        onBlur={(e)=>onChange(e,'y')}
                        max={wrapperY+wrapperH-currentConfig.y}
                        min={0}
                        />
                  </div>
                </> 
            }
        </div>
    )
}