import React, {useRef, useEffect, useState} from 'react'
import { useDrop } from 'react-dnd'
import {useSelector, useDispatch} from 'react-redux'

import {getResultJson, fetchChartsConfig } from '../../../api/chartConfig'

import ChartComponent from '../ChartComponents'
import * as chartType from '../chartConstants'
import {actionCreator} from '../store'

import heatConfig from '../ChartComponents/heatMapOption'
import stepLineConfig from '../ChartComponents/StepLineOption'

import styles from './index.module.less'
import { message } from 'antd'



function getOptions(type,data) {

    switch(type){
        //
        case chartType.HEAT_MAP: return heatConfig(data.heatMap.series.data,
            data.heatMap.xAxis.data, data.heatMap.yAxis.data); break;
        case chartType.LINE_CHART: return stepLineConfig(data.lineChart.series.data[0],
            data.lineChart.xAxis); break;
    }
}



export default function CanvasContent(props){
    const containerRef = useRef()
    const chartData = useSelector(state => state.charts.chartData);
    const activeIndex = useSelector(state => state.charts.activeIndex);
    const dispatch = useDispatch();
    const typeList={
        "heatmap":12,
        "linechart":13
    }

    useEffect(()=>{
        let params={
            fkExperimentRunningId:parseInt(sessionStorage.getItem("runningId")) 
        }
        console.log(params);

        fetchChartsConfig(params).then(res=>{
            let data=JSON.parse(res.data.data.resultJsonString);
            console.log(data);
            dispatch(actionCreator.loadChart(data));

        }).catch(err=>{
            message.error(err);
        })
        return ()=>{
            sessionStorage.removeItem("runningId");
            sessionStorage.removeItem("componentId");
        }
    },[])

    // console.log(chartData, activeIndex);
    const [, dropRef] = useDrop({  
        accept: [...chartType.allType],
        drop: (item, monitor) => {
            const {x: wrapperX, y: wrapperY, width: wrapperW, height: wrapperH} 
                  = document.getElementById('chart-canvas').getBoundingClientRect()
            const currentMouseOffset = monitor.getClientOffset();
            
            if(item.type !== chartType.CHART_COMPONENT){
                const x = currentMouseOffset.x - wrapperX - 150
                const y = currentMouseOffset.y - wrapperY - 150
                //console.log(x, y)

                const params={
                    runningId:sessionStorage.getItem("runningId"),
                    componentId:sessionStorage.getItem("componentId"),
                    type:typeList[item.type]
                }


                getResultJson(params).then(res=>{
                    console.log(res.data.data);
                    const chartConfig = {
                        // 保证 渲染的图不会超出画布的范围
                        x: x >= 0 ? (x <= wrapperW-300 ? x : wrapperW-300) : 0 , 
                        y: y >= 0 ? (y <= wrapperH-300 ? y : wrapperH-300) : 0, 
                        width: 300,
                        height: 300,
                        id: Date.now(),      // 时间戳作为唯一的id
                        type: item.type,
                        options: getOptions(item.type,res.data.data)
                    }
                    dispatch(actionCreator.addChart(chartConfig))
                    
                }).catch(err=>{
                    console.log(err);
                })
                
                
                
            }else{
                const x = currentMouseOffset.x - wrapperX - item.x
                const y = currentMouseOffset.y - wrapperY - item.y
                const chartWidth = chartData[activeIndex].width
                const chartHeight = chartData[activeIndex].height
                const moveConfig = {
                    x: x >= 0 ? (x <= wrapperW-chartWidth ? x : wrapperW-chartWidth) : 0 ,
                    y: y >= 0 ? (y <= wrapperH-chartHeight ? y : wrapperH-chartHeight) : 0,
                }
                dispatch(actionCreator.moveChart(moveConfig))
            }
        },
      })

    return (
        <div 
            id = "chart-canvas"
            className={styles.wrapper}
            ref={(elem) => {
                containerRef.current = elem
                dropRef(elem)
            }}
        >
            {
                chartData.map(
                    (chart, index) => <ChartComponent 
                            config={chart} 
                            key={chart.id}
                            isActive={activeIndex === index}
                            index={index}
                        />
                )
            }
        </div>
    )
}