import React, {useState} from 'react'
import {useDispatch, useSelector} from 'react-redux'
import {InputNumber, Switch, Input, Radio} from 'antd'
import {CaretRightFilled, CaretDownFilled} from '@ant-design/icons'


import { actionCreator } from "../../store";

import styles from './index.module.less'


export default function Config(props){
    const chartData = useSelector(state => state.charts.chartData);
    const activeIndex = useSelector(state => state.charts.activeIndex);
    const dispatch = useDispatch()
    const [fold, setFold] = useState(false)

    const currentConfig = chartData[activeIndex] ? chartData[activeIndex] : null
    const {title} = currentConfig.options

    const changeSwith = (checked) => {
        dispatch(actionCreator.changeTitle({...title, show: checked}))
    }
    const onTextChange = (e) => {
        dispatch(actionCreator.changeTitle({...title, text: e.target.value}))
    }
    const onLeftChange = (e) => {
        dispatch(actionCreator.changeTitle({...title, left: e.target.value}))
    }
    const onTopChange = (e) => {
        dispatch(actionCreator.changeTitle({...title, top: e.target.value}))
    }
    const handleFold = (e) => {
        setFold((fold) => !fold)
    }

    return (
        <div>
            <div onClick={handleFold}>
                {
                    fold ? <CaretDownFilled /> : <CaretRightFilled />
                }
                <span>标题设置</span>
            </div>
            {
                fold && <> 
                <div style={{'marginLeft':'10px'}}>
                    <span>显示标题 &nbsp; </span><Switch checked={title.show} onChange={changeSwith} checkedChildren="是" unCheckedChildren="否"/>
                </div>
                {
                    title.show && <>
                    <div className={styles.line}>
                        <span>文本 &nbsp; &nbsp;</span><Input value={title.text} onChange={onTextChange}></Input>
                    </div>
                    <div className={styles.line}>
                        <span>水平位置 &nbsp;</span>
                        <Radio.Group onChange={onLeftChange} value={title.left}>
                            <Radio.Button value="left">左</Radio.Button>
                            <Radio.Button value="center">中</Radio.Button>
                            <Radio.Button value="right">右</Radio.Button>
                        </Radio.Group>
                    </div>
                    <div className={styles.line}>
                        <span>垂直位置 &nbsp;</span>
                        <Radio.Group onChange={onTopChange} value={title.top}>
                            <Radio.Button value="top">上</Radio.Button>
                            <Radio.Button value="middle">中</Radio.Button>
                            <Radio.Button value="bottom">下</Radio.Button>
                        </Radio.Group>
                    </div>
                </>}
            </>}
        </div>
    )
}