import React, {useState} from 'react'
import {useDispatch, useSelector} from 'react-redux'
import {Radio , InputNumber} from 'antd'
import {CaretRightFilled, CaretDownFilled} from '@ant-design/icons'
import {CompactPicker  } from 'react-color'


import { actionCreator } from "../../store";

import styles from './index.module.less'


export default function LineConfig(props){
    // const {x: wrapperX, y: wrapperY, width: wrapperW, height: wrapperH} 
    //               = document.getElementById('chart-canvas').getBoundingClientRect();
    const chartData = useSelector(state => state.charts.chartData);
    const activeIndex = useSelector(state => state.charts.activeIndex);
    const dispatch = useDispatch()
    const [fold, setFold] = useState(false);

    const currentConfig = chartData[activeIndex] ? chartData[activeIndex] : null;
    const lineStyle=currentConfig.options.series.lineStyle;
    const handleFold = (e) => {
        setFold((fold) => !fold)
    }
    const changeLineType = e => {
      dispatch(actionCreator.changeLineStyle({
        type : e.target.value, 
        width : lineStyle.width,
        color : lineStyle.color
      }));
    };

    const changeLineWidth = e => {
      dispatch(actionCreator.changeLineStyle({
        type : lineStyle.type, 
        width : e.target.value,
        color : lineStyle.color
      }));
    };

    const changeLineColor = color => {
      dispatch(actionCreator.changeLineStyle({
        type : lineStyle.type, 
        width : lineStyle.width,
        color : color.hex
      }));
    };
    

    return (
        <div>
            <div onClick={handleFold}>
                {
                    fold ? <CaretDownFilled /> : <CaretRightFilled />
                }
                <span>线条设置</span>
            </div>
            {
                fold &&
                <>
                  <p className={styles.line}>类型：</p>
                  <div className={styles.base}>
                  <Radio.Group onChange={changeLineType} value={lineStyle.type}>
                    <Radio value='solid'>实线</Radio>
                    <Radio value='dashed'>虚线</Radio>
                    <Radio value='dotted'>点线</Radio>
                  </Radio.Group>
                  </div>
                  <p className={styles.line}>线宽：</p>
                  <div className={styles.base}>
                  <InputNumber 
                      value={lineStyle.width} 
                      onBlur={changeLineWidth}
                      max={10}
                      min={0}
                      />
                  </div>
                  <p className={styles.line}>颜色：</p>
                  <div className={styles.base}>
                      <CompactPicker 
                        color={ lineStyle.color }
                        onChange={changeLineColor}
                      />
                  </div>
                </> 
            }
        </div>
    )
}