import React,{Component} from 'react';
import {
        PageHeader, Button, Input, Spin, Table, Modal, List, Pagination,
        Tabs,Form,Divider,Popover,Select,Tag,message,Tooltip, Card, Icon, Empty,
        Avatar, 
} from 'antd';
import {withRouter} from 'react-router-dom'
import IconFont from '../../common/IconFont';


// import * as actionCreators from './store/actionCreators';
import {connect} from 'react-redux';
import CreateExperiment from './components/createExperiment';
import {getTemplate, deleteTemplate, searchTemplate, restoreTemplate, createExperiment, updateTemplate} from '../../api/template';
import styles from './index.module.less';
import {actionCreator} from '../process/store'

const { Meta } = Card;

@withRouter @Form.create()
class TemplateManage extends Component{

    state={
        templateType:1,  // 0表示系统模板
        dataSource:[],
        page: 1,
        pageSize: 10,
        total: 0,
        editorTemplate: {},
        editorVisible:false,

        dataInTrash:[],
        deleteVisible:false,
        listLoading:false,
        modalLoading:false,
        deleteItem:[],

        selectValue:'name',
        
        trashPage:1,
        trashTotal:1,
        keyword:'',
        createVisible:false,
        processName:'1',
        processId:'',
        ggeditorObj:{},
        isRestore:false,
    }
    getTemplate=()=>{
        const {templateType, page, pageSize}=this.state;
        this.setState({
            listLoading:true
        })
        getTemplate({ type:templateType, pageNum: page, pageSize, isDeleted: 0}).then(res=>{
            if(res.data.meta.code==='001'){
                const data=res.data.data;
                console.log(data)
                const dataSource=data.templateVOList.map((item,index)=>({
                    ...item,
                    index:index+1+(page-1)*10
                }))
                this.setState({
                    listLoading:false,
                    dataSource,
                    total:data.Total
                })
            }else{
                message.error('获取数据失败!');
                this.setState({
                    listLoading:false,
                }) 
            }
        }).catch(err=>{
            message.error('获取数据失败!');
            this.setState({
                listLoading:false,
            }) 
        })
    }
    getTemplateInTrash=()=>{
        const {trashPage}=this.state;
        this.setState({
            modalLoading:true
        })
        const params={
            pageNum:trashPage,
            pageSize:6,
            type:1,
            isDeleted:1,
            userId:2
        }
        getTemplate(params).then(res=>{
            if(res.data.meta.code==='001'){
                const data=res.data.data;
                const dataInTrash=data.templateVOList.map((item,index)=>({
                    ...item,
                    index:index+1+(trashPage-1)*10,
                    key:item.id
                }))
                this.setState({
                    modalLoading:false,
                    dataInTrash,
                    trashTotal:data.Total
                })
            }else{
                message.error('获取数据失败!');
                this.setState({
                    modalLoading:false,
                }) 
            }
        }).catch(err=>{
            message.error('获取数据失败!');
            this.setState({
                modalLoading:false,
            }) 
        })
    }
    changeKeyword=(e)=>{
        this.setState({
            keyword:e.target.value
        })
    }
    handleSearch=()=>{
        var {keyword}=this.state;
        keyword.replace(/\s/g,(a)=>'');
        if(keyword==''){
            message.warning('搜索关键字不能为空格字符串！')
        }else{
            this.setState({
                page:1
            }, this.searchTemplate)
        }
    }
    searchTemplate=()=>{
        const {page,templateType,keyword,selectValue, pageSize}=this.state;
        this.setState({
            listLoading:true
        })
        const params={
            type:templateType,
            pageNum:page,
            pageSize,
        }
        if(selectValue=='name'){
            params.name=keyword;
        }else{
            params.tag=keyword
        }
        searchTemplate(params).then(res=>{
            if(res.data.meta.code==='001'){
                const data=res.data.data;
                const dataSource=data.templateVOList.map((item,index)=>({
                    ...item,
                    index:index+1+(page-1)*10
                }))

                this.setState({
                    listLoading:false,
                    dataSource,
                    total:data.Total
                })
            }else{
                message.error('搜索失败!');
                this.setState({
                    listLoading:false,
                }) 
            }
        }).catch(err=>{
            message.error('搜索失败!');
            this.setState({
                listLoading:false,
            }) 
        })
    }
    changeType=(key)=>{
        console.log(key)
        this.setState({
            templateType:parseInt(key),
            page:1
        },()=>{
            this.getTemplate();
        })
    }
    openModal=()=>{
        this.setState({
            deleteVisible:true,
            trashPage:1
        },this.getTemplateInTrash)
    }
    closeModal=()=>{
        this.setState({
            deleteVisible:false
        })
        this.getTemplate();
    }
    changeDeleteItem=(selectedRowKeys,selectedRows)=>{
        this.setState({
            deleteItem:selectedRowKeys
        })
    }
    createExperiment=()=>{
        const {editorTemplate}=this.state
        this.props.form.validateFields((err, values)=>{
            if(!err){
                console.log(values);
                const params={
                    ...values,
                    templateId: editorTemplate.id,
                }
                createExperiment(params).then(res=>{
                    if(res.data.meta.code=='001'){
                        console.log(res.data)
                        message.success('创建实验成功!');
                        this.props.saveProcess(editorTemplate.ggeditorObjectString);
                        this.props.history.push({
                            pathname: '/index/experiment/create',
                            query: {
                                paramJsonString: res.data.data.paramJsonString,
                                experimentId: res.data.data.id
                            }
                        })
                    }else{
                        message.success(res.data.meta.message);
                    }
                }).catch(err=>{
                    message.success('创建实验失败!');
                })
            }
        })
    }
    closecreateExperiment=()=>{
        this.setState({
            createVisible:false, 
            editorTemplate: {},
        })
        this.props.form.resetFields();
    }
    
    editTemplate = () => {
        const {editorTemplate}=this.state
        this.props.form.validateFields((err, values)=>{
            if(!err){
                console.log(values);
                const params={
                    ...values,
                    templateId: editorTemplate.id,
                }
                updateTemplate(params).then(res=>{
                    if(res.data.meta.code=='001'){
                        message.success('编辑模板成功!');
                        this.setState({
                            editorTemplate: {},
                            editorVisible: false,
                        })
                        this.getTemplate()
                    }else{
                        message.success(res.data.meta.message);
                    }
                }).catch(err=>{
                    message.success('编辑模板失败!');
                })
            }
        })
    }
    
    openCreateModal=(editorTemplate)=>{
        this.setState({editorTemplate, createVisible: true})
    }
    restoreExperiment=()=>{

    }
    notRestore=()=>{
        this.setState({
            isRestore:false
        })
    }
    deleteTemplate=(id)=>{
        const params={
            templateIds:id+''
        };
        deleteTemplate(params).then(res=>{
            if(res.data.meta.code==='001'){
                message.success('删除成功!');
            }else{
                message.error('删除失败!');
            }
            this.getTemplate();
        }).catch(err=>{
            message.error('删除失败!');
        })
    }
    changeSelectValue=(v)=>{
        this.setState({
            selectValue:v
        })
    }
    deleteTemplatePermanently=(id)=>{
        let params={};
        const {deleteItem}=this.state;
        if(typeof id=='number'){
            params.templateIds=id+'';
        }else{
            params.templateIds = deleteItem.join(',');
        }
        deleteTemplate(params).then(res=>{
            if(res.data.meta.code==='001'){
                this.setState({deleteItem: []})
                message.success('删除成功!');
            }else{
                message.error('删除失败!');
            }
            this.getTemplateInTrash();
        }).catch(err=>{
            message.error('删除失败!');
        })
    }
    restoreTemplate=(id)=>{
        console.log(id)
        let params={};
        const {deleteItem}=this.state;
        if(typeof id =='number'){
            params.templateIds=id+'';
        }else{
            params.templateIds=deleteItem.join(',');
        }
        restoreTemplate(params).then(res=>{
            if(res.data.meta.code==='001'){
                this.setState({deleteItem: []})
                message.success('还原成功!');
            }else{
                message.error('还原失败!');
            }
            this.getTemplateInTrash();
        }).catch(err=>{
            message.error('还原失败!');
        })
    }

    componentDidMount=()=>{
        this.getTemplate();
    }
    componentWillUnmount(){
        // this.props.saveTemplateName('');
        // this.props.saveTemplateParams({});
        // this.props.saveProcessId(-1);
        // this.props.saveExperimentId(-1);
    }

    checkTemplate = (template) => {
        console.log('查看实验', template)
        const {paramJsonString, ggeditorObjectString} = template
        this.props.saveProcess(ggeditorObjectString)
        console.log('跳转')
        this.props.history.push({
            pathname: '/index/template/check',
            query: {
                paramJsonString
            }
        })
    }

    onEditClick = (editorTemplate) => {
        this.setState({
            editorTemplate,
            editorVisible: true,
        })
    }

    checkDesc = (editorTemplate) => {
        this.setState({
            editorTemplate,
            checkVisable: true,
        })
    }
    
    render(){
        const { total, templateType, editorTemplate, isRestore, deleteVisible, listLoading, modalLoading, dataSource, dataInTrash,
                deleteItem, pageSize, selectValue, trashPage, trashTotal, keyword, createVisible, page, editorVisible, checkVisable}=this.state;
        const trashCols=[
            { key: 'icon',render: ()=><Avatar size={30} icon={<Icon type="project"  />} style={{ backgroundColor: '#27afff' }} />},
            {
                title:'模板名', key:'name', dataIndex:'name', align:'center'
            },{
                title:'模板标签', key:'tags', dataIndex:'tags', align:'center',
                render:(text,record)=>{
                    return text.split(',').map(i=>(
                        i && <Tag color="orange" style={{height:20,padding:'0 5px',marginRight:5,fontSize:12}}>{i}</Tag>
                    ))
                }
            },{
                title:'模板描述', key:'templateDesc', dataIndex:'templateDesc', align:'center'
            },{
                title:'操作',
                key:'action',
                dataIndex:'action',
                render:(text,record)=>{
                    return (
                        <div>
                            <a onClick={()=>this.restoreTemplate(record.id)}>还原</a>
                            <Divider type='vertical' />
                            <a onClick={()=>this.deleteTemplatePermanently(record.id)}>彻底删除</a>
                        </div>
                    )
                },
                align:'center'
            }
        ];
        const templateCols=[
            { key: 'icon',render: ()=><Avatar size={30} icon={<Icon type="project"  />} style={{ backgroundColor: '#27afff' }} />},
            {
                title:'模板名', key:'name', dataIndex:'name', 
            },{
                title:'模板标签', key:'tags', dataIndex:'tags', 
                render:(text,record)=>{
                    return text.split(',').map(i=>(
                        i && <Tag color="orange" style={{height:20,padding:'0 5px',marginRight:5,fontSize:12}}>{i}</Tag>
                    ))
                }
            },{
                title:'模板描述', key:'templateDesc', dataIndex:'templateDesc', 
            },
        ];
        const processCols=[
            { key: 'icon',render: ()=><Avatar size={30} icon={<Icon type="apartment"  />} style={{ backgroundColor: '#27afff' }} />},
            {title: '流程名称',dataIndex: 'name',key: 'name',
                render:(text,record)=>(
                <a onClick={()=>this.selectProcess(record)}>{text}</a>
                )
            },
            {title: '标签',dataIndex: 'tags', key: 'tags',
                render:tags=>(
                <>
                    {tags.split(',').map((item,index)=>(
                    <Tag color='orange' key={index}>{item}</Tag>
                    ))}
                </>
                )},
            { title: '创建时间',dataIndex: 'createTime', key: 'createTime',},
            {title: '流程描述',dataIndex: 'workflowDesc', key: 'workflowDesc',},
        ];
        const {getFieldDecorator}=this.props.form;
        return (
            <div className={styles.templateWrap}>
                <PageHeader
                    style={{border: '1px solid rgb(235, 237, 240)',marginBottom:10, marginRight:10}}
                    title='模板管理'
                    subTitle='已有模板列表'
                    extra={[
                        <Input.Search 
                            placeholder='搜索模板' 
                            onSearch={this.handleSearch} 
                            value={keyword} 
                            onChange={this.changeKeyword} 
                            style={{width:360, marginRight:'10px'}}
                            addonAfter={
                                <Select value={selectValue} onChange={this.changeSelectValue} style={{width: 120}}>
                                    <Select.Option value='name'>按名称搜索</Select.Option>
                                    <Select.Option value='tag'>按标签搜索</Select.Option>
                                </Select>
                            }  
                        />,
                        <Button type="primary" onClick={this.openModal}>回收站</Button>
                    ]}
                />
                <div className={styles.buttonWrap}>
                    <Button type={templateType===0 ? 'primary':''} onClick={()=>this.changeType(0)}>系统模板</Button>
                    <Button type={templateType===0 ? '':'primary'} onClick={()=>this.changeType(1)}>自定义模板</Button>
                </div>
                {dataSource.length === 0 && <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} style={{alignSelf: 'center'}}/>}
                <Spin spinning={listLoading}><div style={{display: 'flex', flexWrap: 'wrap', marginLeft: 10}}>
                {
                    dataSource.map(item=>{
                        return <Card
                                    style={{ width: 300,  marginRight: 15, marginBottom:15}}
                                    bodyStyle={{paddingTop:0}}
                                    cover={
                                    <img
                                        alt="example"
                                        style={{width: 300, height: 200,}}
                                        src={require('../../common/分支模板.png')}
                                        onClick={()=>this.checkTemplate(item)}
                                    />
                                    }
                                    actions={[
                                    <Tooltip placement="bottom" title='从模板创建实验'><Icon type="plus-square" key='add' onClick={()=>this.openCreateModal(item)}/></Tooltip>,
                                    <Tooltip placement="bottom" title='删除模板'><Icon type="delete" key="delete" onClick={()=>this.deleteTemplate(item.id)}/></Tooltip>,
                                    <Tooltip placement="bottom" title='编辑模板'><Icon type="edit" key="edit" onClick={()=>this.onEditClick(item)} /></Tooltip>,
                                    <Tooltip placement="bottom" title='查看详细信息'><Icon type="ellipsis" key="ellipsis" onClick={()=>this.checkDesc(item)}/></Tooltip>,
                                    ]}
                                >
                                    <Meta
                                    title={item.name}
                                    description={
                                        <><div className={styles.cardMeta}>{item.templateDesc}</div>
                                        {
                                            item.tags.split(',').map(tag=>{
                                               return tag && <Tag color="green" style={{height:20,marginRight:5,fontSize:12}}>{tag}</Tag>
                                            })
                                        }</>
                                    }
                                    />
                                </Card>
                    })
                }
                </div></Spin>
                <Pagination
                    style={{marginRight: 10, float: 'right', marginBottom: 20 }}
                    defaultCurrent={1}
                    current={page}
                    onChange={(page)=>{
                        this.setState({page}, this.getTemplate)
                    }}
                    pageSize={pageSize}
                    total={total}
                />  
                {deleteVisible && <Modal
                    title='回收站'
                    footer={null}
                    visible={deleteVisible}
                    onCancel={this.closeModal}
                    width={'80%'}
                >
                    <div>
                        <Spin spinning={modalLoading}>
                            <Table
                                columns={trashCols}
                                dataSource={dataInTrash}
                                rowSelection={{
                                    type:'checkbox',
                                    onChange:this.changeDeleteItem
                                }}
                                pagination={{
                                    pageSize: 6,
                                    total:trashTotal,
                                    current:trashPage,
                                    onChange:(page)=>{
                                        this.setState({
                                            trashPage:page
                                        },this.getTemplateInTrash)
                                    }
                                  }}
                            />
                        </Spin>
                        <Button type='primary' style={{marginRight:10}} onClick={()=>this.restoreTemplate(deleteItem)}>批量还原</Button>
                        <Button type='primary'  onClick={this.deleteTemplatePermanently}>批量删除</Button>
                    </div>
                </Modal>}
                {createVisible && <Modal
                    title='创建实验'
                    visible={createVisible}
                    onOk={this.createExperiment}
                    onCancel={this.closecreateExperiment}
                    okText='确定'
                    cancelText='取消'
                >
                    <Form labelCol={{span:6}} wrapperCol={{span:18}}>
                    <Form.Item label='实验名称'>
                        {
                            getFieldDecorator('experimentName',{
                                rules:[{required:true,whitespace:true}],
                                initialValue:''
                            })(<Input placeholder='请输入实验名称'/>)
                        }
                    </Form.Item>
                    <Form.Item label='实验描述'>
                        {
                            getFieldDecorator('experimentDesc',{
                                rules:[{required:true,whitespace:true}],
                                initialValue:''
                            })(<Input placeholder='请输入实验描述'/>)
                        }
                    </Form.Item>
                    </Form>
                    {/* <CreateExperiment onRef={ref=>this.createForm=ref} processId={editorTemplate.fkWorkflowId}/> */}
                </Modal>}
                {isRestore && <Modal
                    visible={isRestore}
                    onOk={this.restoreExperiment}
                    onCancel={this.notRestore}
                    okText='还原'
                    cancelText='取消'
                >
                    标记为模板的实验已被放入回收站，是否需要还原该实验?
                </Modal>}
                { // 编辑模板的弹窗
                    editorVisible && <Modal
                    title='编辑模板'
                    visible={editorVisible}
                    onOk={this.editTemplate}
                    onCancel={()=>this.setState({editorVisible: false, editorTemplate: {}})}
                    okText='确定'
                    cancelText='取消'
                >
                    <Form labelCol={{span:6}} wrapperCol={{span:18}}>
                    <Form.Item label='模板名称'>
                        {
                            getFieldDecorator('tamplateName',{
                                rules:[{required:true,whitespace:true}],
                                initialValue: editorTemplate.name
                            })(<Input placeholder='请输入模板名称'/>)
                        }
                    </Form.Item>
                    <Form.Item label='模板标签'>
                        {
                            getFieldDecorator('templateTags',{
                                rules:[{required:true,whitespace:true}],
                                initialValue: editorTemplate.tags 
                            })(<Input placeholder='请输入模板标签，以逗号分隔'/>)
                        }
                    </Form.Item>
                    <Form.Item label='模板描述'>
                        {
                            getFieldDecorator('templateDesc',{
                                rules:[{required:true,whitespace:true}],
                                initialValue: editorTemplate.templateDesc 
                            })(<Input placeholder='请输入模板描述'/>)
                        }
                    </Form.Item>
                    </Form>
                </Modal>
                }
                {
                    checkVisable && <Modal
                        title='查看模板'
                        visible={checkVisable}
                        onCancel={()=>this.setState({checkVisable: false, editorTemplate: {}})}
                        width={"60%"} footer={null}
                    >
                        <div className={styles.descTitleFirst}>流程信息</div>
                        <Table columns={processCols} dataSource={editorTemplate.workflow?[editorTemplate.workflow]:[]} pagination={false} size="middle"></Table>
                        <div className={styles.descTitle}>模板信息</div>
                        <Table columns={templateCols} dataSource={editorTemplate?[editorTemplate]:[]} pagination={false} size="middle"></Table>
                        <div className={styles.descTitle}>模板详细描述</div>
                        <Input.TextArea defaultValue={editorTemplate.templateDesc} readOnly={true}>
                            {/* <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} style={{alignSelf: 'center'}}></Empty> */}
                        </Input.TextArea>
                    </Modal>
                }
            </div>
        )
    }
}
const mapStateToProps=(state)=>({

})
const mapDispatchToProps=(dispatch)=>({
    saveProcess(ggeditorString){
        dispatch(actionCreator.saveProcess(ggeditorString))
    },
})

export default connect(mapStateToProps, mapDispatchToProps)(TemplateManage);
