import React, { Fragment } from 'react';
import { Card, Form, Input, Select, Menu } from 'antd';
import { withPropsAPI } from 'gg-editor';
import { connect } from 'react-redux';
import { actionCreator } from '../../../process/store';
import PubSub from 'pubsub-js';
import NodeItemPanel from './NodeItemPanel';
import { LocalDataCard} from './configurationCards/index';

class DetailForm extends React.Component {

  get item() {
    const { propsAPI } = this.props;
    console.log(propsAPI.getSelected()[0]);
    return propsAPI.getSelected()[0];
  }

  handleSubmit = (e) => {
    if (e && e.preventDefault) {
      e.preventDefault();
    }

    const { form, propsAPI } = this.props;
    const { getSelected, executeCommand, update } = propsAPI;

    setTimeout(() => {
      form.validateFieldsAndScroll((err, values) => {
        if (err) {
          return;
        }

        const item = getSelected()[0];

        if (!item) {
          return;
        }

        executeCommand(() => {
          update(item, {
            ...values,
          });
        });
      });
    }, 0);
  };


  renderConfiguration = ( label) => {
      let nodeItem = this.item;
      PubSub.publish('nodeItem',nodeItem);
      let detail = this.item.getModel();
      this.props.saveItem(detail);
      switch(detail.componentId){
        case 5:  // 数据导入组件
          return (<LocalDataCard detail={detail}/>);
        default:
          return <NodeItemPanel detail={detail}/>
      }
  }

  render() {
    if (!this.item) {
      return (
        <Card title='组件参数配置' bordered={false} bodyStyle={{background: '#fff'}}>
        </Card>
      );
    }

    return (
      <Card type="inner" size="small" bordered={false} bodyStyle={{background: '#fff'}}>
        {this.renderConfiguration(this.props.label)}
      </Card>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    label: state.process.label,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    saveItem(item) {   // 设置currentNode
      console.log(item)
      dispatch(actionCreator.saveItemObj(item));
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Form.create()(withPropsAPI(DetailForm)));
