import Axios from '../utils/request';

//获取流程展示列表
export async function getBaseDataSource(params) {
  return Axios({
    url:'/workflow/selectAllWorkflowByUserId',
    method: 'POST',
    params
  })
}

//新建流程
export async function createProcess(params) {
  let f = new FormData()
  for(let key in params){
    f.append(key, params[key])
  }
  return Axios({
    url:'/workflow/createAndSaveWorkflow',
    method: 'POST',
    data: f,
    headers: {
      'Content-Type': 'application/json',
    }
  })
}
