import React, { Component } from 'react';
import echarts from 'echarts/lib/echarts';
// 引入提示框和标题组件
import 'echarts/lib/component/tooltip';
import  'echarts/lib/chart/scatter';
// 引入提示框和标题组件
import 'echarts/lib/component/visualMap';
import 'echarts/lib/component/title';


class Scatter extends Component{

  constructor(props){
    super(props)
    this.state = {
      scatterShow:'none',
      showOption:'none'
    }
  }

  componentDidMount(){
    this.handleData()
  }

  handleData(){
    const {data} = this.props
    console.log(data)
    let contentObj = {}
    data.map(item => {
      if(contentObj[item[0]]){
        contentObj[item[0]] ++
      }else{
        contentObj[item[0]] = 1
      }
    })
    console.log(contentObj)
    let data0 = [],
        data1 = [],
        ySort = new Set(),
        xSort = new Set();
    for(let [key, value] of Object.entries(contentObj)){
      let point = key.split(' ')
      xSort.add(point[0])
      ySort.add(point[1])
      if(point[0] === point[1]){
        data1.push([point[0], point[1], value])
      }else{
        data0.push([point[0], point[1], value])
      }
    }
    console.log(data0)
    console.log(data1)
    xSort = Array.from(xSort).sort()
    ySort = Array.from(ySort).sort()
    let that = this
    this.setState({data0, data1, ySort, xSort}, ()=>{
      that.eChartScatter()
    })
  }

  eChartScatter() {
    // 基于准备好的dom，初始化echarts实例
    const myChart = echarts.init(document.getElementById('scatterEchart'));
    const { data0, data1, ySort, xSort} = this.state;
    // let data1 = [['1','1', 1], ['3','3', 9]],
    //     data0 = [['1','2', 3],['2','1', 2]],
    //     ySort = ['1', '2', '3'],
    let  y = ['预测成功', '预测失败'];

    // 绘制图表
    myChart.setOption({
        // 标题
      title: {
        // 正标题
        text: '真实值与预测值关系图',
        left: 'center',
      },
      
      tooltip: {
        position: 'top',
        // 鼠标可以进入tooltip提示框并点击提示框内部的信息
        enterable: true,
        formatter: function (params) {
          // 滑过圆点显示的数据
          return params.value[0]
        }
      },

      // grid: {
      //   left: 2,
      //   bottom: 10,
      //   right: 110,
      //   containLabel: true
      // },

      // X轴的数据
      xAxis: {
        type: 'category',
        name: '真实值',
        data: [ '', ...xSort],
        scale: true,
        // min: yMin,
        // max: yMax,
        // 标签和数据点都会在两个刻度之间的带(band)中间
        boundaryGap: false,
        // 线的样式
        splitLine: {
          show: true,
          lineStyle: {
            color: 'rgba(204,204,204,0.30)',
            type: 'dashed',
          }
        },
        // X轴字体样式
        axisLine: {
          show: true,
          lineStyle: {
            color: '#333',
            type: 'solid',
          }
        }
      },

      // 圆点的颜色
      // color: ['#7460ee', '#66cd2e'],

      // Y轴的数据
      yAxis: {
        type: 'category',
        name: '预测值',
        data: [ '', ...ySort],
        boundaryGap: false,
        // name: dataList.yName,
        // 控制网线
        splitLine: {
          show: true,
          lineStyle: {
            color: 'rgba(204,204,204,0.30)',
            type: 'dashed',
          }
        },
        // Y轴的颜色和字体宽度
        axisLine: {
          show: true,
          lineStyle: {
            color: '#333',
            type: 'solid',
          }
        },
        triggerEvent: true
      },
      legend: {
        data: y,
        right: 10
      },
      // 数据系列，一个图表可能包含多个系列，每一个系列可能包含多个数据
      series: [
        {
            name: y[0],
            type: 'scatter',
            symbol:'triangle',
            // 是否开启鼠标 hover 的提示动画效果
            // hoverAnimation: false,
            symbolSize: function (val) {
                return Math.sqrt(val[2]) * 10;  
            },
            data: data0,
            animationDelay: function (idx) {
                return idx * 5;
            },
            itemStyle: {
                normal: {
                    shadowBlur: 10,
                    opacity: 0.8,
                    shadowColor: 'rgba(120, 36, 50, 0.5)',
                    shadowOffsetY: 5,
                    color: new echarts.graphic.RadialGradient(0.4, 0.3, 1, [{
                        offset: 0,
                        color: 'rgb(251, 118, 123)'
                    }, {
                        offset: 1,
                        color: 'rgb(204, 46, 72)'
                    }])
                }
            }
        },
        {
          name: y[1],
          type: 'scatter',
          symbolSize: function (val) {
            return Math.sqrt(val[2]) * 10;
          },
          data: data1,
          animationDelay: function (idx) {
            return idx * 5;
          },
          itemStyle: {
            normal: {
              opacity: 0.6,
              shadowBlur: 10,
              shadowColor: 'rgba(25, 100, 150, 0.5)',
              shadowOffsetY: 5,
              color: new echarts.graphic.RadialGradient(0.4, 0.3, 1, [{
                  offset: 0,
                  color: 'rgb(129, 227, 238)'
              }, {
                  offset: 1,
                  color: 'rgb(25, 183, 207)'
              }])
            }
          }
        }
      ]
    }, true);

  };

  render(){
    return(
        <div id="scatterEchart" style={{ width:650, height:500}} ></div>
    )
  }
}

export default Scatter;