import React,{Component} from 'react';
import {PageHeader,Button,Input,Spin,Table,Modal,Radio,message,Steps,Tag,Divider, Select, Avatar, Icon} from 'antd';

import {getCustomComponentInfo, deleteCustomComponent, deleteCustomComponentPermanently,restoreCustomComponent, deleteWorkflowComponent, 
    deleteWorkflowComponentPermanently,
    searchCustomComponentByName,searchCustomComponentByTag, getCustomComponentInTrash, createCustomComponent} from '../../api/component';
import './index.less';

import CreateCustomComponent from './components/CreateCustomComponent'
import ViewProcess from './components/createProcess'
import { render } from 'less';

class CustomComponent extends Component{

    state={
        loading:false,
        dataSource:[],
        type:'1',
        searchWord:'',
        visible:false,

        listLoading:false,
        newVisible:false,
        viewVisible: false, //查看、编辑流程组件
        
        current:0,
        values:{},
        paramsValues:[],
        dataInTrash:[],
        selectedRowKeys:[],
        searchType:'name',

        total:0,
        page:1,
        pageSize: 10,

        trashRadioValue: '1',
        trashTotal:0,
        trashPage:1,

    }

    getComponent=()=>{
        const {type, page, pageSize}=this.state;
        this.setState({
            loading:true
        })
        const params={
            type,
            pageSize,
            pageNum: page,
        }
        getCustomComponentInfo(params).then((res)=>{
            if(res.data.meta.code=='001'){
                const data=res.data.data;
                const dataSource=data.list.map((item,index)=>({
                    ...item,
                    index:index+1+(page-1)*10
                }))
                this.setState({
                    loading:false,
                    dataSource,
                    total:res.data.data.total
                })
            }
        }).catch(err=>{
            message.error('获取数据出错!')
            console.log(err)
            this.setState({
                loading:false,
            })
        })
    }
    getComponentInTrash=()=>{
        const {trashRadioValue,trashPage, pageSize}=this.state;
        this.setState({
            listLoading:true
        })
        const params={
            type: trashRadioValue,
            pageSize,
            pageNum:trashPage,
            // userId:2
        }
        getCustomComponentInTrash(params).then((res)=>{
            if(res.data.meta.code==='001'){
                const data=res.data.data;
                const dataInTrash=data.list.map((item,index)=>({
                    ...item,
                    index:index+1+(trashPage-1)*10,
                    key:item.id
                }))
                this.setState({
                    listLoading:false,
                    dataInTrash,
                    trashTotal:data.size
                })
            }
        }).catch(err=>{
            message.error('获取数据出错!')
            this.setState({
                listLoading:false,
            })
        })
    }

    clickButton=(t)=>{
        this.setState({
            type:t,
            page:1
        }, this.getComponent)
    }
    changeSearchWord=(e)=>{
        this.setState({
            searchWord:e.target.value
        })
    }
    changePage=(page)=>{
        let that = this
        this.setState(
            {page},
            () =>{
                if(that.state.searchWord){
                    that.searchComponent();
                }else{
                    that.getComponent();
                }
            }
        )
    }
    openModal=()=>{
        this.setState({
            visible:true,
            trashPage:1
        },this.getComponentInTrash)
        
    }
    closeModal=()=>{
        this.setState({
            visible:false
        })
        this.getComponent();
    }
    changeRadioValue=(e)=>{
        this.setState({
            trashRadioValue:e.target.value,
            trashPage:1,
            selectedRowKeys: [],
        }, this.getComponentInTrash)
    }
    changeDeleteItem=(selectedRowKeys,selectedRows)=>{
        this.setState({
            selectedRowKeys
        })
    }

    openNew=()=>{
        this.setState({
            newVisible:true
        })
    }
    
    
    nextStep=()=>{
        const {current}=this.state;
        if(current===0){
            this.componentForm.props.form.validateFields((err,values)=>{
                if(!err){
                    this.setState({
                        current:current+1,
                        values
                    })
                }else{
                    message.warning('请填写完整信息!')
                }
            });
        }else{
            this.paramsForm.props.form.validateFields((err,values)=>{
                if(!err){
                    this.setState({
                        current:current+1,
                        paramsValues:this.handleParamsValues(values)
                    })
                }else{
    
                }
            })
        }
    }
    
    handleParamsValues=(values)=>{
        if(values.name||values.inputName){
            var l;
            if(values.name){
                l=values.name.length;
            }else{
                l=values.inputName.length;
            }
            const p=[];
            for(let j=0;j<l;j++){
                let obj={};
                for(let i in values){
                    if(i!=='keys'&&values[i][j]){
                        obj[i]=values[i][j];
                    }
                }
                if(JSON.stringify(obj)!=='{}')  p.push(obj);
            }
            return p;
        }else{
            return []
        }
    }
    deleteComponent=(id)=>{
        const {type} = this.state
        const deleteCom = type === '1' ? deleteWorkflowComponent({workflowComponentId: id}): deleteCustomComponent({componentId:id})
        deleteCom.then((res)=>{
            if(res.data.meta.code==='001'){
                message.success('删除成功!');
                this.getComponent();
            }else{
                message.error('删除失败!');
            }
        }).catch(err=>{})
    }
    deleteComponentPermanently=(id)=>{
        const {trashRadioValue: type} = this.state
        let componentIds
        if(typeof id=='number'){
            componentIds=id+'';
        }else{
            componentIds=this.state.selectedRowKeys.join(',')
        }
        const deletePermanently = type === '1' ? deleteWorkflowComponentPermanently({workflowComponentId: componentIds}) : 
                                                 deleteCustomComponentPermanently({componentIds})
        deletePermanently.then(res=>{
            if(res.data.meta.code==='001'){
                message.success('删除成功!');
                this.getComponentInTrash();
            }else{
                message.error('删除失败!');
            }
        }).catch(err=>{
            message.error('删除失败!');
        })
    }
    restoreComponent=(id)=>{
        const {trashRadioValue: type} = this.state
        const params={type};
        if(typeof id=='number'){
            params.componentIds=id+'';
        }else{
            params.componentIds=this.state.selectedRowKeys.join(',')
        }
        restoreCustomComponent(params).then(res=>{
            if(res.data.meta.code==='001'){
                message.success('还原成功!');
                this.getComponentInTrash();
            }else{
                message.error('还原失败!');
            }
        }).catch(err=>{
            message.error('还原失败!');
        })
    }
    changeSearchType=(v)=>{
        this.setState({
            searchType:v
        })
    }
    handleSearch=()=>{
        var {searchWord}=this.state;
        searchWord.replace(/\s/g,(a)=>'');
        if(searchWord==''){
            message.warning('搜索关键字不能为空格字符串！')
        }else{
            this.setState({
                page:1
            },this.searchComponent)
        }
    }
    searchComponent=()=>{
        const {searchType,searchWord,page,type, pageSize}=this.state;
        this.setState({
            loading:true
        })
        const params={
            pageSize,
            pageNum:page,
            type,
            // userId:2
        }
        if(searchType==='name'){
            params.keyword=searchWord;
            searchCustomComponentByName(params).then(res=>{
                if(res.data.meta.code==='001'){
                    console.log(res.data)
                    const data=res.data.data;
                    const dataSource=data.ComponentList.list.map((item,index)=>({
                        ...item,
                        index:index+1+(page-1)*10
                    }))
                    this.setState({
                        loading:false,
                        dataSource,
                        total:data.size
                    })
                }else{
                    this.setState({
                        dataSource:[],
                        loading:false
                    })
                }
            }).catch(err=>{
                message.error('搜索失败!');
                console.log(err)
                this.setState({
                    loading:false,
                })
            })
        }else{
            params.tag=searchWord;
            searchCustomComponentByTag(params).then(res=>{
                if(res.data.meta.code==='001'){
                    const data=res.data.data;
                    const dataSource=data.ComponentList.list.map((item,index)=>({
                        ...item,
                        index:index+1+(page-1)*10
                    }))
                    this.setState({
                        loading:false,
                        dataSource,
                        total:data.size
                    })
                }else{
                    this.setState({
                        dataSource:[],
                        loading:false
                    })
                }
            }).catch(err=>{
                message.error('搜索失败!');
                this.setState({
                    loading:false,
                })
            })
        }
    }
    componentDidMount(){
        this.getComponent();
    }

    closeNew = () =>{
        this.setState({
            newVisible: false
        })
        this.getComponent()
    }

    closeView = () => {
        this.setState({
            currentRecord: undefined,
            viewVisible: false
        })
    }

    viewProcessComponent = (record) =>{
        this.setState({
            currentRecord: record,
            viewVisible: true
        })
    }
    
    render(){
        const {dataSource,dataInTrash,loading,total,trashTotal,selectedRowKeys,trashPage,page,type,searchWord, trashRadioValue,
            visible,listLoading,newVisible,searchType, pageSize, viewVisible, currentRecord}=this.state;
        const columns=[
            { align: 'center', key: "icon", render: ()=><Avatar size={40} icon={<Icon type="deployment-unit" />} style={{ backgroundColor: '#27afff' }} />},
            {
                title:'组件名', key:'name', dataIndex:'name', align:'center',
                render: (text, record)=>{
                    if(type === '1') return <a onClick={()=>this.viewProcessComponent(record)}>{text}</a>
                    else return text
                }
            },
            {
                title:'组件标签',
                key:'tags',
                dataIndex:'tags',
                align:'center',
                render:(text,record)=>{
                    if(text){
                        return text.split(',').map((i, index)=>(
                            <Tag key={index} color="orange" style={{height:20,padding:'0 5px',marginRight:5,fontSize:12}}>{i}</Tag>
                        ))
                    }else{
                        return ''
                    }
                }
            },
            {
                title:'组件描述',
                key:'componentDesc',
                dataIndex:'componentDesc',
                align:'center'
            },
            {
                title:'创建时间',
                key:'createTime',
                dataIndex:'createTime',
                align:'center'
            },
            {
                title:'操作',
                key:'action',
                dataIndex:'action',
                render:(text,record)=>{
                    return <a style={{textDecoration:'none'}} onClick={()=>this.deleteComponent(record.id)}>删除</a>
                },
                align:'center'
            }
        ];
        const columns1=[
            {
                title:'组件名',
                key:'name',
                dataIndex:'name',
                align:'center'
            },
            {
                title:'组件标签',
                key:'tags',
                dataIndex:'tags',
                align:'center',
                render:(text,record)=>{
                    return text.split(',').map((i, key)=>(
                        <Tag key={key} color="orange" style={{height:20,padding:'0 5px',marginRight:5,fontSize:12}}>{i}</Tag>
                    ))
                }
            },
            {
                title:'组件描述',
                key:'componentDesc',
                dataIndex:'componentDesc',
                align:'center'
            },
            {
                title:'操作',
                key:'action',
                dataIndex:'action',
                render:(text,record)=>{
                    return (
                        <div>
                            <a onClick={()=>this.deleteComponentPermanently(record.id)}>永久删除</a>
                            <Divider type='vertical' />
                            <a onClick={()=>this.restoreComponent(record.id)}>还原</a>
                        </div>
                    )
                },
                align:'center'
            }
        ]
        return (
            <div className='contentWrap'>
                <PageHeader
                    style={{border:'1px solid rgb(235, 237, 240)'}}
                    title='组件管理'
                    subTitle='已有自定义组件列表'
                    extra={[
                        <Button type='primary' onClick={this.openNew}>创建组件</Button>
                    ]}
                />
                <div className='buttonWrap'>
                    <Button type={type==='1'?'primary':''} onClick={()=>this.clickButton('1')}>自定义流程组件</Button>
                    <Button type={type==='0'?'primary':''} onClick={()=>this.clickButton('0')}>自定义算法组件</Button>
                    <Button type={type==='2'?'primary':''} onClick={()=>this.clickButton('2')}>自定义模型组件</Button>
                    <Button type='primary' style={{float:'right'}} onClick={this.openModal}> 回收站</Button>
                    <Input.Search 
                        placeholder='搜索自定义组件' 
                        onSearch={this.handleSearch} 
                        onChange={this.changeSearchWord} 
                        value={searchWord} 
                        style={{width:360, marginRight:'10px'}}
                        addonAfter={
                            <Select value={searchType} onChange={this.changeSearchType} style={{float:'right', width:120}}>
                                <Select.Option value='name'>按名称搜索</Select.Option>
                                <Select.Option value='tag'>按标签搜索</Select.Option>
                            </Select>
                        }
                    /> 
                </div>
                <div>
                    <Spin spinning={loading}>
                        <Table
                            columns={columns}
                            dataSource={dataSource}
                            pagination={{
                                pageSize,
                                total,
                                current:page,
                                onChange:this.changePage
                            }}
                        />
                    </Spin>
                </div>
                <Modal
                    title='回收站'
                    visible={visible}
                    footer={null}
                    onCancel={this.closeModal}
                    width={800}
                >
                    <div>
                        <Radio.Group value={trashRadioValue} onChange={this.changeRadioValue}>
                            <Radio value='1'>自定义流程组件</Radio>
                            <Radio value='0'>自定义算法组件</Radio>
                            <Radio value='2'>自定义模型组件</Radio>
                        </Radio.Group>
                        <Spin spinning={listLoading}>
                            <Table
                                columns={columns1}
                                dataSource={dataInTrash}
                                rowSelection={{
                                    onChange:this.changeDeleteItem,
                                    selectedRowKeys
                                }}
                                pagination={{
                                    pageSize,
                                    current:trashPage,
                                    total:trashTotal,
                                    onChange:(page)=>{
                                        this.setState({
                                            trashPage:page
                                        },this.getComponentInTrash)
                                    }
                                  }}
                            />
                        </Spin>
                        <Button type='primary' style={{marginRight:10}} onClick={this.deleteComponentPermanently}>批量删除</Button>
                        <Button type='primary' onClick={this.restoreComponent}>批量还原</Button>
                    </div>
                </Modal>
                {
                    newVisible && <Modal
                        title='创建自定义组件'
                        width={1200}
                        visible={newVisible}
                        onCancel={this.closeNew}
                        footer={false}
                    >
                        <CreateCustomComponent onclose={this.closeNew}></CreateCustomComponent>
                    </Modal>
                }{
                    viewVisible && <Modal
                    title='自定义流程组件'
                    width={1200}
                    visible={viewVisible}
                    onCancel={this.closeView}
                    footer={false}
                >
                    <ViewProcess ggeditorObjectString={currentRecord.ggeditorObjectString} componentId={currentRecord.id}></ViewProcess>
                </Modal>
                }
            </div>
        )
    }
}
export default CustomComponent;
