import React, {useState} from 'react'
import {useDispatch, useSelector} from 'react-redux'
import {Input, message} from 'antd'
import {CaretRightFilled, CaretDownFilled} from '@ant-design/icons'


import { actionCreator } from "../../store";

import styles from './index.module.less'


export default function AxisConfig(props){
    const { TextArea } = Input;

    const chartData = useSelector(state => state.charts.chartData);
    const activeIndex = useSelector(state => state.charts.activeIndex);
    const dispatch = useDispatch();

    const currentConfig = chartData[activeIndex] ? chartData[activeIndex] : null;
    const axisData=currentConfig.options[props.axis]?currentConfig.options[props.axis].data:null;
    const [data, setData] = useState(axisData);
    const [fold, setFold] = useState(false);

    const handleFold = (e) => {
        setFold((fold) => !fold)
    }

    const changeText=(e)=>{
      setData(e.target.value);
    }
    const changeAxis = (e) => {
      let data=e.target.value.split(",");
      if(data.length==axisData.length&&data[data.length-1]!=""){
        console.log("数据格式符合");
        if(props.type=='x'){
          dispatch(actionCreator.changeXAxisData(data));
        }else if(props.type=='y'){
          dispatch(actionCreator.changeYAxisData(data));
        }
        
      }else{
        message.error("输入的数据格式不对");
        setData(axisData);
      }
    }

    return (
        axisData?
        <div>
            <div onClick={handleFold}>
                {
                    fold ? <CaretDownFilled /> : <CaretRightFilled />
                }
                <span>{props.title}</span>
            </div>
            {
                fold &&
                <>
                  <p className={styles.line}>类型设置：</p>
                  
                  <p className={styles.line}>坐标轴 （请用,作为分隔符）：</p>
                  <TextArea
                    autoSize={true}
                    value={data}
                    allowClear={true}
                    onChange={changeText}
                    onBlur={changeAxis}
                  />

                </> 
            }
        </div>:null
    )
}