import React, {Component}from 'react';
import { Divider} from 'antd';
import { Toolbar, Command } from 'gg-editor';
import IconFont from '../../../../common/IconFont';
import styles from './index.module.less';
import Save from './components/Save/Save';


class FlowToolbar extends Component {
  render() {
    return(
      <div className={styles.canvaHeader}>
        <Toolbar className={styles.toolbar}>
          <Command name="clear">
            <div className={styles.com} title="清空" >
              <IconFont type={`icon-rerunning-instance`}/><span>清空</span>
            </div>
          </Command>
          {this.props.save !== 'no' && <>
            <Save />
            <Divider type="vertical" className={styles.divider} />
           </> }

          <Command name="copy">
            <div className={styles.com} title="复制" >
              <IconFont type={`icon-copy`}/><span>复制</span>
            </div>
          </Command>
          <Command name="paste">
            <div className={styles.com} title="粘贴" >
              <IconFont type={`icon-paste`}/><span>粘贴</span>
            </div>
          </Command>
          <Command name="delete">
            <div className={styles.com} title="删除" >
              <IconFont type={`icon-delete`}/><span>删除</span>
            </div>
          </Command>
          <Divider type="vertical" className={styles.divider} />
          <Command name="zoomIn">
            <div className={styles.com} title="放大" >
              <IconFont type={`icon-zoom-in`}/><span>放大</span>
            </div>
          </Command>
          <Command name="zoomOut">
            <div className={styles.com} title="缩小" >
              <IconFont type={`icon-zoom-out`}/><span>缩小</span>
            </div>
          </Command>
        </Toolbar>
        {/* <span className={styles.runningMode} title="并行运行方式">
            <IconFont type={`icon-repeat`}/>
        </span> */}
      </div>
    )
  }
};

export default FlowToolbar;
