import React, {Component} from 'react';
import { Link, withRouter } from 'react-router-dom'
import {connect} from 'react-redux'
import { Table, Divider, Spin, Tag, PageHeader, Modal, Input, Button, message, Icon, Avatar, Select, Tooltip} from 'antd';

import {
        getBaseDataSource,  downloadProcess, deleteProcess, searchWorkflowByName, 
        searchWorkflowByTags, 
      } from '../../api/process'
import {deleteExperiment,  runExperimentRunning} from '../../api/experimets'

import Trash from './components/Trash'
import ExperimentRunning from './components/RunningRecord'

import ProcessModal from'./components/modals/processModal'
import ExperimentModal from'./components/modals/experimentModal'
import TemplateModal from './components/modals/templateModal'
import ProcessToComponentForm from './components/modals/ProcessToComponentForm'

import {actionCreator} from './store'

import style from './index.module.less'

@withRouter 
class Process extends Component{
  constructor(props){
    super(props);
    this.state = {
      dataSource: [],            //流程-数据数据列表
      runningDataSource:[],      //实验运行列表
      loading: false,            //表格加载状态
      total:0,                   //当前页的数据条数
      page: 1,                   //当前页
      pageSize: 10,               //页容量
      search:'',                 //搜索结果
      selectValue: 'name',

      cloneProcessVisible:false,      //克隆流程对话框
      cloneExperimentsVisible:false,  //克隆实验对话框
      editorExperimentsVisible:false, //修改实验对话框
      markTemplateVisible:false,      //标记实验对话框
      runningVisible:false,           //实验运行对话框
      trashVisible:false,             //回收站对话框
      markProcessVisable: false,      // 流程标记为组件
      
      cloneId:null ,           //克隆流程的id
      processId:null ,         //流程id    ---从指定的流程创建实验
      experimentId:null,       //实验id    ---实验运行删除，还原等操作
      selectProcessKey:'',     //定位实验 的流程id（字符串）模板新建实验功能-当实验已经存在进行的定位功能
      selectExperimentKey:null //定位实验 的实验id 把实验记录的名称标红
    }
  }
  componentDidMount(){
    this.getBaseDataSource()
  }
  //获取流程和实验信息
  getBaseDataSource=()=>{
    this.setState({loading: true})
    const params = {
      pageNum: this.state.page,
      pageSize: this.state.pageSize,
      isDeleted:'0',
    }
    const {page} = this.state
    getBaseDataSource(params).then((res)=>{
        if(res){
          if(res.data.meta.code == '001'){
            const dataSource = res.data.data.WorkflowVOList.map((item,index)=>{
              const {ggeditorObejectString, isMl} = item
              let experimentList = item.experimentList.map(experiment=>{
                return {...experiment, ggeditorObejectString, isMl}
              })
              return { ...item, experimentList, index: index+1+(page-1)*10, key:item.id }
            })
            console.log(dataSource)
            this.setState({ 
              dataSource, 
              loading: false,
              total: res.data.data.Total
            })
          }else{
            message.error(res.data.meta.message)
          }
        }
      }).catch(err=>{
        console.log(err)
      })
  }
  //方法执行之后的message展示
  showMessage=(res)=>{
    if(res){
      if(res.data.meta.code === '001'){
        message.success(res.data.meta.message)
      }else{
        message.error(res.data.meta.message)
      }
    }
  }
  
  selectProcess=(record)=>{
    const {ggeditorObejectString, id} = record
    this.props.saveProcess(ggeditorObejectString)
    this.props.saveProcessId(id)
    this.props.history.push({
      pathname: '/index/process/create',
    })
  }
  
  //下载流程 
  downloadProcess=(id)=>{
    downloadProcess(id).then(res=>{
      console.log(res)
      // if(res){
      //   var blob = new Blob([res.data])
      //   console.log(blob)
      //   var href = window.URL.createObjectURL('');
      //   var link = document.createElement('a');
      //   link.setAttribute("download", 'pixture');
      //   link.href = href;
      //   link.click();
    // }
    }).catch(err=>{
      console.log(err)
    })
  }
  //搜索流程
  handleSearch = value => {
    let that = this
    this.setState({
      page: 1,
      search: value
    }, function(){
      that.getSearchData(value)      
    })
  }
  //换页
  changePage = () => {
    const { search } = this.state
    search ? this.getSearchData(search) : this.getBaseDataSource()
  }
  //获取搜索结果
  getSearchData=(value)=>{
    const {page, pageSize, selectValue}=this.state
    let params={
      pageNum: page,
      pageSize: this.state.pageSize,
    }
    let searchWorkflow
    if(selectValue === 'name'){
      searchWorkflow = searchWorkflowByName
      params = {...params, workflowName: value}
    }else{
      searchWorkflow = searchWorkflowByTags
      params = {...params, workflowTags: value}
    }
    searchWorkflow(params).then(res=>{
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data.WorkflowVOList.map((item,index)=>{
            return { ...item, index: index+1+(page-1)*10,key:item.id}
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total
          })
        }else{
          message.error(res.data.meta.message)
          this.setState({ loading: false })
        }
      }
    }).catch(err=>{
      this.setState({ loading: false })
      console.log(err)
    })
  }
  //克隆流程 
  cloneProcess=(id)=>{
    this.setState({
      cloneProcessVisible:true,
      cloneId:id
    })
  }
  //从流程创建实验
  newExperiments=(process)=>{
    this.props.saveProcess(process.ggeditorObejectString);
    this.props.saveProcessId(process.id)
    this.props.changeML(process.isMl)
    this.props.history.push('/index/experiment/create')
  }
  //删除流程
  deleteProcess=(id)=>{
    console.log('删除实验'+id);
    const params={
      workflowId:id
    }
    deleteProcess(params).then(res=>{
      console.log('删除流程')
      this.showMessage(res)
      this.getBaseDataSource()
    }).catch(err=>{
      console.log(err)
    })
  }
  //编辑实验
  editorExperiment=(id)=>{
    this.setState({
      editorExperimentsVisible:true,
      experimentId:id
    })
  }
  //删除实验
  deleteExperiment=(id)=>{
    const params={
      experimentIds:id
    }
    deleteExperiment(params).then(res=>{
      console.log('删除实验'+id)
      this.showMessage(res)
      this.getBaseDataSource()
    }).catch(err=>{
      console.log(err)
    })
  }
  //运行实验
  runExperiment=(record)=>{
    const {ggeditorObejectString, id} = record
    const params={
      experimentId: id
    }
    runExperimentRunning(params).then(res=>{
      if(res){
        if(res.data.meta.code == '001'){
          message.success('成功创建实验运行！')
          this.props.saveConversationId(res.data.data.conversationId)
          this.props.saveExperimentRunningId(res.data.data.experimentRunningId)
          this.props.saveProcess(ggeditorObejectString)
          this.props.history.push('/index/experiment/run')
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
      console.log(err)
    })
  }

  handleRunningRecord = (record) => {
    this.props.saveProcess(record.ggeditorObejectString)
    this.setState({runningVisible:true, experimentId:record.id})
  }

  
  //克隆实验
  cloneExperiment=(id)=>{
    this.setState({
      cloneExperimentsVisible:true,
      experimentId:id
    })
  }

  changeSelectValue=(v)=>{
    this.setState({
        selectValue:v
    })
  }

  checkExperiment = (item) => {
    const {id: experimentId, paramJsonString, ggeditorObejectString, isMl} = item
    this.props.saveProcess(ggeditorObejectString);
    this.props.changeML(isMl)
    this.props.history.push({
      pathname: '/index/experiment/create',
      query: { paramJsonString, experimentId}
    })
  }

  markProcess = (ggeditorObejectString) => {
    this.setState({
      ggeditorObejectString,
      markProcessVisable: true
    })
  }

  render(){
    const { dataSource, cloneProcessVisible, selectValue,
      cloneExperimentsVisible,  editorExperimentsVisible,   markTemplateVisible,     
      runningVisible,  trashVisible,   markProcessVisable } = this.state
   
    //流程列结构
    const columns = [
      { key: 'icon',render: ()=><Avatar size={40} icon={<Icon type="apartment"  />} style={{ backgroundColor: '#27afff' }} />},
      {title: '流程名称',dataIndex: 'name',key: 'name',
        render:(text,record)=>(
          <a onClick={()=>this.selectProcess(record)}>{text}</a>
        )
      },
      {title: '标签',dataIndex: 'tags', key: 'tags',
        render:tags=>(
          <>
            {tags.split(',').map((item,index)=>(
              <Tag color='orange' key={index}>{item}</Tag>
            ))}
          </>
        )},
      { title: '创建时间',dataIndex: 'createTime', key: 'createTime',},
      {title: '流程描述',dataIndex: 'workflowDesc', key: 'workflowDesc',},
      {title: '操作',key: 'action',align:'center',
        render: (text, record) => (
          <span>
            <a onClick={()=>{this.cloneProcess(record.id)}}>克隆</a>
            {/* <Divider type="vertical" />
            <a onClick={()=>{this.saveProcess(record)}}>保存</a> */}
            {/* <Divider type="vertical" />
            <a onClick={()=>{this.downloadProcess(record.id)}}>下载</a> */}
            <Divider type="vertical" />
            <a onClick={()=>{this.newExperiments(record)}}>创建实验</a>
            <Divider type="vertical" />
            <a onClick={()=>{this.deleteProcess(record.id)}}>删除</a>
            <Divider type="vertical" />
            <a onClick={()=>{this.markProcess(record.ggeditorObejectString)}}>标记为组件</a>
          </span>
        )},
        
    ];

    //实验表结构
    const experimentsColumns = [
      // { title: '实验id', dataIndex: 'id', key: 'id' },
      { key: 'icon',render: ()=><Avatar size={36} icon={<Icon type="experiment"  />} style={{ backgroundColor: '#7cb305' }} />},
      {title: '实验名称',dataIndex: 'name', key: 'name',
        render:(text,record)=>(
          <a style={{color:this.state.selectExperimentKey===record.id?'red':''}} onClick={()=>this.checkExperiment(record)}>{text}</a>
        )
      },
      { title: '创建时间', dataIndex: 'createTime', key: 'createTime' },
      { title: '实验描述', dataIndex: 'experimentDesc', key: 'experimentDesc'},
      {
        title: '操作', align:'center',
        dataIndex: 'action',
        key: 'action',
        render: (text,record) => (
          <span>
            <a onClick={()=>this.runExperiment(record)}>运行</a>
            {/* <Divider type='vertical'/>
            <a onClick={()=>this.stopExperiment(record.id)}>停止</a> */}
            <Divider type='vertical'/>
            <a onClick={()=>this.editorExperiment(record.id)}>编辑</a>
            <Divider type='vertical'/>
            <a onClick={()=>this.deleteExperiment(record.id)}>删除</a>
            <Divider type='vertical'/>
            <a onClick={()=>this.handleRunningRecord(record)}>运行记录</a>
            <Divider type='vertical'/>
            <a onClick={()=>this.cloneExperiment(record.id)}>克隆</a>
            {
              record.isMarkTemplate === 0 && <>
              <Divider type='vertical'/>
              <a onClick={()=>this.setState({markTemplateVisible:true,experimentId:record.id})}>标记</a></>
            }
          </span>
        ),
      },
      {dataIndex: 'isMarkTemplate', key: 'isMarkTemplate',
        render:isMarkTemplate=>{
          return isMarkTemplate === 1 ? 
          <Tooltip placement="bottom" title="已标记为模板">
            <Icon type="project" theme="twoTone" style={{ fontSize: '16px'}}  /> 
          </Tooltip>
          : null
        }
      },
    ];

    return(
    <div className={style.contentWrap}>
      <PageHeader
        style={{border: '1px solid rgb(235, 237, 240)', marginBottom: '19px'}}
        title={<span onClick={this.getBaseDataSource}>流程-实验管理</span>}
        extra={[
            <Input.Search 
              style={{width:360, marginRight:'10px'}}
              onSearch={this.handleSearch} placeholder="搜索流程" 
              addonAfter={
                <Select value={selectValue} onChange={this.changeSelectValue} style={{width: 120}}>
                    <Select.Option value='name'>按名称搜索</Select.Option>
                    <Select.Option value='tag'>按标签搜索</Select.Option>
                </Select>
              }
            />,
            <Button type='primary' onClick={()=>this.setState({trashVisible:true})}>回收站</Button>,
            <Button type='primary' onClick={()=>{this.props.history.push('/index/process/create')}}>新建流程</Button>,
        ]}
      />
      <Spin spinning={this.state.loading} tip="loading...">
        <Table 
          tableLayout="auto"
          columns={columns}
          defaultExpandedRowKeys={this.state.selectProcessKey}
          expandIcon={({ expanded, onExpand, record }) =>
              expanded ? (
                <Icon type="caret-down" onClick={e => onExpand(record, e)} />
              ) : (
                <Icon type="caret-right" onClick={e => onExpand(record, e)}/>
              )
          }
          expandedRowRender={
            record=>(record.experimentList.length !== 0
              ? <Table columns={experimentsColumns} dataSource={record.experimentList} pagination={false} />
              : null)}
          dataSource={dataSource}
          pagination={{
            onChange: page => {
              this.setState({page},()=>{
                this.changePage()
              });
            },
            current:this.state.page,
            pageSize: 10,
            total: this.state.total
          }}
        />
      </Spin>

      {cloneProcessVisible&&
      <Modal
        title="克隆流程"
        visible={cloneProcessVisible}
        footer={null}
        onCancel={()=>{this.setState({cloneProcessVisible:false,cloneId:null})}}
      >
        <ProcessModal 
          cloneId={this.state.cloneId}
          close={()=>{
            this.setState({cloneProcessVisible:false})
            this.getBaseDataSource()
          }}
          isClone={true} 
        />
      </Modal>
      }

      {editorExperimentsVisible &&
      <Modal
        title="编辑实验信息"
        visible={editorExperimentsVisible}
        footer={null}
        onCancel={()=>{this.setState({editorExperimentsVisible:false,experimentId:null})}}
      >
        <ExperimentModal
          close={()=>{
            this.setState({editorExperimentsVisible:false,experimentId:null})
            this.getBaseDataSource()
          }}
          experimentId={this.state.experimentId}
          modalType={'editor'}
        />
      </Modal>}

      {cloneExperimentsVisible &&
      <Modal
        title="克隆实验"
        visible={cloneExperimentsVisible}
        footer={null}
        onCancel={()=>{
          this.setState({cloneExperimentsVisible:false,experimentId:null})
          this.getBaseDataSource()
        }}
      >
        <ExperimentModal
          close={()=>{
            this.setState({cloneExperimentsVisible:false,experimentId:null})
            this.getBaseDataSource()
          }}
          experimentId={this.state.experimentId}
          modalType={'clone'}
        />
      </Modal>}

      {runningVisible &&
      <Modal
        title="实验运行记录" width='80%'
        visible={runningVisible}
        footer={null}
        onCancel={()=>{this.setState({runningVisible:false,experimentId:null})}}
      >
        <ExperimentRunning 
          experimentId={this.state.experimentId}
          close={()=>{
            this.setState({runningVisible:false, experimentId:null})
            // this.getBaseDataSource()
          }}
        />
      </Modal>}

      {markTemplateVisible &&
      <Modal
        title="实验标记为模板"
        visible={this.state.markTemplateVisible}
        footer={null}
        onCancel={()=>{this.setState({markTemplateVisible:false,experimentId:null})}}
      >
        <TemplateModal 
          experimentId={this.state.experimentId}
          close={()=>{
            this.setState({markTemplateVisible:false,experimentId:null})
            }}/>
      </Modal>}

      {markProcessVisable &&
      <Modal
        title="流程标记为自定义组件"
        visible={this.state.markProcessVisable}
        footer={null}
        onCancel={()=>{this.setState({markProcessVisable:false,ggeditorObejectString:undefined})}}
      >
        <ProcessToComponentForm 
          ggeditorObejectString={this.state.ggeditorObejectString}
          close={()=>{
            this.setState({markProcessVisable:false,ggeditorObejectString:undefined})
          }}/>
      </Modal>}

      {trashVisible &&
      <Modal
        title="回收站" width='80%'
        visible={trashVisible}
        footer={null}
        onCancel={()=>{
          this.setState({trashVisible:false})
          this.getBaseDataSource()
        }}
      >
        <Trash />
      </Modal>}
    </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
  }
}
const mapDispatchToProps=(dispatch)=>({
  saveProcessId(processId){
    dispatch(actionCreator.saveProcessId(processId));
  },
  saveProcess(ggeditorString){
    dispatch(actionCreator.saveProcess(ggeditorString))
  },
  saveConversationId(id){
    dispatch(actionCreator.saveConversationId(id))
  },
  saveExperimentRunningId(id){
    dispatch(actionCreator.saveExperimentRunningId(id))
  },
  changeML(isMl){
    dispatch(actionCreator.changeMLorDL(isMl))
  },
})

export default connect(mapStateToProps, mapDispatchToProps)(Process);
