import React from 'react';
import { Card } from 'antd';
import { NodePanel, DetailPanel, CanvasPanel } from 'gg-editor';
import DetailForm from './DetailForm';
import './index.less';
import { connect } from 'react-redux';

import { saveItemObj } from '../../../process/store/actionCreator';


class FlowDetailPanel extends React.Component {

  componentWillUnmount(){
    this.props.saveItem(undefined);
  }
  
  render() {
    return (
      <DetailPanel className="detailPanel">
        <NodePanel>
          <Card title="组件参数配置" bordered={false} bodyStyle={{background: '#fff', height: '100%'}}>
            <DetailForm type="node" />
          </Card>
        </NodePanel>
        <CanvasPanel>
          <Card title="组件参数配置" bodyStyle={{background: '#fff', height: '100%'}}>
          </Card>
        </CanvasPanel>
      </DetailPanel>
    );
  }
};

const mapStateToProps = (state) => {
  return {
    configCurrentKey: state.process.configCurrentKey,
    label: state.process.label,
    currentNode: state.process.currentNode,
  }
}
const mapDispatchToProps=(dispatch)=>({
  saveItem(obj){
    dispatch(saveItemObj(obj));
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(FlowDetailPanel);
