import React, { Component } from 'react'
import { Form, Input, Button, message} from 'antd'
import { createExperiment,editorExperiment,cloneExperiment} from '../../../../api/experimets'

class ExperimentModal extends Component{
  constructor(props){
    super(props);
    this.state={
      
    }
  }
   //新建||编辑Modal
   submit=()=>{
    const { getFieldValue,validateFields } = this.props.form
    validateFields(err => {
      if(!err){
        let params = {
          name: getFieldValue('name'),
          experimentDesc: getFieldValue('desc')
          }
        if(this.props.modalType==='editor'){
          console.log('编辑实验')
          params={...params,experimentId:this.props.experimentId}
          editorExperiment(params).then((res)=>{
            const newRes=res.data.meta;
            if(newRes.code==='001'){
              message.success(newRes.message)
            }else{
              message.error(newRes.message)
            }
            //关闭modal
            this.props.close();
          }
          ).catch(err=>{
            console.log(err)
          })
        }else if(this.props.modalType==='create'){
          console.log('新建实验')
          params={...params,fkWorkflowId:this.props.processId}
          createExperiment(params).then((res)=>{
            const newRes=res.data.meta;
            if(newRes.code==='001'){
              message.success(newRes.message)
            }else{
              message.error(newRes.message)
            }
            //关闭modal
            this.props.close();
          }
          ).catch(err=>{
            console.log(err)
          })
        }else{
          console.log('克隆实验')
          params={...params,experimentId:this.props.experimentId}
          cloneExperiment(params).then((res)=>{
            const newRes=res.data.meta;
            if(newRes.code==='001'){
              message.success(newRes.message)
            }else{
              message.error(newRes.message)
            }
            //关闭modal
            this.props.close();
          }
          ).catch(err=>{
            console.log(err)
          })
        }
      }
    });
  }
  //重置
  reset=()=>{
    this.props.form.resetFields();
  }
  render(){
    
    const { getFieldDecorator } = this.props.form;
    const config = {
      rules: [{ required: true, message: 'Please input!' }],
    };
    return(
      <Form labelAlign='left'> 
          
        <Form.Item label='实验名称'>
          {getFieldDecorator('name',config)(<Input placeholder="请输入实验名称"/>)} 
        </Form.Item>

        <Form.Item label='实验描述'>
          {getFieldDecorator('desc',{initialValue:''})(<Input.TextArea placeholder="请输入实验描述" />)} 
        </Form.Item>
        <Form.Item style={{textAlign:'center'}}>
          <Button style={{marginRight:'5px'}} type='primary'
            onClick={this.reset}
          >
            重置
          </Button>
          <Button style={{marginLeft:'5px'}} type='primary'
            onClick={this.submit}
          >
             {this.props.modalType==='create' ? '新建':this.props.modalType==='clone'?'克隆':'修改'}
          </Button>
        </Form.Item>
        
        </Form>
    )
  }
}

export default Form.create()(ExperimentModal);