import React from 'react';
import { Menu, Icon, Collapse, Button, Modal, Tabs } from 'antd';
import { ItemPanel, Item } from 'gg-editor';
import { connect } from 'react-redux';
import { actionCreator } from '../../store';
import './index.less';
import IconFont from '../../../../common/IconFont'
import  backgroundpic  from '../../../../common/backgroundno.png'
import {loadPublicComponentInfo, getCustomComponent} from '../../../../api/component'

const { SubMenu } = Menu;
const { TabPane } = Tabs;

class FlowItemPanel extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      visible: false
    }
    this.myRef = React.createRef();
  }

  componentDidMount(){
    this.getData(this.props.isML)
  }

  callback = (key) => {
    console.log(key, this.props.isML)
    if(key !== this.props.isML){
      this.props.changeML(key)
      this.setState({
        customProcessComponent: [], customModelComponent: [], customAlgorithmComponent: [], components: []
      })
      this.getData(key)
    }
  }

  getData = (isMl) => {
    let componentInfo = {} 
    let components = []
    loadPublicComponentInfo({isMl}).then(res=>{
      console.log('组件信息',res)
      if(res.data.meta.code == '001'){
        res.data.data['System Component'].map(item=>{
          componentInfo = { ...componentInfo, [item.componentId] : item.variables}
          components.push(item)
        })
        this.props.saveComponentInfo(componentInfo)
        this.setState({
          components,
          componentInfo
        })
      }
    })
    getCustomComponent().then(res=>{
      if(res.data.meta.code == '001'){
        let customComponent = []
        res.data.data['CustomModel Component'].map(item=>{
          // component.push()
          componentInfo = { ...componentInfo, [item.componentId] : item.variables}
          customComponent.push(item)
        })
        this.setState({
          customModelComponent: res.data.data['CustomModel Component'],
          customProcessComponent: res.data.data['CustomProcess Component'],
          customAlgorithmComponent: res.data.data['CustomAlgorithm Component'],
          componentInfo
        })
      }
    })
  }

  // //调接口得到数据列表，处理好放在state
  // getData = () =>{
  //   let componentInfo = {}
  //   let datapre = []
  //   let character = []
  //   let auto = []
  //   let test = []
  //   getAllComponentInfo().then(res=>{
  //     console.log(res);
  //     if(res){
  //       if(res.data.meta.code == '001'){
  //         const customComponent = res.data.data.componentVOs['Custom Component'];
  //         res.data.data.componentVOs['System Component'].map(item=>{
  //           componentInfo = { ...componentInfo, [item.componentId] : item.variables}
  //           switch(item.componentType){
  //             case '数据预处理':
  //               datapre.push(item);
  //               break;
  //             case '特征工程':
  //               character.push(item);
  //               break;
  //             case '机器学习':
  //               auto.push(item);
  //               break;
  //             case '测试组件':
  //               test.push(item);
  //               break;
  //             default:
  //               return null
  //           }
  //         })
  //         customComponent.map(item=>{
  //           componentInfo = { ...componentInfo, [item.componentId] : item.variables}
  //         })
  //         console.log(componentInfo)
  //         this.setState({
  //           customComponent,
  //           componentInfo,
  //           datapre,
  //           character,
  //           auto,
  //           test
  //         })
  //         this.props.saveComponentInfo(componentInfo)
  //       }
  //     }
  //   })
  // }

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  handleCancel = e => {
    // this.child.clear();
    // console.log(e);
    this.setState({
      visible: false,
    });
  };

  //处理每个组件的参数
  handleConf = id => {
    let conf = {}
    if(this.state.componentInfo){
      this.state.componentInfo[id].map(item=>{
        conf = { ...conf, [item.variableName]:item.defaultValue }
      })
      console.log('conf',conf);
      return conf;
    }
  }


  render(){
    const { customProcessComponent, customModelComponent, customAlgorithmComponent, components } = this.state;
    const {custom}=this.props;
    return (
      <>
      <Tabs defaultActiveKey="1" onChange={this.callback}>
        <TabPane tab="机器学习" key="1"></TabPane>
        <TabPane tab="深度学习" key="0"></TabPane>
      </Tabs>
      <div  className="flowPanelContainer">
         <ItemPanel className="itemPanel">
          <Menu
            mode="inline"
            className="sidebarMenu"
          >
            <SubMenu
              key="source"
              title={
                <span>
                  <IconFont type="icon-shujuyuan" />
                  <span>源/目标</span>
                </span>
              }
            >
              <div className="nodeContain start">
                <Item
                    type="node"
                    shape="start-node"
                    model={{
                      color: '#1890FF',
                      color_type:'#5d8fe9',
                      size: [48, 48], 
                      conf: '',
                      label: 'start',
                      nodeType: '开始',
                      nodeRemarks: '开始',
                      state_icon_url:'',
                      style: {
                        fill: '#D5E2F3',
                        stroke: '#D5E2F3'
                      },
                    }}
                    src={backgroundpic}
                    />    
                    <div className='spanContain'><span>开始</span></div>
                </div>
                {/* <div className="nodeContain data_origin">
                  <Item
                      type="node"
                      shape="custom-node"
                      model={{
                        color: '#1890FF',
                        color_type:'#5d8fe9',
                        componentId: 5,
                        size: [240, 48], 
                        conf: {
                          addr: ''
                        },
                        label: 'dataImport',
                        nodeType: '数据导入',
                        nodeRemarks: '数据导入',
                        state_icon_url:'',
                        style: {
                          fill: '#fff',
                          stroke: '#d0d1d5'
                        },
                      }}
                      src={backgroundpic}
                      />    
                    <div className='spanContain'><span>数据导入</span></div>
                </div>   */}
            </SubMenu>
            <SubMenu
              key="group"
              title={
                <span>
                  <Icon type="team" />
                  <span>所有组件</span>
                </span>
              }
            >
              {/* {
                components.map(item=>{
                  return (<div className="nodeContain model">
                    <Item
                      type="node"
                      shape="custom-node"
                      model={{
                        color: '#1890FF',
                        color_type:'#5d8fe9',
                        componentId: item.id,
                        size: [240, 48], 
                        label: 'importModel',
                        nodeType: item.name,
                        nodeRemarks: item.name,
                        conf: {},
                        input: item.inputStub,
                        output: item.outputStub,
                        state_icon_url:'',
                        style: {
                          fill: '#fff',
                          stroke: '#d0d1d5'
                        },
                      }}
                      src={backgroundpic}
                    />    
                    <div className='spanContain'><span>模型导入</span></div>
                  </div>)
                })
              } */}
              {
                components && components.map(item=>{
                    return(
                      <div className={`nodeContain`}>
                        <Item
                          type="node"
                          shape="custom-node"
                          model={{
                            color: '#1890FF',
                            color_type:'#5d8fe9',
                            componentId: item.componentId,
                            size: [240, 48], 
                            label: item.componentName,
                            nodeType: item.componentNameChs,
                            nodeRemarks: item.componentNameChs,
                            conf : this.handleConf(item.componentId),
                            input: ['train'],
                            output: ['model'],
                            state_icon_url:'',
                            style: {
                              fill: '#fff',
                              stroke: '#d0d1d5'
                            },
                          }}
                          src={backgroundpic}
                          />    
                          <div className='spanContain'><span>{item.componentNameChs}</span></div>
                      </div>
                    )
                })
              }
            </SubMenu>
            <SubMenu
              key="model"
              title={
                <span>
                  <Icon type="funnel-plot" />
                  <span>自定义模型组件</span>
                </span>
              }
            >
              {
                customModelComponent && customModelComponent.map(item=>{
                    return(
                      <div className={`nodeContain`}>
                        <Item
                          type="node"
                          shape="custom-node"
                          model={{
                            color: '#1890FF',
                            color_type:'#5d8fe9',
                            componentId: item.componentId,
                            size: [240, 48], 
                            label: item.componentName,
                            nodeType: item.componentNameChs,
                            nodeRemarks: item.componentNameChs,
                            conf : this.handleConf(item.componentId),
                            componentType: item.componentType,
                            input: ['train'],
                            output: ['model'],
                            state_icon_url:'',
                            style: {
                              fill: '#fff',
                              stroke: '#d0d1d5'
                            },
                          }}
                          src={backgroundpic}
                          />    
                          <div className='spanContain'><span>{item.componentNameChs}</span></div>
                      </div>
                    )
                })
              }
            </SubMenu>
            <SubMenu
              key="process"
              title={
                <span>
                  <Icon type="apartment" />
                  <span>自定义流程组件</span>
                </span>
              }
            >
              {
                customProcessComponent && customProcessComponent.map(item=>{
                  return (<div className={`nodeContain`}>
                  <Item
                    type="node"
                    model={{
                      componentType: 'process',
                      ggeditorObjectString: item.ggeditorObjectString,
                      input: ['train'],
                      output: ['model'],
                      style: {
                        fill: '#fff',
                        stroke: '#d0d1d5'
                      },
                    }}
                    src={backgroundpic}
                    />    
                    <div className='spanContain'><span>{item.componentNameChs}</span></div>
                </div>)
                })
              }
            </SubMenu>
            <SubMenu
              key="algorithm"
              title={
                <span>
                  <Icon type="account-book" />
                  <span>自定义算法组件</span>
                </span>
              }
            >
              {
                customAlgorithmComponent && customAlgorithmComponent.map(item=>{
                  return (<div className={`nodeContain`}>
                  <Item
                    type="node"
                    model={{
                      componentType: 'process',
                      
                      input: ['train'],
                      output: ['model'],
                      style: {
                        fill: '#fff',
                        stroke: '#d0d1d5'
                      },
                    }}
                    src={backgroundpic}
                    />    
                    <div className='spanContain'><span>{item.componentNameChs}</span></div>
                </div>)
                })
              }
            </SubMenu>
          </Menu>
        </ItemPanel>
        {custom!=='no' && <div className='buttonWrapper' style={{visibility:custom==='no' ? 'hidden':'visible'}}>
          <Button type='primary' icon="plus" onClick={this.showModal}>自定义组件</Button>
        </div>}
        <Modal
          className="custom"
          title="自定义组件"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          width='927px'
          footer={null}
        >
          {/* <Custom onCancel={this.handleCancel} getData={this.getData} onRef={(ref)=>{ this.child = ref}} /> */}
          <span>自定义组件</span>
        </Modal>
      </div>
    </>);
  }
};

const mapStateToProps = (state) => {
  return {
    isML: state.process.isML,
  }
}

const mapDispatchToProps = (dispatch) => ({
  saveComponentInfo(componentInfo) {
    dispatch(actionCreator.saveComponentInfo(componentInfo));
  },
  changeML(isML){
    console.log('change ', isML)
    dispatch(actionCreator.changeMLorDL(isML))
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(FlowItemPanel);
