import React, { Component } from 'react'
import { Input, Form, Upload, Button, Icon, message,Radio} from 'antd'

import { insertUserDataset, uploadDataset,uploadUserDatasetFromMysql,insertUserDlDataset} from '../../api/dataset';


class UploadDataset extends Component {

  constructor(props){
    super(props)
    this.state = {
      fileList: [],
      k:0,
      registerType:0,  //0代表txt，csv注册  1-代表表结构注册  2-表示图片数据集注册
    }
  }
  //选择注册数据集的方式
  onChange=(e)=>{
    this.setState({
      registerType:e.target.value
    });
    this.clear();
  }

  
  componentDidMount() {
    this.props.onRef(this)
  }

  submit_1= () => {
    const { getFieldValue, validateFields } = this.props.form
    validateFields((err,values) => {
      if(!err){
        let params = {
          datasetName: values.sourceName,
          tags: values.tags.split(','),
          datasetDesc: values.desc,
          // columnSeparator:values.colSpilt,
          // familyName:JSON.stringify(values.family),
          file:this.state.fileList[0],
          // userId:2
          }
          // const columnName=[];
          // for(let i=0; i<values.family.length; i++){
          //   const arr=values.columnName[i].split(',');
          //   for(let j=0; j<arr.length; j++){
          //     var str =values.family[i]+':'+arr[j];
          //     columnName.push(str);
          //   }
          // }
          // params.columnName=JSON.stringify(columnName);
          console.log(params);
          //异步请求
          uploadDataset(params).then(res => {
          // insertUserDataset(params).then((res)=>{
            const newRes=res.data.meta;
            if(newRes.code==='001'){
              message.success(newRes.message)
              this.props.datasetType === 'machine' ? this.props.getUploadData() : this.props.getDlUploadData();
            }else{
              message.error(newRes.message)
            }
          }).catch(err=>{
            console.log(err)
          })
          this.props.close();
      }
    });
  }

  submit_2 = () => {
    const { getFieldValue, validateFields } = this.props.form
    validateFields((err,values) => {
      if(!err){
        let params = {
          url: values.url,
          username: values.userName,
          password:values.password,
          tableName:values.tableName,
          datasetName:values.datasetName,
          tags:values.tags_mysql.split(','),
          datasetDesc:values.desc_mysql
          }
        
          console.log(params);
        
          //异步请求
          uploadUserDatasetFromMysql(params).then(res => {
          // insertUserDataset(params).then((res)=>{
            const newRes=res.data.meta;
            if(newRes.code==='001'){
              message.success(newRes.message)
              this.props.datasetType === 'machine' ? this.props.getUploadData() : this.props.getDlUploadData();
            }else{
              message.error(newRes.message)
            }
          }).catch(err=>{
            console.log(err)
          })
          this.props.close();
      }
    });
  }

  submit_3 = () => {
    const { getFieldValue, validateFields } = this.props.form
    validateFields((err,values) => {
      if(!err){
        let params = {
          datasetName: values.datasetName_1,
          tags: values.tags_1.split(','),
          datasetDesc: values.desc_1,
          isAnnotation:values.isAnnotation,
          originFileType:values.originFileType,
          file:this.state.fileList[0],
          // userId:2
          }
          
          console.log(params)
          //异步请求
          insertUserDlDataset(params).then(res => {
          // insertUserDataset(params).then((res)=>{
            const newRes=res.data.meta;
            if(newRes.code==='001'){
              message.success(newRes.message)
              this.props.datasetType === 'machine' ? this.props.getUploadData() : this.props.getDlUploadData();
            }else{
              message.error(newRes.message)
            }
          }).catch(err=>{
            console.log(err)
          })
          this.props.close();
      }
    });
  }

  clear = () => {
    this.setState({
      fileList: []
    })
    this.props.form.resetFields()
  }
  remove=k=>{
    const { form } = this.props;
    const keys = form.getFieldValue('keys');
    const newKeys=keys.filter(key => key !== k);
    form.setFieldsValue({
      keys:newKeys,
    });
  };
  add=()=>{
    const { form } = this.props;
    const {k}=this.state;
    const keys = form.getFieldValue('keys');
    const nextKeys = keys.concat(k+1);
    form.setFieldsValue({
        keys:nextKeys
    });
    this.setState({
        k:k+1
    })
  };
  

  render(){
    const { fileList,registerType} = this.state;
    const { getFieldDecorator,getFieldValue } = this.props.form;
    getFieldDecorator('keys', { initialValue:[0]});
    const props = {
      onRemove: file => {
        this.setState(state => {
          const index = state.fileList.indexOf(file);
          const newFileList = state.fileList.slice();
          newFileList.splice(index, 1);
          return {
            fileList: newFileList,
          };
        });
      },
      beforeUpload: file => {
        this.setState(state => ({
          fileList: [...state.fileList, file],
        }));
        return false;
      },
      onChange:  info => {
        let fileList = [...info.fileList];
        console.log(fileList);
        fileList = fileList.slice(-1);
        console.log(fileList);
      },
      fileList,
    };
    return(
      <>
        <Radio.Group onChange={this.onChange} value={registerType}>
            <Radio value={0}>非表结构数据集</Radio>
            <Radio value={1}>表结构数据集</Radio>
            <Radio value={2}>深度学习数据集</Radio>
        </Radio.Group>
        {
          registerType==0&&
          <Form labelAlign='left'> 
          <Form.Item label='数据集名称'>
            {getFieldDecorator('sourceName',{rules: [{ required: true, message: 'Please input!' }]})
              (<Input placeholder="请输入数据集名称"/>)} 
          </Form.Item>

          <Form.Item label='数据集标签'
            name='tags'
          >
            {getFieldDecorator('tags', {initialValue:'',rules: [{ required: true, message: 'Please input!' }]})
            (<Input.TextArea 
              placeholder='请输入标签，并以 , 分割'/>
              )}
          </Form.Item>

          <Form.Item label='数据集描述'>
            {getFieldDecorator('desc',{initialValue:''})(<Input.TextArea placeholder="请输入数据集描述" />)} 
          </Form.Item>

          <Form.Item required={true}>
            {getFieldDecorator('file',{rules: [{ required: true, message: 'Please input!' }]})(
            <Upload {...props}>
              <Button>
                <Icon type="upload" /> 选择文件
              </Button>
            </Upload>
            )} 
          </Form.Item>

          {/* {
            formItems
          } */}
          {/* <Form.Item>
            <Button type="dashed" onClick={this.add} style={{width:300,marginLeft:80}}>
                <Icon type="plus" /> 添加列族
            </Button>
          </Form.Item> */}

          <Form.Item style={{textAlign:'center'}}>
            <Button style={{marginRight:'5px'}} type='primary'
              onClick={this.clear}
            >
              重置
            </Button>
            <Button style={{marginLeft:'5px'}} type='primary'
              onClick={this.submit_1}
            >
              注册
            </Button>
          </Form.Item>
        </Form>
        }

        {
          registerType==1&&
          <Form labelAlign='left'> 
          <Form.Item label='数据库URL地址'>
            {getFieldDecorator('url',{rules: [{ required: true, message: 'Please input!' }]})
              (<Input placeholder="请输入数据库URL地址"/>)} 
          </Form.Item>
          <Form.Item label='用户名'>
            {getFieldDecorator('userName',{rules: [{ required: true, message: 'Please input!' }]})
              (<Input placeholder="请输入用户名"/>)} 
          </Form.Item>
          <Form.Item label='密码'>
            {getFieldDecorator('password',{rules: [{ required: true, message: 'Please input!' }]})
              (<Input.Password />)} 
          </Form.Item>
          <Form.Item label='表名'>
            {getFieldDecorator('tableName',{rules: [{ required: true, message: 'Please input!' }]})
              (<Input placeholder="请输入表名"/>)} 
          </Form.Item>
          <Form.Item label='数据集名称'>
            {getFieldDecorator('datasetName',{rules: [{ required: true, message: 'Please input!' }]})
              (<Input placeholder="请输入数据集名称"/>)} 
          </Form.Item>
          {/* <Form.Item label='行分割符'>
            {getFieldDecorator('rowSpilt',{initialValue:''})
              (<Input/>)} 
          </Form.Item> */}

          {/* <Form.Item label='列分割符'>
            {getFieldDecorator('colSpilt',{initialValue:','})
              (<Input/>)} 
          </Form.Item> */}

          <Form.Item label='数据集标签'
            name='tags'
          >
            {getFieldDecorator('tags_mysql', {initialValue:'',rules: [{ required: true, message: 'Please input!' }]})
            (<Input.TextArea 
              placeholder='请输入标签，并以 , 分割'/>
              )}
          </Form.Item>

          <Form.Item label='数据集描述'>
            {getFieldDecorator('desc_mysql',{initialValue:''})(<Input.TextArea placeholder="请输入数据集描述" />)} 
          </Form.Item>

          {/* {
            formItems
          } */}
          {/* <Form.Item>
            <Button type="dashed" onClick={this.add} style={{width:300,marginLeft:80}}>
                <Icon type="plus" /> 添加列族
            </Button>
          </Form.Item> */}

          <Form.Item style={{textAlign:'center'}}>
            <Button style={{marginRight:'5px'}} type='primary'
              onClick={this.clear}
            >
              重置
            </Button>
            <Button style={{marginLeft:'5px'}} type='primary'
              onClick={this.submit_2}
            >
              注册
            </Button>
          </Form.Item>
        </Form>
        }

        {
          registerType==2&&
          <Form labelAlign='left'> 
          
          <Form.Item label='数据集名称'>
            {getFieldDecorator('datasetName_1',{rules: [{ required: true, message: 'Please input!' }]})
              (<Input placeholder="请输入数据集名称"/>)} 
          </Form.Item>

          <Form.Item label='数据集标签'
            name='tags'
          >
            {getFieldDecorator('tags_1', {initialValue:'',rules: [{ required: true, message: 'Please input!' }]})
            (<Input.TextArea 
              placeholder='请输入标签，并以 , 分割'/>
              )}
          </Form.Item>

          
          <Form.Item label="是否标注">
            {getFieldDecorator('isAnnotation')(
              <Radio.Group>
                <Radio value="1">是</Radio>
                <Radio value="0">否</Radio>
              </Radio.Group>,
            )}
          </Form.Item>

          <Form.Item label="数据集类型">
            {getFieldDecorator('originFileType')(
              <Radio.Group>
                <Radio value="0">文本</Radio>
                <Radio value="1">图片</Radio>
                <Radio value="2">其他</Radio>
              </Radio.Group>,
            )}
          </Form.Item>

          <Form.Item label='数据集描述'>
            {getFieldDecorator('desc_1',{initialValue:''})(<Input.TextArea placeholder="请输入数据集描述" />)} 
          </Form.Item>

          <Form.Item required={true}>
            {getFieldDecorator('file',{rules: [{ required: true, message: 'Please input!' }]})(
            <Upload {...props}>
              <Button>
                <Icon type="upload" /> 选择文件
              </Button>
            </Upload>
            )}
            
          </Form.Item>

          <Form.Item style={{textAlign:'center'}}>
            <Button style={{marginRight:'5px'}} type='primary'
              onClick={this.clear}
            >
              重置
            </Button>
            <Button style={{marginLeft:'5px'}} type='primary'
              onClick={this.submit_3}
            >
              注册
            </Button>
          </Form.Item>
        </Form>
        }
        
      </>
    )
  }
}

export default Form.create()(UploadDataset);