import React, { Component } from 'react'
import {Form, Input, Button, message} from 'antd'

import { workflowToComponent } from '../../../../api/component'

class ProcessToComponentForm extends Component{
  constructor(props){
    super(props);
  }
  submit=()=>{
    const { getFieldValue,validateFields } = this.props.form
    const {ggeditorObejectString: ggeditorObjectString} = this.props
    console.log(this.props)
    validateFields((err,res) => {
      console.log(res)
      if(!err){
        let params = { ...res, ggeditorObjectString}
        console.log(params)
        workflowToComponent(params).then((res)=>{
          const newRes=res.data.meta;
          if(newRes.code==='001'){
            message.success(newRes.message)
          }else{
            message.error(newRes.message)
          }
          //关闭modal
          this.props.close();
          }).catch(err=>{
            console.log(err)
        })
      }
    });
  }
 
  //重置
  reset=()=>{
    this.props.form.resetFields();
  }

  render(){
    const { getFieldDecorator } = this.props.form;
    return(
      <Form labelAlign='left'> 
        <Form.Item label='组件名称'>
          {getFieldDecorator('componentName',{rules: [{ required: true, message: '请输入组件名称!' }]})(<Input placeholder="请输入组件名称"/>)} 
        </Form.Item>
        
        <Form.Item label='组件标签'>
          {getFieldDecorator('tagString', {initialValue:'',rules: [{ required: true, message: '请输入组件标签!' }]})
          (<Input.TextArea 
            placeholder='请输入组件标签，并以 , 分割'/>
            )}
        </Form.Item>

        <Form.Item label='组件描述'>
          {getFieldDecorator('workflowDesc',{initialValue:''})(<Input.TextArea placeholder="请输入组件描述" />)} 
        </Form.Item>
        <Form.Item style={{textAlign:'center'}}>
          <Button style={{marginRight:'5px'}} type='primary'
            onClick={this.reset}
          >
            重置
          </Button>
          <Button style={{marginLeft:'5px'}} type='primary' onClick={this.submit}>
            标记
          </Button>
        </Form.Item>
      </Form>
    )
  }
}

export default Form.create()(ProcessToComponentForm);