import axios from 'axios';
import qs from 'querystring';
import { message } from 'antd'; 
import { createHashHistory } from 'history'; // history路由

const history = createHashHistory();
// import { updateAuthState } from './checkStore';

const service = axios.create({
  //baseURL: 'http://192.168.0.235:8080', // 请求地址
  // timeout: 15000, // 请求超时限制
  timeout: 150000, // 请求超时限制
});



// 请求处理
service.interceptors.request.use(config => {
  return config;
}, error => {
  // 错误处理
  message.error(error);
  Promise.reject(error);
});

// 响应处理
service.interceptors.response.use(res => {
  // 处理接口返回数据
  if (res.status === 200) {
    if(res.data.code == 500){
      message.error('未登录！')
      sessionStorage.setItem("isLogin", false)
      history.push('/login');
    }
    // else if(res.data.meta.code == 405){
    //   message.error('未授权！')
    //   history.push('/licenseError');
    // }
    // else if(res.data.meta.code == 402 || res.data.meta.code == 403){
    //   message.error('未授权！')
    //   history.push('/licenseError');
    // }
    // else{
    //   // //更新token
    //   // updateAuthState();
    //   return res;
    // }//
    return res;
  } 
  else if (res.status === 500){
    message.info("未登录！");
    sessionStorage.setItem("isLogin", false)
    this.props.history.push('/login');
  }
  else if ( res.status === 401 ){
    window.location.href = '/#/NotLogin';
  }

}, error => {
  // console.log(error);
  // console.log(error.response)
  if(error && error.response){
    switch (error.response.status){
      case 401:
        message.error("未登录！");
        sessionStorage.setItem("isLogin", false);
        history.push('/login');
        return;
      default:
        message.error("请求失败！")
    }
  }
  return Promise.reject(error)
});

export default service;
