import React,{Component} from 'react';
import {Button,Input,Col,Row,Form,Icon} from 'antd';

class ParamsForm extends Component{

    state={
        values:[],
        k:-1
    }
    remove=k=>{
        const { form } = this.props;
        const keys = form.getFieldValue('keys');
        const newKeys=keys.filter(key => key !== k);
        form.setFieldsValue({
          keys:newKeys,
        });
        this.setState({
            values:newKeys
        })
    };
    add=()=>{
        const { form } = this.props;
        const {k,values}=this.state;
        values.push(k+1);
        this.setState({
            values,
            k:k+1
        })
        form.setFieldsValue({
            keys:values
        });
      };

    componentDidMount(){
        this.props.onRef(this);
        let values=[];
        for(let i=0;i<this.props.values.length;i++){
            values.push(i);
        }
        this.setState({
            values,
            k:values.length-1
        })
        
    }
    render(){
        const {getFieldDecorator,getFieldValue}=this.props.form;
        const {values}=this.state;
        const propsValues=this.props.values;
        const formItemLayout = {
            labelCol: {
              span:6
            },
            wrapperCol: {
              span:16
            },
          };
        const rules=[
            {
                required: true,
                whitespace: true,
            },
        ]
        getFieldDecorator('keys', { initialValue: values });
        const keys = getFieldValue('keys');
        const formItems = keys.map((k, index) => (
            <Row>
                <Col span={12}>
                    <Form.Item
                        label={'参数名'}
                        key={k+'1'}
                        {...formItemLayout}
                    >
                        {getFieldDecorator(`name[${k}]`, {
                            rules,
                            initialValue:propsValues[k]?propsValues[k]['name']:''
                        })(<Input placeholder="请输入参数名称" />)}
                    </Form.Item>
                    <Form.Item
                        label={'默认值'}
                        key={k+'3'}
                        {...formItemLayout}
                    >
                        {getFieldDecorator(`defaultValue[${k}]`, {
                            rules,
                            initialValue:propsValues[k]?propsValues[k]['defaultValue']:''
                        })(<Input placeholder="请输入参数默认值" />)}
                    </Form.Item>
                </Col>
                <Col span={12}>
                    <Form.Item
                        label={'参数类型'}
                        key={k+'2'}
                        {...formItemLayout}
                    >
                        {getFieldDecorator(`parameterType[${k}]`, {
                            rules,
                            initialValue:propsValues[k]?propsValues[k]['parameterType']:''
                        })(<Input placeholder="请输入参数类型" />)}
                        
                    </Form.Item>
                    <Form.Item
                        label={'参数描述'}
                        key={k+'4'}
                        {...formItemLayout}
                    >
                        {getFieldDecorator(`parameterDesc[${k}]`, {
                            rules,
                            initialValue:propsValues[k]?propsValues[k]['parameterDesc']:''
                        })(<Input placeholder="请输入参数描述" />)}
                    </Form.Item>
                    <Icon
                        type="minus-circle-o"
                        onClick={() => this.remove(k)}
                    />
                </Col>
                
                
            </Row>
            
        ));
        return (
            <div>
                <Form colon={false}>
                    {formItems}
                    <Form.Item>
                        <Button type="dashed" onClick={this.add} style={{width:500,marginLeft:80}}>
                            <Icon type="plus" /> Add field
                        </Button>
                    </Form.Item>
                </Form>
            </div>
        )  
    }
}
export default Form.create()(ParamsForm);