import React, { Component } from 'react';
import './index.less';
import { register } from '../../api/login.js';
import { Form, Input, Button, message, Icon } from 'antd';
import {Link} from 'react-router-dom'

class Register extends Component {

  constructor(props){
    super(props);
    this.state = {

    }
  }


  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
        const { confirm, ...param } = values;
        register(param).then(res=>{
          console.log(res);
          if(res.data.meta.code === '001'){
            message.success('注册成功！')
            this.props.history.push('/login');
          }else{
            message.error(res.data.meta.message)
          }
        })
      }
    });
  };


  compareToFirstPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && value !== form.getFieldValue('password')) {
      callback('Two passwords that you enter is inconsistent!');
    } else {
      callback();
    }
  };

  validateToNextPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && this.state.confirmDirty) {
      form.validateFields(['confirm'], { force: true });
    }
    callback();
  };



  render(){

    const { getFieldDecorator } = this.props.form;

    return(
      <div className='loginContainer'>
        <div className="registerContainer">
          <Form onSubmit={this.handleSubmit} className="login-form" style={{top: '460px',left: '93px'}} autocomplete="off">
            <Form.Item>
              {getFieldDecorator('username', {
                rules: [
                  {
                    required: true,
                    message: 'Please input your userName!',
                  },
                ],
              })(<Input 
                size="large"
                placeholder='请输入用户名' 
                prefix={<Icon type="user" className="loginIcon" />}
              />)}
            </Form.Item>
            <Form.Item hasFeedback>
              {getFieldDecorator('password', {
                rules: [
                  {
                    required: true,
                    message: 'Please input your password!',
                  },
                  {
                    validator: this.validateToNextPassword,
                  },
                ],
              })(<Input.Password 
                placeholder='请输入密码'
                size="large"
                prefix={<Icon type="lock" className="loginIcon" />}
            />)}
            </Form.Item>
            <Form.Item hasFeedback>
              {getFieldDecorator('confirm', {
                rules: [
                  {
                    required: true,
                    message: 'Please confirm your password!',
                  },
                  {
                    validator: this.compareToFirstPassword,
                  },
                ],
              })(<Input.Password 
                onBlur={this.handleConfirmBlur} 
                placeholder='请确认密码' 
                size="large"
                prefix={<Icon type="lock" className="loginIcon" />}
              />)}
            </Form.Item>
            <Link to='/login'>
                <a className="login-form-forgot" href="">
                  登录
                </a>
              </Link>
            <Form.Item className="registerButton">
              
              <Button type="primary" htmlType="submit" className="login-form-button">
                注册
              </Button>
            </Form.Item>
          </Form>
        </div>

      </div>
    )
  }
}

export default Form.create()(Register);