import * as constants from './constants';

const defaultState = {
  chartData: [],
  activeIndex: -1,  // 当前组件的下标
};

export default(state=defaultState, action) => {
  const newState = JSON.parse(JSON.stringify(state));
  switch(action.type) {

    case constants.LOAD_COMPONENT: // 机器学习还是深度学习
      newState.chartData = [...action.chartData]
      newState.activeIndex = action.activeIndex
      return newState

    case constants.ADD_COMPONENT: // 机器学习还是深度学习
      newState.chartData = [...newState.chartData, action.chart]
      newState.activeIndex = newState.chartData.length - 1
      return newState

    case constants.CHANGE_TITLE:
      newState.chartData[newState.activeIndex].options.title = action.title
      return newState

    case constants.CHANGE_SIZE:
      newState.chartData[newState.activeIndex].width = action.size.width
      newState.chartData[newState.activeIndex].height = action.size.height
      if(newState.chartData[newState.activeIndex].options.visualMap){
        if(newState.chartData[newState.activeIndex].options.visualMap.type=='continuous'){
          newState.chartData[newState.activeIndex].options.visualMap.itemHeight = action.size.width / 2;
        }else if(newState.chartData[newState.activeIndex].options.visualMap.type=='piecewise'){
          newState.chartData[newState.activeIndex].options.visualMap.itemWidth = action.size.width / 20;
          newState.chartData[newState.activeIndex].options.visualMap.itemHeight = action.size.height / 20
        }
      }
      
      return newState

    case constants.MOVE_COMPONENT:
      newState.chartData[newState.activeIndex].x = action.config.x
      newState.chartData[newState.activeIndex].y = action.config.y
      return newState

    case constants.SET_OFFSET:
      newState.chartData[newState.activeIndex].offsetX = action.offset.x
      newState.chartData[newState.activeIndex].offsetY = action.offset.y
      return newState

    case constants.CHANGE_ACTIVEINDEX:
      newState.activeIndex = action.index
      return newState
      
    //下面series指定的是第一条线，如果多条可能需要改一下
    case constants.CHANGE_LINESTYLE:
      newState.chartData[newState.activeIndex].options.series.lineStyle = action.lineStyle
      return newState

    case constants.CHANGE_XAXISDATA:
      newState.chartData[newState.activeIndex].options.xAxis.data = action.data
      return newState
    
    case constants.CHANGE_YAXISDATA:
      newState.chartData[newState.activeIndex].options.yAxis.data = action.data
      return newState
      
    default:
      return state;
  }
}


